import { GameConstants } from "../GameConstants";
import IGameAPI from "./IGameAPI";
import { ExplorerApi } from "atomicassets";
import { JsonRpc } from "eosjs";

export default class GameAPI implements IGameAPI {

    private wax: any;
    private userName: string;
    private atomicAPI: ExplorerApi;
    private clashdomeClient: any;
    private urls: string[];
    private urls_i: number;

    // INIT

    public init(wax: any, userName: string, clashdomeClient: any, reactClass: any): void {

        let url;

        if (reactClass && reactClass.state && reactClass.state.serverType === "testnet") {
            url = "https://test.wax.api.atomicassets.io";
            this.urls = ["https://wax-test.eosdac.io", "https://testnet.wax.pink.gg", "https://api.waxtest.alohaeos.com", "https://waxtest.eosn.io", "https://testnet.wax.eosrio.io"];
        } else {
            url = "https://aa.dapplica.io";
            this.urls = ["https://api.wax.alohaeos.com", "https://query.3dkrender.com", "https://wax.pink.gg", "https://wax.eu.eosamsterdam.net", "https://api.waxsweden.org", "https://wax.blacklusion.io", "https://wax.dapplica.io", "https://wax.eoseoul.io", "https://api.wax.greeneosio.com", "https://wax.hkeos.com", "https://wax.eosn.io", "https://api-wax-mainnet.wecan.dev", "https://wax.cryptolions.io"];
        }

        this.urls_i = 0;

        this.wax = wax;
        this.userName = userName;
        this.atomicAPI = new ExplorerApi(url, "atomicassets", { fetch });
        this.clashdomeClient = clashdomeClient;
    }

    // GET

    public async getEarlyAccess(): Promise<any> {

        return await this.atomicAPI.getAssets({ owner: this.userName, collection_name: GameConstants.COLLECTION_NAME, schema_name: GameConstants.EARLY_ACCESS_SCHEMA });
    }

    public async getPlayerInfo(visitMode: boolean): Promise<any> {

        let playerInfo = await this.getTableRows(GameConstants.CONTRACT_NAME, "accounts", 1, 1, "i64", this.userName, this.userName, 0);
        await this.sleep(250);
        let citizenInfo = await this.getTableRows(GameConstants.CONTRACT_NAME, "citiz", 1, 1, "i64", this.userName, this.userName, 0);
        await this.sleep(250);

        let trialInfo;

        if (playerInfo[0]) {

            let ludioBalance = visitMode ? await this.getCurrencyBalance("clashdometkn", this.userName, "LUDIO", 0) : ["0.0000 LUDIO"];
            await this.sleep(250);
            let carbzBalance = visitMode ? await this.getCurrencyBalance("clashdometkn", this.userName, "CDCARBZ", 0) : ["0.0000 CDCARBZ"];
            await this.sleep(250);
            let jigosBalance = visitMode ? await this.getCurrencyBalance("clashdometkn", this.userName, "CDJIGO", 0) : ["0.0000 CDJIGO"];

            if (!ludioBalance || !ludioBalance[0]) {
                ludioBalance = ["0.0000 LUDIO"];
            }

            if (!carbzBalance || !carbzBalance[0]) {
                carbzBalance = ["0.0000 CDCARBZ"];
            }

            if (!jigosBalance || !jigosBalance[0]) {
                jigosBalance = ["0.0000 CDJIGO"];
            }

            playerInfo[0].balances = [ludioBalance[0], carbzBalance[0], jigosBalance[0]];
        } else {

            trialInfo = await this.getTableRows(GameConstants.CONTRACT_NAME, "trials", 1, 1, "i64", this.userName, this.userName, 0);
        }

        if (citizenInfo.length) {
            return { player: playerInfo[0], citizen: citizenInfo[0], trial: null };
        } else if (playerInfo.length) {
            return { player: playerInfo[0], citizen: null, trial: null };
        } else if (trialInfo.length) {
            return { trial: trialInfo[0] };
        } else {
            return null;
        }
    }

    public async getPlayerTokens(): Promise<any> {

        let ludioBalance = await this.getCurrencyBalance("clashdometkn", this.userName, "LUDIO", 0);
        await this.sleep(250);
        let carbzBalance = await this.getCurrencyBalance("clashdometkn", this.userName, "CDCARBZ", 0);
        await this.sleep(250);
        let jigoBalance = await this.getCurrencyBalance("clashdometkn", this.userName, "CDJIGO", 0);

        return { ludio: parseFloat(ludioBalance[0]), cdcarbz: parseFloat(carbzBalance[0]), cdjigo: parseFloat(jigoBalance[0]) };
    }

    public async getConfigInfo(): Promise<any> {

        let config = await this.getTableRows(GameConstants.CONTRACT_NAME, "config", 1, 1, "i64", null, null, 0);

        return config[0];
    }

    public async getCitizenConfigInfo(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "citizconfig", 10, 1, "i64", null, null, 0);
    }

    public async getToolsConfigInfo(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "toolconfig", 10, 1, "i64", null, null, 0);
    }

    public async getWalletsConfigInfo(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "walletconfig", 10, 1, "i64", 350000, null, 0);
    }

    public async getCitizenInfo(name: string): Promise<any> {

        let citizenInfo = await this.getTableRows(GameConstants.CONTRACT_NAME, "citiz", 1, 1, "i64", name, name, 0);

        if (citizenInfo.length) {
            return citizenInfo[0];
        } else {
            return null;
        }
    }

    public async getCitizenData(id: string): Promise<any> {

        return await this.atomicAPI.getAsset(id);
    }

    public async getInitialCitizens(): Promise<any> {

        return this.atomicAPI.getAssets({ owner: this.userName, collection_name: GameConstants.COLLECTION_NAME, schema_name: "citizen" });
    }

    public async getUnstakedSlots(): Promise<any> {

        return await this.atomicAPI.getAssets({ owner: this.userName, collection_name: GameConstants.COLLECTION_NAME, schema_name: GameConstants.SLOTS_SCHEMA });
    }

    public async getStakedSlots(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "slots", 100, 2, "i64", this.userName, this.userName, 0);
    }

    public async getUnstakedTools(): Promise<any> {

        return await this.atomicAPI.getAssets({ owner: this.userName, collection_name: GameConstants.COLLECTION_NAME, schema_name: GameConstants.TOOLS_SCHEMA });
    }

    public async getStakedTools(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "tools", 100, 2, "i64", this.userName, this.userName, 0);
    }

    public async getUnstakedWallets(): Promise<any> {

        return await this.atomicAPI.getAssets({ owner: this.userName, collection_name: GameConstants.COLLECTION_NAME, schema_name: GameConstants.WALLETS_SCHEMA });
    }

    public async getStakedWallets(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "wallets", 1, 2, "i64", this.userName, this.userName, 0);
    }

    public async getStakedDecoration(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "decorations", 100, 2, "i64", this.userName, this.userName, 0);
    }

    public async getSocialData(username: string): Promise<any> {

        let rows = await this.getTableRows(GameConstants.CONTRACT_NAME, "social", 1, 1, "i64", username, username, 0);

        if (rows.length) {
            return JSON.parse(rows[0].data);
        } else {
            return null;
        }
    }

    public async getGameRewardData(): Promise<any> {

        const response = await fetch("/api/clashdome-game/get-token-mining-game-config/" + this.userName);
        const data = await response.json();

        if (data.error) {
            return [
                { game: "ringy-dingy", ludio: 5, mult: 1 },
                { game: "templok", ludio: 5, mult: 1 },
                { game: "endless-siege-2", ludio: 5, mult: 1 },
                { game: "candy-fiesta", ludio: 5, mult: 1 },
                { game: "rug-pool", ludio: 5, mult: 1 },
            ];
        } else {

            let result = [];

            Object.keys(data.gamesFees).map(function (key) {

                let game = "";
                if (key === "1") {
                    game = "candy-fiesta";
                } else if (key === "2") {
                    game = "templok";
                } else if (key === "3") {
                    game = "ringy-dingy";
                } else if (key === "4") {
                    game = "endless-siege-2";
                } else if (key === "5") {
                    game = "rug-pool";
                }

                result.push({ game: game, ludio: data.gamesFees[key].reward ? parseFloat(data.gamesFees[key].reward) : 5, mult: data.playerMultiplier });
            });

            return result;
        }
    }

    // STAKE

    public async stakeCitizen(data: any): Promise<boolean> {
        return await this.takeAction("atomicassets", "transfer", this.userName, data);
    }

    public async stakeTool(data: any): Promise<boolean> {
        return await this.takeAction("atomicassets", "transfer", this.userName, data);
    }

    public async stakeSlot(data: any): Promise<boolean> {
        return await this.takeAction("atomicassets", "transfer", this.userName, data);
    }

    // REPAIR

    public async repairStamina(data: any): Promise<boolean> {
        return await this.takeAction("clashdometkn", "transfer", this.userName, data);
    }

    public async repairAccountBattery(data: any): Promise<boolean> {
        return await this.takeAction("clashdometkn", "transfer", this.userName, data);
    }

    public async repairBattery(data: any): Promise<boolean> {
        return await this.takeAction("clashdometkn", "transfer", this.userName, data);
    }

    public async repairIntegrity(data: any): Promise<boolean> {
        return await this.takeAction("clashdometkn", "transfer", this.userName, data);
    }

    // CLAIM

    public async claimTool(data: any): Promise<boolean> {
        return await this.takeAction(GameConstants.CONTRACT_NAME, "claimtool", this.userName, data);
    }
    public async claimCredits(data: any): Promise<boolean> {
        return await this.takeAction(GameConstants.CONTRACT_NAME, "claim", this.userName, data);
    }

    // WITHDRAW

    public async withdrawTokens(data: any): Promise<boolean> {
        return await this.takeAction(GameConstants.CONTRACT_NAME, "withdraw", this.userName, data);
    }

    // GIGASWAP

    public async gigaswapTokens(data: any): Promise<any> {
        return await this.takeActionWithError("clashdometkn", "transfers", this.userName, data);
    }

    public async checkGigaswapState(): Promise<any> {

        return await this.getTableRows(GameConstants.CONTRACT_NAME, "gigaswap", 1, 1, "i64", this.userName, this.userName, 0);
    }

    // DEPOSIT

    public async depositTokens(data: any): Promise<boolean> {
        return await this.takeMultipleActions("clashdometkn", "transfer", this.userName, data);
    }

    // CHANGE BACKGROUND

    public async changeBackground(data: any): Promise<boolean> {
        return await this.takeAction("clashdomewld", "setdecdata", this.userName, data);
    }

    public async changeDecorationPositions(data: any): Promise<boolean> {
        return await this.takeAction("clashdometkn", "transfer", this.userName, data);
    }

    // LAUNCH GAMES

    public launchGame(url: string): void {
        this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "launch-game", data: url } });
    }

    // ADD FRIEND
    public async addFriend(data: any): Promise<boolean> {
        return await this.takeAction("clashdometkn", "transfer", data.username, { from: data.username, to: "clashdomewld", quantity: "100.0000 CDJIGO", memo: "send_friend_request:" + data.adversary });
    }

    // PRIVATE

    private async getCurrencyBalance(contract, username, token, count) {

        const rpc = new JsonRpc(this.urls[this.urls_i], { fetch });
        this.urls_i = this.urls_i === this.urls.length - 1 ? 0 : this.urls_i + 1;

        try {

            return await rpc.get_currency_balance(contract, username, token);

        } catch (err) {

            if (count !== this.urls.length - 1) {
                return this.getCurrencyBalance(contract, username, token, count + 1);
            } else {
                return { "error": err };
            }
        }
    }

    private async getTableRows(contract, table, limit, index_position, key_type, lower_bound, upper_bound, count) {

        const rpc = new JsonRpc(this.urls[this.urls_i], { fetch });
        this.urls_i = this.urls_i === this.urls.length - 1 ? 0 : this.urls_i + 1;

        try {

            let result = await rpc.get_table_rows({
                json: true,
                code: contract,
                scope: contract,
                table: table,
                limit: limit,
                index_position: index_position,
                key_type: key_type,
                lower_bound: lower_bound,
                upper_bound: upper_bound,
                reverse: true,
                show_payer: false,
            });

            return result.rows;

        } catch (err) {
            if (count !== this.urls.length - 1) {
                return this.getTableRows(contract, table, limit, index_position, key_type, lower_bound, upper_bound, count + 1);
            } else {
                return { "error": err };
            }
        }
    }

    private async takeAction(contract: string, action_name: string, username: string, data: any): Promise<any> {

        let result;

        try {
            let actions = [{
                account: contract,
                name: action_name,
                authorization: [{
                    actor: username,
                    permission: "active",
                }],
                data: data
            }];

            if (this.wax.type === "wcw") {
                result = await this.wax.api.transact({
                    actions: actions
                }, {
                    blocksBehind: 3,
                    expireSeconds: 30,
                });
            } else if (this.wax.type === "anchor") {
                result = await this.wax.signTransaction({
                    actions: actions
                }, {
                    blocksBehind: 3,
                    expireSeconds: 30,
                });
            }
        } catch (e) {
            console.log(e);
            this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "transaction", data: { error: true, message: e.message } } });
            return false;
        }

        if (result) {
            this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "transaction", data: { error: false, message: "" } } });
            return true;
        }
    }

    private async takeActionWithError(contract: string, action_name: string, username: string, data: any): Promise<any> {

        let result;

        try {
            let actions = [{
                account: contract,
                name: action_name,
                authorization: [{
                    actor: username,
                    permission: "active",
                }],
                data: data
            }];

            if (this.wax.type === "wcw") {
                result = await this.wax.api.transact({
                    actions: actions
                }, {
                    blocksBehind: 3,
                    expireSeconds: 30,
                });
            } else if (this.wax.type === "anchor") {
                result = await this.wax.signTransaction({
                    actions: actions
                }, {
                    blocksBehind: 3,
                    expireSeconds: 30,
                });
            }
        } catch (e) {
            this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "transaction", data: { error: true, message: e.message } } });
            return { error: true, message: e.message };
        }

        if (result) {
            this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "transaction", data: { error: false, message: "" } } });
            return true;
        }
    }

    private async takeMultipleActions(contract: string, action_name: string, username: string, data: any[],): Promise<any> {

        let result;

        try {
            let actions = [];

            for (let i = 0; i < data.length; i++) {

                actions.push({
                    account: contract,
                    name: action_name,
                    authorization: [{
                        actor: username,
                        permission: "active",
                    }],
                    data: data[i]
                })
            }

            if (this.wax.type === "wcw") {
                result = await this.wax.api.transact({
                    actions: actions
                }, {
                    blocksBehind: 3,
                    expireSeconds: 30,
                });
            } else if (this.wax.type === "anchor") {
                result = await this.wax.signTransaction({
                    actions: actions
                }, {
                    blocksBehind: 3,
                    expireSeconds: 30,
                });
            }
        } catch (e) {
            this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "transaction", data: { error: true, message: e.message } } });
            return false;
        }

        if (result) {
            this.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "transaction", data: { error: false, message: "" } } });
            return true;
        }
    }

    private async sleep(ms): Promise<any> {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
}
