import { GameManager } from "../GameManager";
import { Game } from "../Game";
import { GameConstants } from "../GameConstants";
import { GameVars } from "../GameVars";

export class BootScene extends Phaser.Scene {

    public static currentInstance: BootScene;

    public static onOrientationChange(): void {

        BootScene.currentInstance.time.addEvent({
            delay: 100, callback: () => {
                GameVars.scaleX = (GameConstants.GAME_WIDTH / GameConstants.GAME_HEIGHT) / (window.innerWidth / window.innerHeight);
                Game.currentInstance.scale.refresh();
            }, callbackScope: BootScene.currentInstance
        });
    }

    constructor() {

        super("BootScene");

        BootScene.currentInstance = this;
    }

    public preload(): void {

        this.load.image("logo", "assets/logo.png");
        this.load.image("floor_06", "assets/floor_06.png");
        this.load.image("wall_01", "assets/wall_01.png");
    }

    public create(): void {

        GameManager.setCurrentScene(this);

        this.scene.setVisible(false);

        if (GameVars.currentScene.sys.game.device.os.desktop) {
            GameManager.init();
        } else {
            GameManager.init();
        }
    }
}
