import { GameConstants } from "../../GameConstants";

export class LoadLayer extends Phaser.GameObjects.Container {

    private progressBar: Phaser.GameObjects.Graphics;
    private text: Phaser.GameObjects.Text;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.x = GameConstants.GAME_WIDTH / 2;
        this.y = GameConstants.GAME_HEIGHT / 2;

        const logo = new Phaser.GameObjects.Image(this.scene, 0, 0, "logo");
        this.add(logo);

        this.progressBar = new Phaser.GameObjects.Graphics(this.scene);
        this.progressBar.fillGradientStyle(0xFF0098, 0xFF0098, 0x00DBFF, 0x00DBFF);
        this.progressBar.fillRect(0, 0, 8, 400);
        this.progressBar.angle = -90;
        this.progressBar.x = -200;
        this.progressBar.y = 54;
        this.progressBar.scaleY = 1;
        this.add(this.progressBar);

        this.text = new Phaser.GameObjects.Text(this.scene, 0, 83, "COMMUNICATING WITH THE DOME", {fontFamily: "Oswald-Light", fontSize: "22px", color: "#01183c", align: "center"});
        this.text.setOrigin(.5);
        this.add(this.text);
    }

    public changeText(newText: string): void {

        this.text.text = newText;
    }
}