import { NinePatch } from "@koreez/phaser3-ninepatch";
import { Scene } from "phaser";

export class GameBoxButton extends Phaser.GameObjects.Container {

    private bg: NinePatch;
    private gameData: { frame: string, ludio: number, multiplier: number, callback: () => void };

    public get GameData(): { frame: string, ludio: number, multiplier: number, callback: () => void } {

        return this.gameData;
    }

    constructor(scene: Scene, x: number, y: number, size: number, key: string, data: { frame: string, ludio: number, multiplier: number, disableTitleAutoFit?: boolean, disabled?: boolean, comingSoon?: boolean, callback: () => void }) {

        super(scene, x, y);

        this.gameData = data;

        this.bg = new NinePatch(scene, 0, 0, size, size, "texture_atlas_1", "game_box_idle", { top: 10, bottom: 10, left: 10, right: 10 });
        this.add(this.bg);

        if (!data.disabled) {
            this.setInteractive({
                hitArea: new Phaser.Geom.Rectangle(-size / 2, -size / 2, size, size),
                hitAreaCallback: Phaser.Geom.Rectangle.Contains,
                useHandCursor: true,
            });
        }

        this.on(Phaser.Input.Events.GAMEOBJECT_POINTER_OVER, this.onOver, this);
        this.on(Phaser.Input.Events.GAMEOBJECT_POINTER_OUT, this.onOut, this);

        const title = new Phaser.GameObjects.Image(this.scene, 0, 0, key, data.frame);
        this.add(title);

        const padding = 5;

        title.setDisplaySize(this.bg.displayWidth - padding, title.height * (this.bg.displayWidth - padding) / title.width);

        if (data.disableTitleAutoFit) {
            title.setDisplaySize(this.bg.displayWidth / 4, title.height * (this.bg.displayWidth / 4) / title.width);
        }


        if (!data.ludio && !data.multiplier && !data.comingSoon) {
            return;
        }
        const line = new Phaser.GameObjects.Line(this.scene, 0, this.bg.displayHeight / 4, 0, 0, this.bg.displayWidth - 8, 0, 0xcccccc, 0.2);
        line.width = 1;
        this.add(line);
        const bottomY = ((this.bg.displayHeight / 4) + (this.bg.displayHeight / 2)) / 2;

        if (!!data.comingSoon) {
            const cText = new Phaser.GameObjects.Text(this.scene, 0, bottomY, `COMING SOON`, { fontFamily: "Oswald-ExtraLight", fontSize: `${this.bg.displayHeight / 8}px`, color: "#6083b0" });
            this.add(cText);
            cText.setOrigin(0.5);
            title.y = ((this.bg.displayHeight / 4) + (this.bg.displayHeight / 2)) / 2 - this.bg.displayHeight / 2;
            return;
        }



        title.y = ((this.bg.displayHeight / 4) + (this.bg.displayHeight / 2)) / 2 - this.bg.displayHeight / 2;


        const roundedLudio = Number(data.ludio).toFixed(1);

        const ludioText = new Phaser.GameObjects.Text(this.scene, -this.bg.displayWidth / 2 + padding, bottomY, `${roundedLudio}`, { fontFamily: "Oswald-ExtraLight", fontSize: `${this.bg.displayHeight / 8}px`, color: "#FFFF00" });
        ludioText.setOrigin(0, 0.5);
        this.add(ludioText);
        if (!data.ludio) {
            ludioText.setVisible(false);
        }

        const ludioIcon = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "icon_credits");
        this.add(ludioIcon);

        ludioIcon.displayHeight = ludioText.displayHeight;
        ludioIcon.displayWidth = ludioIcon.width * ludioIcon.displayHeight / ludioIcon.height;
        ludioIcon.x = ludioText.x + ludioText.displayWidth + ludioIcon.displayWidth / 2 + padding / 2;
        ludioIcon.y = ludioText.y;

        const multText = new Phaser.GameObjects.Text(this.scene, this.bg.displayWidth / 2 - padding, bottomY, `x${data.multiplier}`, { fontFamily: "Oswald-ExtraLight", fontSize: `${this.bg.displayHeight / 8}px`, color: "#66dae1" });
        ludioText.setOrigin(0, 0.5);
        this.add(multText);

        multText.setOrigin(1, 0.5);
    }


    private onOver(): void {

        this.bg.setFrame("game_box_hover");
    }

    private onOut(): void {

        this.bg.setFrame("game_box_idle");
    }
}
