import { ButtonInfo } from "./ButtonInfo";
import { Button } from "./../../utils/Button";
import { GameVars } from "../../GameVars";
import { GameManager } from "../../GameManager";
import { LedText } from "./LedText";

export class Piggybank extends Phaser.GameObjects.Container {

    private btnClaim: Button;
    private btnBattery: ButtonInfo;
    private unclaimedText: LedText;
    private percentText: Phaser.GameObjects.BitmapText;
    private pig: Phaser.GameObjects.Sprite;
    private uiLayer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, uiLayer: Phaser.GameObjects.Container) {

        super(scene);
        this.uiLayer = uiLayer;

        this.y = 90;

        let light = new Phaser.GameObjects.Image(this.scene, -10, -90, "texture_atlas_1", "piggybank_04");
        this.add(light);

        let baseName = "pig_2_a";

        if (GameVars.stakedWallets && GameVars.stakedWallets.length) {

            if (GameVars.stakedWallets[0].template_id === 398061 || GameVars.stakedWallets[0].template_id === 358834) {
                baseName = "pig_2_c";
            } else {
                baseName = "pig_2_b";
            }
        }

        let base = new Phaser.GameObjects.Image(this.scene, -10, -90, "texture_atlas_1", baseName);
        this.add(base);

        this.pig = this.scene.add.sprite(0, -80, "texture_atlas_1", "piggy_01");
        this.pig.flipX = true;
        this.add(this.pig);

        this.pig.play("pig_anim");

        if (!GameVars.visitMode) {

            this.unclaimedText = new LedText(this.scene, 0, -190, GameVars.formatNumber(GameVars.playerInfo.unclaimed_credits) + " ¢", 30, 0xedef35);
            this.add(this.unclaimedText);

            let max_unclaimed_credits = GameVars.configInfo.max_unclaimed_credits;

            if (GameVars.stakedWallets.length) {
                max_unclaimed_credits += GameVars.walletsConfigInfo.find(x => x.template_id === GameVars.stakedWallets[0].template_id).extra_capacity;
            }

            this.percentText = new Phaser.GameObjects.BitmapText(this.scene, 55, -150, "LED_counter_7", Math.round((GameVars.playerInfo.unclaimed_credits / max_unclaimed_credits) * 100) + " %");
            this.percentText.setFontSize(28);
            this.percentText.setTint(0x68f3ff);
            this.percentText.setOrigin(0, .5);
            this.add(this.percentText);

            this.btnClaim = new Button(this.scene, this.x + 0, this.y + 135, "texture_atlas_1", "btn_claim_idle", "btn_claim_over");
            this.btnClaim.onDown(() => {
                this.onClickClaim(this.btnClaim);
            }, this);
            this.uiLayer.add(this.btnClaim);

            this.btnBattery = new ButtonInfo(this.scene, this.x + 0, this.y - 270, "battery", GameVars.configInfo.max_battery, GameVars.playerInfo.battery, () => {
                this.onClickBattery(this.btnBattery);
            }, this);
            this.uiLayer.add(this.btnBattery);

            if (!GameVars.playerInfo.unclaimed_credits || !GameVars.citizenData) {
                this.btnClaim.input.enabled = false;
                this.btnClaim.alpha = .25;
            }
            this.once(Phaser.GameObjects.Events.DESTROY, this.destroyed, this);

        }
    }

    public enterEditMode(): void {

        this.alpha = .3;
        this.btnBattery.setVisible(false);
        this.btnClaim.setVisible(false);
        this.unclaimedText.setVisible(false);
        this.percentText.setVisible(false);
    }

    public exitEditMode(): void {

        this.alpha = 1;
        this.btnBattery.setVisible(true);
        this.btnClaim.setVisible(true);
        this.unclaimedText.setVisible(true);
        this.percentText.setVisible(true);
    }

    public updateBattery(): void {

        this.btnBattery.updateValue(GameVars.playerInfo.battery);
    }

    public updateValues(): void {

        this.btnBattery.updateValue(GameVars.playerInfo.battery);
        this.unclaimedText.text = (GameVars.formatNumber(GameVars.playerInfo.unclaimed_credits) + " ¢");

        if (!GameVars.playerInfo.unclaimed_credits) {
            this.btnClaim.input.enabled = false;
            this.btnClaim.alpha = .25;
        } else {
            this.btnClaim.input.enabled = true;
            this.btnClaim.alpha = 1;
        }
    }

    public onPiggyClaimAnimationEnd(): void {

        this.pig.visible = true;
    }

    public enableClaim(): void {

        this.btnClaim.alpha = 1;
        this.btnClaim.input.enabled = true;
    }

    public async updatePiggyAmount(amount: number) {
        this.unclaimedText.text = GameVars.formatNumber((GameVars.playerInfo.unclaimed_credits - amount)) + " ¢";
        const newAmount = new LedText(
            this.scene,
            0, -230,
            `${GameVars.formatNumber(amount)}¢`,
            30,
            0xedef35
        );
        newAmount.setScale(0.2);
        this.add(newAmount);
        await new Promise(r =>
            this.scene.add.tween({
                targets: newAmount,
                scale: 1.5,
                duration: 400,
                ease: Phaser.Math.Easing.Quadratic.Out,
                onComplete: r
            })
        );
        await new Promise(r => {
            this.scene.add.tween({
                targets: newAmount,
                scale: 0.2,
                alpha: 0,
                y: this.unclaimedText.y,
                duration: 1000,
                delay: 700,
                ease: Phaser.Math.Easing.Quadratic.InOut,
            });
            setTimeout(r, 1200);
        });

        this.unclaimedText.text = GameVars.formatNumber((GameVars.playerInfo.unclaimed_credits)) + " ¢";

        await new Promise(r => {
            this.scene.add.tween({
                targets: this.unclaimedText,
                scale: 1.1,
                duration: 300,
                angle: "-=10",
                ease: Phaser.Math.Easing.Quadratic.Out,
                onComplete: r
            });
            this.scene.add.tween({
                targets: this.pig,
                scale: 1.1,
                duration: 300,
                angle: "+=10",
                ease: Phaser.Math.Easing.Quadratic.Out,
            });
        });
        await new Promise(r => {
            this.scene.add.tween({
                targets: this.unclaimedText,
                scale: 0.9,
                duration: 300,
                angle: "+=5",
                ease: Phaser.Math.Easing.Quadratic.In,
                onComplete: r
            });
            this.scene.add.tween({
                targets: this.pig,
                scale: 0.9,
                duration: 300,
                angle: "-=5",
                ease: Phaser.Math.Easing.Quadratic.In,
            });
        });
        await new Promise(r => {
            this.scene.add.tween({
                targets: this.unclaimedText,
                scale: 1,
                duration: 150,
                angle: 0,
                ease: Phaser.Math.Easing.Linear,
                onComplete: r
            });
            this.scene.add.tween({
                targets: this.pig,
                scale: 1,
                duration: 150,
                angle: 0,
                ease: Phaser.Math.Easing.Linear,
                onComplete: r
            });
        });
    }

    public onClaimTransactionEnd(): void {

        let max_unclaimed_credits = GameVars.configInfo.max_unclaimed_credits;

        if (GameVars.stakedWallets.length) {
            max_unclaimed_credits += GameVars.walletsConfigInfo.find(x => x.template_id === GameVars.stakedWallets[0].template_id).extra_capacity;
        }

        let currentUnclaimed = parseFloat(this.unclaimedText.text.replace(/,/g, ""));
        let currentPercent = parseFloat(this.percentText.text.replace(/,/g, ""));
        let finalPercent = Math.round((Math.max(0, currentUnclaimed - max_unclaimed_credits) / max_unclaimed_credits) * 100);

        if (finalPercent !== 0) {
            this.btnClaim.alpha = 1;
            this.btnClaim.input.enabled = true;
        }

        let tween = this.scene.tweens.addCounter({
            from: currentUnclaimed,
            to: Math.max(0, currentUnclaimed - max_unclaimed_credits),
            duration: 1000,
            onUpdate: () => {

                this.unclaimedText.text = (Math.floor(tween.getValue()) + " ¢");
            },
            onUpdateScope: this
        });

        let tween2 = this.scene.tweens.addCounter({
            from: currentPercent,
            to: finalPercent,
            duration: 1000,
            onUpdate: () => {

                this.percentText.text = Math.floor(tween2.getValue()) + " %";
            },
            onUpdateScope: this
        });

        setTimeout(() => {
            GameVars.onAnimation = false;
            GameManager.updatePlayerValues();
        }, 1000);
    }

    private async onClickClaim(btn: Button): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .25;
        btn.input.enabled = false;

        this.onClaimStart();
    }

    private async onClickBattery(btn: ButtonInfo): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .25;
        btn.setInputEnabled(false);
        btn.showRepairButton();

        let result = await GameManager.repairAccountBattery((btn.maxValue - btn.currentValue) / GameVars.configInfo.jigowatts_to_battery);

        if (!result) {
            btn.alpha = 1;
            btn.setInputEnabled(true);
        } else {
            btn.alpha = 1;

            setTimeout(() => {
                GameManager.updatePlayerValues();
            }, 1000);
        }

        GameVars.onAnimation = false;
    }

    private onClaimStart(): void {

        GameManager.piggyClaimAnimation();

        this.pig.visible = false;
        this.btnClaim.input.enabled = false;
        this.btnClaim.alpha = .25;
    }

    private destroyed() {
        this.uiLayer.remove([this.btnClaim, this.btnBattery]);
    }
}