export class ArcadeModding extends Phaser.GameObjects.Container {

    private img: Phaser.GameObjects.Image;
    private anim: Phaser.GameObjects.Sprite;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.visible = false;

        this.img = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_3", "");
        this.add(this.img);

        this.once(Phaser.GameObjects.Events.DESTROY, this.onDestroy, this);
    }

    public setMod(data: any, modType: string) {

        this.visible = true;

        this.img.setFrame("arcade_" + modType + "_" + data.id);
        if (modType === "side_back") {
            return;
        }

        if (data.animation) {
            let time = new Date().getTime();
            for (let i = 0; i < data.animation.length; i++) {
                time += 1;
                let extra = data.animation[i].id ? data.animation[i].id + "_" : "";

                this.scene.anims.create({ key: "animation_" + data.animation[i].id + "_" + modType + "_" + time, frames: this.scene.anims.generateFrameNames("texture_atlas_3", { prefix: "arcade_" + data.type + "_" + data.id + "_" + extra, start: 1, end: data.animation[i].frames, zeroPad: 2 }), frameRate: 24, repeat: -1 });

                this.anim = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "");
                this.anim.play("animation_" + data.animation[i].id + "_" + modType + "_" + time);

                this.scene.add.existing(this.anim);
                this.add(this.anim);
            }
        }
    }

    private onDestroy() {
        this.anim?.destroy();
        this.img.destroy();
    }
}
