import { GameConstants } from "../../../GameConstants";
import { GameVars } from "../../../GameVars";
import { Column } from "../Column";
import { CarbzSlot } from "./CarbzSlot";
import { DrinkMachine } from "./DrinkMachine";

export class CarbzZone extends Phaser.GameObjects.Container {

    private column: Column;
    private drinkMachines: DrinkMachine[];
    private slots: CarbzSlot[];

    private uiLayer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, uiLayer: Phaser.GameObjects.Container) {

        super(scene);
        this.uiLayer = uiLayer;

        this.drinkMachines = [];
        this.slots = [];

        let count = 0;

        if (GameVars.stakedDrink) {
            for (let i = 0; i < GameVars.stakedDrink.length; i++) {

                let drinkMachine = new DrinkMachine(this.scene, i, this.uiLayer);
                this.add(drinkMachine);
                this.drinkMachines.push(drinkMachine);
                count++;
            }
        }

        while (count < 3 && GameVars.stakedSlotsDrink && GameVars.stakedSlotsDrink.length >= count) {

            let carbzSlot = new CarbzSlot(this.scene, count, false);
            this.uiLayer.add(carbzSlot);
            this.sendToBack(carbzSlot);
            this.slots.push(carbzSlot);
            count++;
        }

        if (count < 3) {

            let carbzSlot = new CarbzSlot(this.scene, count, true, 0);
            this.uiLayer.add(carbzSlot);
            this.sendToBack(carbzSlot);
            this.slots.push(carbzSlot);
            count++;
        }

        if (count < 3) {
            let carbzSlot = new CarbzSlot(this.scene, count, true, 1);
            this.uiLayer.add(carbzSlot);
            this.sendToBack(carbzSlot);
            this.slots.push(carbzSlot);
        }

        this.column = new Column(this.scene, - GameConstants.GAME_WIDTH * (3 / 2), 0);
        this.column.setColors();
        this.add(this.column);
        this.sendToBack(this.column);
    }

    public setColumns(value?: number): void {

        this.column.setColors(value);

        for (let i = 0; i < this.slots.length; i++) {
            this.slots[i].setColumnColors(value);
        }
    }

    public enterEditMode(): void {

        for (let i = 0; i < this.slots.length; i++) {
            this.slots[i].setVisible(false);
        }

        for (let i = 0; i < this.drinkMachines.length; i++) {
            this.drinkMachines[i].enterEditMode();
        }
    }

    public exitEditMode(): void {

        for (let i = 0; i < this.slots.length; i++) {
            this.slots[i].setVisible(true);
        }

        for (let i = 0; i < this.drinkMachines.length; i++) {
            this.drinkMachines[i].exitEditMode();
        }
    }

    public onDrinkClaimAnimationEnd(): void {

        setTimeout(() => {
            for (let i = 0; i < this.drinkMachines.length; i++) {
                this.remove(this.drinkMachines[i], true);
            }

            this.drinkMachines = [];

            for (let i = 0; i < GameVars.stakedDrink.length; i++) {

                let drinkMachine = new DrinkMachine(this.scene, i, this.uiLayer);
                this.add(drinkMachine);
                this.drinkMachines.push(drinkMachine);
            }
        }, 500);
    }

    public onClaimDrinkMachineTransactionEnd(index: number): void {

        this.drinkMachines[index].onClaimEnd();
    }

    public enableClaim(index: number): void {

        this.drinkMachines[index].enableClaim();
    }

    destroy(fromScene?: boolean): void {
        this.uiLayer.remove(this.slots);
        super.destroy(fromScene);
    }
}
