import { GameConstants } from "../../GameConstants";

export class FireContainer extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.y = GameConstants.GAME_HEIGHT;

        const flame1 = new Phaser.GameObjects.Sprite(this.scene, -170, 40, "texture_atlas_1");
        flame1.setOrigin(.5, 1);
        flame1.setScale(-2.5, 2.5);
        this.scene.add.existing(flame1);
        flame1.play("flames");
        this.add(flame1);

        let startFrame = flame1.anims.currentAnim.frames[Math.floor(12 * Math.random())];
        flame1.anims.setCurrentFrame(startFrame);

        const flame2 = new Phaser.GameObjects.Sprite(this.scene, -90, 20, "texture_atlas_1");
        flame2.setOrigin(.5, 1);
        flame2.setScale(2);
        this.scene.add.existing(flame2);
        flame2.play("flames");
        this.add(flame2);

        startFrame = flame2.anims.currentAnim.frames[Math.floor(12 * Math.random())];
        flame2.anims.setCurrentFrame(startFrame);

        const flame3 = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1");
        flame3.setOrigin(.5, 1);
        flame3.setScale(2.5);
        this.scene.add.existing(flame3);
        flame3.play("flames");
        this.add(flame3);

        startFrame = flame3.anims.currentAnim.frames[Math.floor(12 * Math.random())];
        flame3.anims.setCurrentFrame(startFrame);

        const flame4 = new Phaser.GameObjects.Sprite(this.scene, 90, 20, "texture_atlas_1");
        flame4.setOrigin(.5, 1);
        flame4.setScale(2);
        this.scene.add.existing(flame4);
        flame4.play("flames");
        this.add(flame4);

        startFrame = flame4.anims.currentAnim.frames[Math.floor(12 * Math.random())];
        flame4.anims.setCurrentFrame(startFrame);

        const flame5 = new Phaser.GameObjects.Sprite(this.scene, 170, 40, "texture_atlas_1");
        flame5.setOrigin(.5, 1);
        flame5.setScale(-2.5, 2.5);
        this.scene.add.existing(flame5);
        flame5.play("flames");

        startFrame = flame5.anims.currentAnim.frames[Math.floor(12 * Math.random())];
        flame5.anims.setCurrentFrame(startFrame);
        
        this.add(flame5);
    }

    public onBagContainerFallen(): void {

        this.scene.tweens.add({
            targets: this,
            scaleX: 1.2,
            scaleY: 1.5,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 390,
            onComplete: function(): void {
                this.scene.tweens.add({
                    targets: this,
                    scaleX: 1,
                    scaleY: 1,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 1500
                });
            },
            onCompleteScope: this
        });
    }

    public onBagGrowing(): void {

        this.scene.tweens.add({
            targets: this,
            scaleX: .9,
            scaleY: .8,
            y: this.y + 30,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500
        });
    }
}

