    public static async loadBlockchainValues(): Promise<void> {

        let username = GameVars.userName;

        GameVars.socialData = await GameVars.api.getSocialData(GameVars.reactClass?.state.wax.userAccount);

        if (GameVars.visitMode) {

            if (!GameConstants.DEVELOPMENT) {
                GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "fetch-visitors", data: null } });
            }

            GameVars.citizenData = null;
            GameVars.playerInfo = null;

            GameManager.resetValues();

            GameManager.getVisitorData();

            MainScene.currentInstance.blockchainValuesLoaded();

            let playerInfo = await GameVars.api.getPlayerInfo(GameVars.visitMode);

            if (!this.checkUsername(username)) {
                return;
            }

            if (playerInfo && playerInfo.error) {
                // TODO: gestiona error
                // console.log("ERROR");
                // console.log(playerInfo.error);
            } else if (!playerInfo) {
                GameManager.showInitialCitizen();
            } else {

                if (playerInfo.trial) {

                    GameVars.trialInfo = playerInfo.trial;

                    if (GameVars.trialInfo.staked) {
                        GameManager.showTrialCitizen();
                    } else {
                        GameManager.showInitialCitizen();
                    }

                } else {

                    GameVars.playerInfo = {
                        account: playerInfo.player.account,
                        credits: parseFloat(playerInfo.player.balances[0]),
                        carbs: parseFloat(playerInfo.player.balances[1]),
                        jigowatts: parseFloat(playerInfo.player.balances[2]),
                        stamina: playerInfo.player.stamina,
                        battery: playerInfo.player.battery,
                        unclaimed_actions: playerInfo.player.unclaimed_actions,
                        unclaimed_credits: parseFloat(playerInfo.player.unclaimed_credits),
                        type: playerInfo.citizen ? playerInfo.citizen.type : ""
                    };

                    GameManager.playerValuesLoaded(playerInfo, true, username);
                }
            }

        } else {

            GameVars.gameRewards = await GameVars.api.getGameRewardData();

            await GameManager.sleep(250);

            let configInfo = await GameVars.api.getConfigInfo();

            await GameManager.sleep(250);

            let citizensInfo = await GameVars.api.getCitizenConfigInfo();

            await GameManager.sleep(250);

            MainScene.currentInstance.changeLoadText("RETRIEVING CITIZEN'S APPEARANCE");

            let toolsConfigInfo = await GameVars.api.getToolsConfigInfo();

            await GameManager.sleep(250);

            let walletsConfigInfo = await GameVars.api.getWalletsConfigInfo();

            await GameManager.sleep(250);

            let playerInfo = await GameVars.api.getPlayerInfo(GameVars.visitMode);

            if (!this.checkUsername(username)) {
                return;
            }

            GameVars.configInfo = configInfo;
            GameVars.citizensInfo = citizensInfo;
            GameVars.toolsConfigInfo = toolsConfigInfo;
            GameVars.walletsConfigInfo = walletsConfigInfo;

            if (playerInfo && playerInfo.error) {
                // TODO: gestiona error
                // console.log("ERROR");
                // console.log(playerInfo.error);
            } else if (!playerInfo) {
                GameManager.showInitialCitizen();
            } else {

                if (playerInfo.trial) {

                    GameVars.trialInfo = playerInfo.trial;

                    if (GameVars.trialInfo.staked) {
                        GameManager.showTrialCitizen();
                    } else {
                        GameManager.showInitialCitizen();
                    }

                } else {

                    GameVars.playerInfo = {
                        account: playerInfo.player.account,
                        credits: parseFloat(playerInfo.player.balances[0]),
                        carbs: parseFloat(playerInfo.player.balances[1]),
                        jigowatts: parseFloat(playerInfo.player.balances[2]),
                        stamina: playerInfo.player.stamina,
                        battery: playerInfo.player.battery,
                        unclaimed_actions: playerInfo.player.unclaimed_actions,
                        unclaimed_credits: parseFloat(playerInfo.player.unclaimed_credits),
                        type: playerInfo.citizen ? playerInfo.citizen.type : ""
                    };

                    GameManager.playerValuesLoaded(playerInfo, false, username);
                }
            }
        }
    }

    public static getVisitorData(): void {

        let data = GameVars.visitorsData ? GameVars.visitorsData[GameVars.userName] : null;

        if (data) {

            let decorationData = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? require("../assets/decoration_testnet.json") : require("../assets/decoration.json");

            Object.values(data).forEach(function (value: any) {

                if (decorationData.avatar[value.id]) {
                    GameVars.stakedAvatarDecoration.push(decorationData.avatar[value.id]);
                    GameVars.stakedDecoration.push({ template_id: value.id });
                } else if (decorationData.decoration[value.id]) {
                    if (!value.data) {
                        value.data = '{"wall": 0, "floor": 5}';
                    }
                    GameVars.stakedApartmentCustom = { template_id: value.id, data: value.data };
                    GameVars.backgroundValues = JSON.parse(GameVars.stakedApartmentCustom.data);
                    GameVars.stakedDecoration.push({ template_id: value.id, data: value.data });

                } else if (decorationData.items[value.id] || decorationData.arcade[value.id]) {
                    GameVars.stakedDecoration.push({ template_id: value.id, data: value.data });
                } else if (decorationData.tools_carbz[value.id]) {
                    GameVars.stakedDrink.push(decorationData.tools_carbz[value.id]);
                } else if (decorationData.tools_jigos[value.id]) {
                    GameVars.stakedTreadmill.push(decorationData.tools_jigos[value.id]);
                } else if (decorationData.wallets[value.id]) {
                    GameVars.stakedWallets.push({ template_id: value.id });
                } else if (decorationData.textures.floors[value.id] || decorationData.textures.walls[value.id] || decorationData.textures.baseboards[value.id] || decorationData.textures.ceiling[value.id]) {
                    GameVars.stakedDecoration.push({ template_id: value.id, data: value.data });
                }
            });
        }
    }

