    public static async playerValuesLoaded(playerInfo: any, visitor: boolean, username: string): Promise<void> {

        if (playerInfo.citizen) {
            let citizen = await GameVars.api.getCitizenData(playerInfo.citizen.citizen_id);

            await GameManager.sleep(250);

            if (!this.checkUsername(username)) {
                return;
            }

            GameVars.citizenData = citizen.data;
        } else {

            GameVars.citizenData = null;
            GameManager.showInitialCitizen();
        }

        if (!visitor) {
            await GameManager.getChestInfo();
        }

        MainScene.currentInstance.blockchainValuesLoaded(visitor);
    }

    public static async getChestInfo(): Promise<void> {

        let username = GameVars.userName;

        MainScene.currentInstance.changeLoadText("CHECKING CITIZEN'S BELONGINGS");

        // GET UNSTAKED SLOTS

        let unstakedSlots = await GameVars.api.getUnstakedSlots();

        await GameManager.sleep(250);

        if (!this.checkUsername(username)) {
            return;
        }

        GameVars.unstakedSlots = unstakedSlots;

        // GET STAKED SLOTS

        let stakedSlots = await GameVars.api.getStakedSlots();

        await GameManager.sleep(250);

        if (!this.checkUsername(username)) {
            return;
        }

        GameVars.stakedSlotsTreadmill = [];
        GameVars.stakedSlotsDrink = [];

        for (let i = 0; i < stakedSlots.length; i++) {
            if (stakedSlots[i].type === GameConstants.JIGOWATTS_NAME) {
                GameVars.stakedSlotsTreadmill.push(stakedSlots[i]);
            } else if (stakedSlots[i].type === GameConstants.CARBZ_NAME) {
                GameVars.stakedSlotsDrink.push(stakedSlots[i]);
            }
        }

        // GET UNSTAKED TOOLS
        let unstakedTools = await GameVars.api.getUnstakedTools();

        await GameManager.sleep(250);

        if (!this.checkUsername(username)) {
            return;
        }

        MainScene.currentInstance.changeLoadText("LOADING APARTMENT'S FRAGANCE");

        GameVars.unstakedTreadmill = [];
        GameVars.unstakedDrink = [];

        for (let i = 0; i < unstakedTools.length; i++) {
            if (unstakedTools[i].data.type === GameConstants.JIGOWATTS_NAME) {
                GameVars.unstakedTreadmill.push(unstakedTools[i]);
            } else if (unstakedTools[i].data.type === GameConstants.CARBZ_NAME) {
                GameVars.unstakedDrink.push(unstakedTools[i]);
            }
        }

        // GET STAKED TOOLS
        await GameManager.getStakedTools();

        await GameManager.sleep(250);

        // GET UNSTAKED WALLETS
        let unstakedWallets = await GameVars.api.getUnstakedWallets();

        await GameManager.sleep(250);

        if (!this.checkUsername(username)) {
            return;
        }

        GameVars.unstakedWallets = unstakedWallets;

        // GET STAKED WALLETS
        let stakedWallets = await GameVars.api.getStakedWallets();

        await GameManager.sleep(250);

        if (!this.checkUsername(username)) {
            return;
        }

        GameVars.stakedWallets = stakedWallets;

        // GET STAKED DECORATION

        let stakedDecoration = await GameVars.api.getStakedDecoration();

        if (!this.checkUsername(username)) {
            return;
        }

        GameVars.stakedDecoration = stakedDecoration;

        let decorationData = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? require("../assets/decoration_testnet.json") : require("../assets/decoration.json");

        GameVars.stakedApartmentCustom = null;

        GameVars.stakedAvatarDecoration = [];

        for (let i = 0; i < GameVars.stakedDecoration.length; i++) {
            if (decorationData.decoration[GameVars.stakedDecoration[i].template_id]) {
                GameVars.stakedApartmentCustom = GameVars.stakedDecoration[i];
            } else if (decorationData.avatar[GameVars.stakedDecoration[i].template_id]) {
                GameVars.stakedAvatarDecoration.push(decorationData.avatar[GameVars.stakedDecoration[i].template_id]);
            }
        }

        if (GameVars.stakedApartmentCustom && GameVars.stakedApartmentCustom.data) {
            GameVars.backgroundValues = JSON.parse(GameVars.stakedApartmentCustom.data);
        } else {
            GameVars.backgroundValues = { floor: 5, wall: 0 };
        }
    }

