    public static withdrawTokens(carbz: number, credits: number, jigo: number): void {

        // BLOQEUAR ENTRADA DE TEXTO
        GameVars.paused = true;

        (async () => {

            let data = { account: GameVars.userName, quantities: [credits.toFixed(4) + " CREDITS", carbz.toFixed(4) + " CARBZ", jigo.toFixed(4) + " JIGO"] };

            let value = await GameVars.api.withdrawTokens(data);

            GameVars.paused = false;

            if (value) {
                ExchangeLayer.currentInstance.onWithdrawCompleted(false);
                setTimeout(() => {
                    GameManager.updatePlayerValues();
                }, 2000);
            } else {
                ExchangeLayer.currentInstance.onWithdrawCompleted(true);
            }
        })();
    }

    public static deposiTokens(cdcarbz: number, ludio: number, cdjigo: number): void {

        GameVars.paused = true;

        (async () => {

            let data = [];

            if (cdcarbz > 0) {
                data.push({
                    from: GameVars.userName,
                    to: "clashdomewld",
                    quantity: cdcarbz.toFixed(4) + " CDCARBZ",
                    memo: "deposit"
                });
            }

            if (ludio > 0) {
                data.push({
                    from: GameVars.userName,
                    to: "clashdomewld",
                    quantity: ludio.toFixed(4) + " LUDIO",
                    memo: "deposit"
                });
            }

            if (cdjigo > 0) {
                data.push({
                    from: GameVars.userName,
                    to: "clashdomewld",
                    quantity: cdjigo.toFixed(4) + " CDJIGO",
                    memo: "deposit"
                });
            }

            let value = await GameVars.api.depositTokens(data);

            GameVars.paused = false;

            if (value) {
                ExchangeLayer.currentInstance.onDepositCompleted(false);
                setTimeout(() => {
                    GameManager.updatePlayerValues();
                }, 2000);
            } else {
                ExchangeLayer.currentInstance.onDepositCompleted(true);
            }
        })();
    }

