    public static showLeftAlert(): boolean {

        if (GameVars.visitMode || GameVars.trialInfo || GameVars.editMode) {
            return false;
        }

        var now = new Date;
        var current_timestamp = Math.floor(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(),
            now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds(), now.getUTCMilliseconds()) / 1000);

        for (let i = 0; i < GameVars.stakedDrink.length; i++) {

            if (GameVars.stakedDrink[i].last_claim + GameVars.toolsConfigInfo.find(x => x.template_id === GameVars.stakedDrink[i].template_id).cooldown <= current_timestamp) {
                return true;
            }
        }

        return false;
    }

    public static showRigthAlert(): boolean {

        if (GameVars.visitMode || GameVars.trialInfo || GameVars.editMode) {
            return false;
        }

        var now = new Date;
        var current_timestamp = Math.floor(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(),
            now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds(), now.getUTCMilliseconds()) / 1000);

        for (let i = 0; i < GameVars.stakedTreadmill.length; i++) {

            if (GameVars.stakedTreadmill[i].last_claim + GameVars.toolsConfigInfo.find(x => x.template_id === GameVars.stakedTreadmill[i].template_id).cooldown <= current_timestamp) {
                return true;
            }
        }

        return false;
    }

