export class InputFieldsContainer {

    private inputElement: Phaser.GameObjects.DOMElement;

    constructor(scene: Phaser.Scene) {

        this.inputElement = scene.add.dom(410, 20).createFromCache("input");
    }

    public hide(): void {

        this.inputElement.visible = false;
    }

    public show(): void {

        this.inputElement.visible = true;

        this.resetIntputFields();
    }

    public resetIntputFields(): void {

        const inputText1 = <any> this.inputElement.getChildByName("inputField1");
        const inputText2 = <any> this.inputElement.getChildByName("inputField2");
        const inputText3 = <any> this.inputElement.getChildByName("inputField3");

        inputText1.value = "0.0";
        inputText2.value = "0.0";
        inputText3.value = "0.0";

        inputText1.onclick = () => {
            if (inputText1.value === "0.0") {
                inputText1.value = "";
            }
        };

        inputText1.addEventListener("focusout", () => {
            if (inputText1.value === "") {
                inputText1.value = "0.0";
            }
        });

        inputText2.onclick = () => {
            if (inputText2.value === "0.0") {
                inputText2.value = "";
            }
        };

        inputText2.addEventListener("focusout", () => {
            if (inputText2.value === "") {
                inputText2.value = "0.0";
            }
        });

        inputText3.onclick = () => {
            if (inputText3.value === "0.0") {
                inputText3.value = "";
            }
        };

        inputText3.addEventListener("focusout", () => {
            if (inputText3.value === "") {
                inputText3.value = "0.0";
            }
        });

        inputText1.focus();
    }

    public disable(): void {

        const inputText1 = <any> this.inputElement.getChildByName("inputField1");
        const inputText2 = <any> this.inputElement.getChildByName("inputField2");
        const inputText3 = <any> this.inputElement.getChildByName("inputField3");

        inputText1.disabled = true;
        inputText2.disabled = true;
        inputText3.disabled = true;
    }

    public enable(): void {

        const inputText1 = <any> this.inputElement.getChildByName("inputField1");
        const inputText2 = <any> this.inputElement.getChildByName("inputField2");
        const inputText3 = <any> this.inputElement.getChildByName("inputField3");

        inputText1.disabled = false;
        inputText2.disabled = false;
        inputText3.disabled = false;
    }

    public setValue(field: number, value: number): void {

        const inputText = <any> this.inputElement.getChildByName("inputField" + field);
        inputText.value = Math.floor(value * 100) / 100;
    }

    public getValues(): {token1: number, token2: number, token3: number} {

        const inputText1 = <any> this.inputElement.getChildByName("inputField1");
        const inputText2 = <any> this.inputElement.getChildByName("inputField2");
        const inputText3 = <any> this.inputElement.getChildByName("inputField3");

        return {
            token1: Number(inputText1.value),
            token2: Number(inputText2.value),
            token3: Number(inputText3.value)
        };
    }
}
