import { GameVars } from './../../GameVars';
import { GameManager } from './../../GameManager';
import { GameConstants } from "../../GameConstants";

export class TransitionLayer extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene, enter: boolean) {

        super(scene);

        this.x = GameConstants.GAME_WIDTH / 2;
        this.y = GameConstants.GAME_HEIGHT / 2;

        let topContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(topContainer);

        let graphics = new Phaser.GameObjects.Graphics(this.scene);
        graphics.fillGradientStyle(0x03222C, 0x03222C, 0x005D7B, 0x005D7B, 1);
        graphics.fillRect(-GameConstants.GAME_WIDTH / 2, -GameConstants.GAME_HEIGHT / 2, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT / 2);
        topContainer.add(graphics);

        let border = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "border_visit");
        border.setOrigin(1, 0);
        border.scaleX = 1.05;
        border.scaleY = -1;
        topContainer.add(border);

        border = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "border_visit");
        border.setOrigin(1, 0);
        border.scaleX = -1.05;
        border.scaleY = -1;
        topContainer.add(border);

        let bottomContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(bottomContainer);

        graphics = new Phaser.GameObjects.Graphics(this.scene);
        graphics.fillGradientStyle(0x005D7B, 0x005D7B, 0x03222C, 0x03222C, 1);
        graphics.fillRect(-GameConstants.GAME_WIDTH / 2, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT / 2);
        bottomContainer.add(graphics);

        border = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "border_visit");
        border.setOrigin(1, 0);
        border.scaleX = 1.05;
        bottomContainer.add(border);

        border = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "border_visit");
        border.setOrigin(1, 0);
        border.scaleX = -1.05;
        bottomContainer.add(border);

        border = new Phaser.GameObjects.Image(this.scene, - GameConstants.GAME_WIDTH / 2, 0, "texture_atlas_1", "border_visit");
        border.setOrigin(.5, 1);
        border.angle = 90;
        border.scaleX = 1.05;
        this.add(border);

        border = new Phaser.GameObjects.Image(this.scene, GameConstants.GAME_WIDTH / 2, 0, "texture_atlas_1", "border_visit");
        border.setOrigin(.5, 0);
        border.angle = 90;
        border.scaleX = 1.05;
        this.add(border);

        if (enter) {

            if (GameVars.showTransition) {
                this.scene.tweens.add({
                    targets: topContainer,
                    y: - GameConstants.GAME_HEIGHT / 2,
                    ease: Phaser.Math.Easing.Cubic.InOut,
                    duration: 2000
                });

                this.scene.tweens.add({
                    targets: bottomContainer,
                    y: GameConstants.GAME_HEIGHT / 2,
                    ease: Phaser.Math.Easing.Cubic.InOut,
                    duration: 2000,
                    onCompleteScope: this,
                    onComplete: () => {
                        if (!GameVars.visitMode) {
                            this.visible = false;
                        }
                    }
                });
            } else {
                GameVars.showTransition = true;
                topContainer.y = - GameConstants.GAME_HEIGHT / 2;
                bottomContainer.y = GameConstants.GAME_HEIGHT / 2;

                if (!GameVars.visitMode) {
                    this.visible = false;
                }
            }

        } else {

            topContainer.y = - GameConstants.GAME_HEIGHT / 2;

            this.scene.tweens.add({
                targets: topContainer,
                y: 0,
                ease: Phaser.Math.Easing.Cubic.InOut,
                duration: 2000
            });

            bottomContainer.y = GameConstants.GAME_HEIGHT / 2;

            this.scene.tweens.add({
                targets: bottomContainer,
                y: 0,
                ease: Phaser.Math.Easing.Cubic.InOut,
                duration: 2000,
                onCompleteScope: this,
                onComplete: () => {
                    GameManager.init();
                }
            });
        }
    }
}
