import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { Button } from "../../utils/Button";
import { ButtonInfo } from "./ButtonInfo";
import { data } from "../../../assets/characterData.json";
import { GameConstants } from "../../GameConstants";

export class Citizen extends Phaser.GameObjects.Container {

    public btnClaim: Button;
    public btnStamina: ButtonInfo;
    public staked: boolean;
    public character: any;

    private characterData: any;
    private isThin: boolean;
    private rarity: string;
    private currentAvatar: any;
    private moveTween: Phaser.Tweens.Tween;

    constructor(scene: Phaser.Scene, x: number, y: number, staked: boolean, visit?: boolean) {

        super(scene);

        this.setPosition(x, y);

        this.characterData = data;
        this.isThin = true;
        this.staked = staked;

        this.character = (this as any).scene.add.spine(0, 0, "avatar");
        this.character.skeleton.setToSetupPose();
        this.character.setSkinByName("default");
        this.add(this.character);


        this.setMixes();

        this.turnRigth();
        this.character.play("idle", true, true);

        if (GameVars.citizenData) {

            this.rarity = GameVars.citizenData.rarity;
            this.idsToSpine(JSON.parse(GameVars.citizenData.data));

            this.addAvatarDecoration();

            if (!GameVars.visitMode) {
                this.btnStamina = new ButtonInfo(this.scene, 0, -370, "stamina", GameVars.citizensInfo.find(x => x.key === GameVars.playerInfo.type).max_stamina, GameVars.playerInfo.stamina, () => {
                    this.onClickStamina(this.btnStamina);
                }, this);
                this.add(this.btnStamina);
            }

            this.setTiredAnimation();
        }

        this.character.state.listeners[0].complete = (trackIndex: any) => {
            if (trackIndex.animation.name === "dance_start") {
                this.character.play("dance_1");
            } else if (trackIndex.animation.name === "dance_1") {
                this.character.play("dance_end");
            } else if (trackIndex.animation.name === "dance_end") {
                this.character.play("idle", true, true);
                this.setTiredAnimation();
                GameManager.onPiggyClaimAnimationEnd();
            } else if (trackIndex.animation.name === "carbz1_claim" || trackIndex.animation.name === "carbz2_claim" || trackIndex.animation.name === "carbz3_claim") {
                this.walkBack();
            }
        };

        if (visit) {
            this.x = - GameConstants.GAME_WIDTH / 2 - 200;
            this.move(x, false);
        }

        if (GameVars.stakedDecoration && GameVars.stakedDecoration.findIndex(d => Number(d.template_id) === 641149) >= 0) {
            this.hideSlot("JACKET");
        }

    }

    public showBuy(): void {

        if (GameVars.visitMode) {

            this.character.visible = false;

            let generic = new Phaser.GameObjects.Image(this.scene, 0, -150, "texture_atlas_1", "generic_avatar");
            generic.setScale(.75);
            this.add(generic);

        } else {

            this.character.visible = false;

            let generic = new Phaser.GameObjects.Image(this.scene, 0, -150, "texture_atlas_1", "generic_avatar");
            generic.setScale(.75);
            this.add(generic);

            let textMore = new Phaser.GameObjects.Text(this.scene, -140, -200, "BUY A\nCITIZEN", { fontFamily: "Oswald-Medium", fontSize: "22px", color: "#FFFFFF", align: "center" });
            textMore.setShadow(2, 2, "#000000", 5);
            textMore.setWordWrapWidth(200);
            textMore.setOrigin(.5);
            this.add(textMore);

            let btnMore = new Button(this.scene, -140, -120, "texture_atlas_1", "btn_plus_yellow", "btn_plus_yellow_over");
            btnMore.onDown(this.onClickBuy, this);
            this.add(btnMore);
        }
    }

    public addAvatarDecoration(): void {

        if (!GameVars.stakedAvatarDecoration) {
            return;
        }

        for (let i = 0; i < GameVars.stakedAvatarDecoration.length; i++) {

            let index = this.character.skeleton.findSlotIndex(GameVars.stakedAvatarDecoration[i].type);

            if (GameVars.stakedAvatarDecoration[i].type === "tops" && !this.isThin) {
                this.character.skeleton.setAttachment(GameVars.stakedAvatarDecoration[i].type, GameVars.stakedAvatarDecoration[i].attachmentName2);
                this.character.skeleton.slots[index].data.attachmentName = GameVars.stakedAvatarDecoration[i].attachmentName2;
            } else {
                this.character.skeleton.setAttachment(GameVars.stakedAvatarDecoration[i].type, GameVars.stakedAvatarDecoration[i].attachmentName);
                this.character.skeleton.slots[index].data.attachmentName = GameVars.stakedAvatarDecoration[i].attachmentName;
            }

            if (GameVars.stakedAvatarDecoration[i].animation) {
                this.character.setAnimation(i + 2, GameVars.stakedAvatarDecoration[i].animation, true);
            }
            let s = this.character.skeleton.findSlot(GameVars.stakedAvatarDecoration[i].type);
            s.color.setFromString("FFFFFF");
            if (s.darkColor) {
                s.darkColor.setFromString("000000");
            }
        }
    }

    public showStake(citizens: any): void {

        if (GameVars.visitMode) {

            this.character.visible = false;

            let generic = new Phaser.GameObjects.Image(this.scene, 0, -150, "texture_atlas_1", "generic_avatar");
            generic.setScale(.75);
            this.add(generic);

        } else {

            this.character.visible = false;

            let generic = new Phaser.GameObjects.Image(this.scene, 0, -150, "texture_atlas_1", "generic_avatar");
            generic.setScale(.75);
            this.add(generic);

            let btnStake = new Button(this.scene, -140, -120, "texture_atlas_1", "btn_stake_yellow", "btn_stake_yellow_over");
            btnStake.onDown(() => {
                this.onClickStake(btnStake, citizens[0].asset_id);
            }, this);
            this.add(btnStake);
        }
    }

    public updateValues(): void {
        if (!this.staked) {
            return;
        }

        if (this.btnStamina) {
            this.btnStamina.updateValue(GameVars.playerInfo.stamina);
        }

        let percent = 1 - (GameVars.playerInfo.stamina / GameVars.citizensInfo.find(x => x.key === GameVars.playerInfo.type).max_stamina)
        this.character.state.tracks[1].alpha = percent;
    }

    public move(x: number, finalLeft: boolean): void {

        if (!this.staked) {
            return;
        }

        if (this.moveTween) {
            this.moveTween.stop();
            this.moveTween = null;
        }

        let time = Math.abs(this.x - x) * 3;

        this.moveTween = this.scene.tweens.add({
            targets: this,
            x: x,
            ease: Phaser.Math.Easing.Linear,
            duration: time,
            delay: 500,
            onStart: () => {
                if (this.scene) {
                    this.character.play("walking", true, true);
                    if (this.btnStamina) {
                        this.btnStamina.visible = false;
                    }

                    if (x < this.x) {
                        this.turnLeft();
                    } else {
                        this.turnRigth();
                    }
                }

            },
            onStartScope: this,
            onComplete: () => {
                if (this.scene) {
                    this.character.play("idle", true, true);
                    this.setTiredAnimation();
                    if (this.btnStamina) {
                        this.btnStamina.visible = true;
                    }
                    if (finalLeft) {
                        this.turnLeft();
                    } else {
                        this.turnRigth();
                    }
                }
            },
            onCompleteScope: this
        });
    }

    public turnLeft(): void {

        if (!this.staked) {
            return;
        }

        this.character.setScale(-1, 1);
    }

    public turnRigth(): void {

        if (!this.staked) {
            return;
        }

        this.character.setScale(1);
    }

    public piggyClaimAnimation(): void {

        this.character.play("dance_start");
        this.character.clearTrack(1);
    }

    public drinkStartClaimAnimation(rarity: string, index: number): void {

        if (this.btnStamina) {
            this.btnStamina.visible = false;
        }
        this.character.play("walking", true, true);

        let x = -635 - (index * 310);
        let time = Math.abs(this.x - x) * 3;

        this.scene.tweens.add({
            targets: this,
            x: x,
            ease: Phaser.Math.Easing.Linear,
            duration: time,
            onStart: () => {

                if (x < this.x) {
                    this.turnLeft();
                } else {
                    this.turnRigth();
                }
            },
            onStartScope: this,
            onComplete: () => {

                this.character.play("idle", true, true);
                this.setTiredAnimation();
                this.turnLeft();
            },
            onCompleteScope: this
        });
    }

    public drinkClaimAnimation(rarity: string, index: number): void {

        setTimeout(() => {
            if (rarity === GameConstants.RARITY_COMMON) {
                this.character.play("carbz1_claim");
                this.character.clearTrack(1);
            } else if (rarity === GameConstants.RARITY_UNCOMMON) {
                this.character.play("carbz2_claim");
                this.character.clearTrack(1);
            } else if (rarity === GameConstants.RARITY_RARE) {
                this.character.play("carbz3_claim");
                this.character.clearTrack(1);
            }
        }, 2000);
    }

    public treadmillStartClaimAnimation(rarity: string, index: number): void {

        if (this.btnStamina) {
            this.btnStamina.visible = false;
        }
        this.character.play("walking", true, true);

        let x = 700 + (index * 305);
        let time = Math.abs(this.x - x) * 3;

        this.scene.tweens.add({
            targets: this,
            x: x,
            ease: Phaser.Math.Easing.Linear,
            duration: time,
            onStart: () => {

                if (x < this.x) {
                    this.turnLeft();
                } else {
                    this.turnRigth();
                }
            },
            onStartScope: this,
            onComplete: () => {

                this.turnRigth();
                this.y = 160;
                this.parentContainer.sendToBack(this);

                this.character.play("running", true, true);
                this.character.clearTrack(1);
            },
            onCompleteScope: this
        });
    }

    public treadmillClaimAnimation(): void {

        setTimeout(() => {

            this.y = 190;
            this.parentContainer.bringToTop(this);

            this.character.play("idle", true, true);
            this.setTiredAnimation();

            let x = 600;
            let time = Math.abs(this.x - x) * 3;

            this.turnLeft();

            this.scene.tweens.add({
                targets: this,
                x: x,
                ease: Phaser.Math.Easing.Linear,
                duration: time,
                delay: 250,
                onStart: () => {
                    this.character.play("walking", true, true);
                },
                onStartScope: this,
                onComplete: () => {

                    this.turnRigth();
                    if (this.btnStamina) {
                        this.btnStamina.visible = true;
                    }

                    this.character.play("idle", true, true);
                    this.setTiredAnimation();
                    GameManager.onTreadmillClaimAnimationEnd();
                },
                onCompleteScope: this
            });


        }, 2000);
    }

    public walkBack(): void {

        let x = -600;
        let time = Math.abs(this.x - x) * 3;

        this.scene.tweens.add({
            targets: this,
            x: x,
            ease: Phaser.Math.Easing.Linear,
            duration: time,
            delay: 500,
            onStart: () => {
                this.character.play("walking", true, true);
                this.turnRigth();
            },
            onStartScope: this,
            onComplete: () => {

                this.turnLeft();
                if (this.btnStamina) {
                    this.btnStamina.visible = false;
                }

                this.character.play("idle", true, true);
                this.setTiredAnimation();
                GameManager.onDrinkClaimAnimationEnd();
            },
            onCompleteScope: this
        });
    }

    private async onClickStake(btn: Button, asset_id: string): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "scroll", data: null } });
    }

    private onClickMore(): void {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "scroll", data: null } });
    }

    private onClickBuy(): void {

        if (GameVars.onAnimation) {
            return;
        }

        window.open("https://wax.atomichub.io/market?collection_name=clashdomenft&order=asc&schema_name=citizen&sort=price&symbol=WAX", "_blank").focus();
    }

    private async onClickStamina(btn: ButtonInfo): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        btn.alpha = .5;
        btn.setInputEnabled(false);
        btn.showRepairButton();

        let carbz = (btn.maxValue - btn.currentValue) / GameVars.configInfo.carbz_to_stamina;

        let result = await GameManager.repairStamina(carbz);

        if (!result) {
            btn.alpha = 1;
            btn.setInputEnabled(true);
        } else {
            btn.alpha = 1;

            setTimeout(() => {
                GameManager.onStaminaRecovered();
                this.recoverStaminaEffect();
            }, 1000);
        }
    }

    private recoverStaminaEffect(): void {

        let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "restore_fx_01");
        this.scene.add.existing(fx);
        fx.setOrigin(.5, 1);
        fx.setScale(2);
        fx.play("restore_fx");
        fx.setTint(0x8AF722);
        fx.on("animationcomplete", () => {
            this.remove(fx);
        }, this);
        this.add(fx);
    }

    private setTiredAnimation(): void {

        this.character.setAnimation(1, "tired", true);

        let percent = 0;

        if (GameVars.citizensInfo) {
            percent = 1 - (GameVars.playerInfo.stamina / GameVars.citizensInfo.find(x => x.key === GameVars.playerInfo.type).max_stamina);
        }

        this.character.state.tracks[1].alpha = percent;
    }

    private setMixes(): void {

        this.character.setMix("idle", "walking", .25);
        this.character.setMix("walking", "idle", .25);

        this.character.setMix("idle", "running", .25);
        // this.character.setMix("running", "idle", .25);

        this.character.setMix("walking", "running", .25);
        this.character.setMix("running", "walking", .25);

        this.character.setMix("idle", "dance_start", .25);
        this.character.setMix("dance_start", "dance_1", .25);
        this.character.setMix("dance_1", "dance_end", .25);
        this.character.setMix("dance_end", "idle", .25);

    }

    private idsToSpine(ids: any): void {

        let arr = [];

        for (let aux in ids) {
            if (aux) {
                arr.push(aux);
            }
        }

        let serverToSpine = {
            "accessory": "HOLOGRAM", "accessory_ear": "HEADPHONES", "bangs": "FRONT HAIR", "backhair": "BACK HAIR", "head": "FACE", "facialmark": "FACIAL MARK", "eyebrows": "EYEBROWS", "eyes": "EYES", "nose": "NOSE", "mouth": "MOUTH", "beard": "BEARD", "neck": "NECK", "arm": "SLEEVES", "jackets": "JACKET", "torso": "torso", "tops": "TOP", "belts": "BELT", "cape": "CAPE", "bottom": "BOTTOM", "leg": "SOCKS", "shoes": "SHOES", "bottom_back": "bottom_back", "pelvis": "pelvis", "flaps": "flaps", "arm_skin": "arm_skin", "neck_skin": "neck_skin", "leg_skin": "leg_skin"
        };

        this.currentAvatar = {};

        if (ids["arm_skin"].id === "thin") {
            this.currentAvatar["isThin"] = true;
        } else {
            this.currentAvatar["isThin"] = false;
        }

        for (let iter = 0; iter < arr.length; ++iter) {

            if (arr[iter] === null) {
                continue;
            }
            let slotName = arr[iter];
            let idsValue = ids[slotName];
            let tableValue = this.characterData[serverToSpine[arr[iter]]];

            let numIndex;
            if (idsValue.id === null) {
                numIndex = null;
            } else if (arr[iter] === "arm_skin") {
                if (idsValue.id === "thick_1") {
                    numIndex = 0;
                } else if (idsValue.id === "thin") {
                    numIndex = 2;
                } else {
                    numIndex = 1;
                }
            } else if (arr[iter] === "neck_skin") {
                if (idsValue.id === "thick") {
                    numIndex = 1;
                } else {
                    numIndex = 0;
                }
            } else {
                numIndex = parseInt(idsValue.id) - 1;
            }

            if (arr[iter] === "leg_skin" || arr[iter] === "pelvis") {
                numIndex = 0;
            }

            let colors = [];
            if (arr[iter] === "mouth" && (numIndex === 31 || numIndex === 32 || numIndex === 33 || numIndex === 34)) {

                colors = colors.concat(this.characterData["BEARD"].colors);

                colors = colors.concat(this.characterData["BEARD"].vipColors);


            } else {

                colors = colors.concat(tableValue.colors);

                colors = colors.concat(tableValue.vipColors);

            }

            let color = null;
            for (let j = 0; j < colors.length; ++j) {
                if (idsValue.color === colors[j].name) {
                    color = colors[j].hexa;
                    break;
                }
            }
            if (idsValue.color === null || idsValue.color === "" || slotName === "eyebrows" || slotName === "nose" || slotName === "arm_skin" || slotName === "torso" || slotName === "flaps") {
                color = null;
            }
            this.currentAvatar[serverToSpine[arr[iter]]] = { id: numIndex, color: color };
        }

        this.readAvatarData();
    }

    private async readAvatarData(): Promise<void> {

        const arr = [];

        for (let s in this.currentAvatar) {
            if (s) {
                arr.push(s);
            }
        }

        if (this.isThin !== this.currentAvatar["isThin"]) {
            this.switchBody();
        }

        for (let i = 0; i < arr.length; ++i) {

            const slotName = arr[i];

            if (slotName === "isThin") {
                continue;
            }

            const currentData = this.currentAvatar[slotName];
            const currentSlot = this.characterData[slotName];

            if (slotName === "neck_skin" || slotName === "bottom_back" || slotName === "leg_skin") {
                continue;
            }

            let index = this.character.skeleton.findSlotIndex(currentSlot.line);
            let index2 = this.character.skeleton.findSlotIndex(currentSlot.fill);

            let attachments = [];

            for (let att in this.character.skeleton.skin.attachments[index]) {
                if (att) {
                    attachments.push(att);
                }
            }

            if (slotName === "FACIAL MARK") {
                for (let att in this.character.skeleton.skin.attachments[index2]) {
                    if (att) {
                        attachments.push(att);
                    }
                }
            }

            if (slotName === "FACIAL MARK") {

                this.character.skeleton.slots[index2].data.attachmentName = arr[currentData.id];
                this.character.skeleton.slots[index].data.attachmentName = arr[currentData.id];

                if (currentData.id < currentSlot.numAssets) {

                    let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                    s.color.a = 1;
                    s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                    s.color.a = 0;

                    this.setCharacterAttachment(this.characterData[slotName].line, attachments[currentData.id]);

                } else {

                    let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                    s.color.a = 0;

                    s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                    s.color.a = 1;
                    this.setCharacterAttachment(this.characterData[slotName].fill, attachments[currentData.id]);

                }

            } else {

                this.character.skeleton.slots[index].data.attachmentName = attachments[currentData.id];

                this.setCharacterAttachment(this.characterData[slotName].line, attachments[currentData.id]);

                this.changeFill(attachments, currentData.id, this.characterData[slotName]);

                this.changeSecondSlot(attachments, currentData.id, this.characterData[slotName]);
            }


            if (currentSlot.fill || slotName === "HOLOGRAM") {
                this.changeColor(slotName, currentData.color);

                if (this.isThin && slotName === "JACKET") {
                    this.hideSlot(slotName);
                }

                if (this.isThin && slotName === "CAPE" && this.currentAvatar["TOP"].id > 7) {
                    this.hideSlot(slotName);
                }

            }

            this.specificChanges(this.characterData[slotName], attachments[currentData.id], currentData.id, currentData.color);
        }
    }

    private switchBody(): void {

        this.isThin = !this.isThin;

        if (this.isThin) {
            this.hideSlot("JACKET");
            this.hideSlot("CAPE");
        } else {
            this.showSlot("JACKET");
            this.showSlot("CAPE");
        }

        this.switchAttachment("arm_skin", true);
        this.switchAttachment("SLEEVES", true);
        this.switchAttachment("TOP", true);
        this.switchAttachment("torso", true);
        this.switchAttachment("NECK", true);
    }

    private setCharacterAttachment(slot: string, name: string): void {

        this.character.skeleton.setAttachment(slot, name);

        let index = this.character.skeleton.findSlotIndex(slot);

        this.character.skeleton.slots[index].data.attachmentName = name;
    }

    private changeFill(arr: any[], rand: number, currentSlot: any): void {

        if (currentSlot.fill && arr[rand]) {
            let fillSlot = arr[rand] + "_fill";
            this.setCharacterAttachment(currentSlot.fill, fillSlot);
        } else if (currentSlot.fill) {
            this.setCharacterAttachment(currentSlot.fill, arr[rand]);

        }
    }

    private changeSecondSlot(arr: any[], rand: number, currentSlot: any): void {

        if (!arr[rand] && currentSlot.assossiations.name) {
            this.setCharacterAttachment(currentSlot.assossiations.fill, arr[rand]);
            this.setCharacterAttachment(currentSlot.assossiations.name, arr[rand]);

            return;
        }
        if (currentSlot.assossiations.name) {
            let arr_a = [];
            // change sprites
            let index_a = this.character.skeleton.findSlotIndex(currentSlot.assossiations.name);
            for (let s in this.character.skeleton.skin.attachments[index_a]) {
                if (s) {
                    arr_a.push(s);
                }
            }

            this.setCharacterAttachment(currentSlot.assossiations.name, arr_a[rand]);


            if (currentSlot.assossiations.fill) {
                let p = arr[rand].indexOf("/lines/");
                let fillSlot = arr[rand];
                if (p !== -1) {
                    fillSlot = arr[rand].substr(0, p + 1) + "fills" + arr[rand].substr(p + 6, arr[rand].length);
                }

                this.setCharacterAttachment(currentSlot.assossiations.fill, fillSlot + "_fill");
            }
        }
    }

    private changeColor(slot: string, color: string): void {
        if (slot === "HOLOGRAM") {
            this.setSlotColor(this.characterData[slot].line, slot, color);
        } else if (slot === "MOUTH") {
            let index = this.character.skeleton.findSlotIndex(this.characterData[slot].line);

            let s = this.character.skeleton.slots[index];
            let attName: string = s.getAttachment().name;

            // array of assets
            let i = 0;
            for (let s in this.character.skeleton.skin.attachments[index]) {
                if (s === attName) {
                    break;
                } else {
                    ++i;
                }
            }

            this.specificChanges(this.characterData[slot], attName, i, color);
        } else {
            this.setSlotColor(this.characterData[slot].fill, slot, color);
            if (this.characterData[slot].assossiations.fill) {
                this.setSlotColor(this.characterData[slot].assossiations.fill, slot, color);
            }

            let index = this.character.skeleton.findSlotIndex(this.characterData[slot].line);

            let s = this.character.skeleton.slots[index];
            if (s.getAttachment()) {
                this.specificChanges(this.characterData[slot], s.getAttachment().name, null, color);
            }
        }
    }

    private hideSlot(slot: string): void {

        let currentSlot = this.characterData[slot];
        let s;

        if (currentSlot.line) {
            s = this.character.skeleton.findSlot(this.characterData[slot].line);
            s.color.a = 0;
        }

        if (currentSlot.fill) {
            s = this.character.skeleton.findSlot(this.characterData[slot].fill);
            s.color.a = 0;
        }

        if (currentSlot.assossiations.name) {
            s = this.character.skeleton.findSlot(currentSlot.assossiations.name);
            s.color.a = 0;
        }

        if (currentSlot.assossiations.fill) {
            s = this.character.skeleton.findSlot(currentSlot.assossiations.fill);
            s.color.a = 0;
        }
    }

    private showSlot(slot: string, index?: number): void {
        let currentSlot = this.characterData[slot];
        let s;

        if (currentSlot.line === "facialmark") {

            if (index < currentSlot.numAssets) {
                if (currentSlot.line) {
                    s = this.character.skeleton.findSlot(this.characterData[slot].line);
                    s.color.a = 1;
                }
            } else {
                s = this.character.skeleton.findSlot(this.characterData[slot].fill);
                s.color.a = 1;
            }
        } else {
            if (currentSlot.line) {
                s = this.character.skeleton.findSlot(this.characterData[slot].line);
                s.color.a = 1;
            }

            if (currentSlot.fill) {
                s = this.character.skeleton.findSlot(this.characterData[slot].fill);
                s.color.a = 1;
            }

            if (currentSlot.assossiations.name) {
                s = this.character.skeleton.findSlot(currentSlot.assossiations.name);
                s.color.a = 1;
            }

            if (currentSlot.assossiations.fill) {
                s = this.character.skeleton.findSlot(currentSlot.assossiations.fill);
                s.color.a = 1;
            }
        }
    }

    private specificChanges(slot: any, currentAtt?: string, currentId?: number, col?: string) {

        if (slot.specificChanges.length > 0) {

            slot.specificChanges.forEach(c => {

                switch (c.type) {
                    case "forceTogether":

                        let color = JSON.parse(JSON.stringify(this.character.skeleton.findSlot(slot.fill).color));
                        this.forceTogether(c, currentAtt, color);
                        break;

                    case "changeColor":

                        let s = this.character.skeleton.findSlot(c.slot);

                        let ref = this.character.skeleton.findSlot(c.ref);

                        s.color = JSON.parse(JSON.stringify(ref.color));
                        break;

                    case "changeColorCategory":

                        let isInArray = false;

                        for (let i = 0; i < c.ids.length; ++i) {
                            if (c.ids[i] === currentId) {
                                isInArray = true;
                                break;
                            }
                        }

                        if (isInArray) {
                            if (currentAtt !== null) {
                                this.setCharacterAttachment(c.newLine, currentAtt);
                                this.setCharacterAttachment(c.newFill, currentAtt + "_fill");
                            }

                            let index = this.character.skeleton.findSlotIndex(slot.line);
                            this.character.skeleton.slots[index].color.a = 0;

                            index = this.character.skeleton.findSlotIndex(c.newLine);
                            this.character.skeleton.slots[index].color.a = 1;

                            index = this.character.skeleton.findSlotIndex(slot.fill);
                            this.character.skeleton.slots[index].color.a = 0;

                            index = this.character.skeleton.findSlotIndex(c.newFill);
                            if (col) {
                                this.character.skeleton.slots[index].color.setFromString(col);
                            } else {
                                this.character.skeleton.slots[index].color.a = 1;
                            }

                        } else {
                            let index = this.character.skeleton.findSlotIndex(slot.line);
                            this.character.skeleton.slots[index].color.a = 1;

                            index = this.character.skeleton.findSlotIndex(c.newLine);
                            this.character.skeleton.slots[index].color.a = 0;

                            index = this.character.skeleton.findSlotIndex(slot.fill);
                            if (col) {
                                this.character.skeleton.slots[index].color.setFromString(col);
                            } else {
                                this.character.skeleton.slots[index].color.a = 1;
                            }

                            index = this.character.skeleton.findSlotIndex(c.newFill);
                            this.character.skeleton.slots[index].color.a = 0;

                        }
                        break;

                    default:
                        console.log("no exist this type!!");
                        break;

                }
            });
        }
    }

    private forceTogether(c: any, currentAtt: string, color: any): void {
        let isInArray = false;
        let i = 0;
        for (i; i < c.att.length; ++i) {
            if (c.att[i] === currentAtt) {
                isInArray = true;
                break;
            }
        }
        if (c.parnerSlot === "arm_skin_fill") {
            if (!isInArray && !this.isThin) {
                isInArray = true;
                i = 3;
            }
        }
        if (!isInArray && c.inv) {
            let s = this.character.skeleton.findSlot(c.parnerSlot);
            s.color.a = 0;

            s = this.character.skeleton.findSlot(c.parnerSlotFill);

            if (s) {
                s.color.a = 0;
            }
        } else if (!isInArray && !c.inv) {

            if (c.parnerSlot === "torso" && this.isThin) {

                this.setCharacterAttachment(c.parnerSlot, "q_torso/torso_02");

                this.setCharacterAttachment(c.parnerSlotFill, "q_torso/torso_02_fill");
            }

        } else if (isInArray) {
            let s = this.character.skeleton.findSlot(c.parnerSlot);
            s.color.a = 1;

            this.setCharacterAttachment(c.parnerSlot, c.parner[i]);

            if (c.parnerSlotFill) {
                s = this.character.skeleton.findSlot(c.parnerSlotFill);

                this.setCharacterAttachment(c.parnerSlotFill, c.parnerFill[i]);
            }
        }
    }

    private switchAttachment(slotName: string, left: boolean): void {

        let currentSlot = this.characterData[slotName];

        let index = this.character.skeleton.findSlotIndex(currentSlot.line);

        // array of assets
        let arr = [];
        for (let s in this.character.skeleton.skin.attachments[index]) {
            if (s) {
                arr.push(s);
            }
        }

        let index2;

        if (currentSlot.line === "facialmark") {

            index2 = this.character.skeleton.findSlotIndex(currentSlot.fill);

            for (let s in this.character.skeleton.skin.attachments[index2]) {
                if (s) {
                    arr.push(s);
                }
            }
        }

        let assetsIds = [];

        if (this.isThin) {
            assetsIds = currentSlot.basicThinAssets;
            if (this.rarity.toUpperCase() === "UBERNORM" || this.rarity.toUpperCase() === "HI-CLONE") {
                assetsIds = assetsIds.concat(currentSlot.middleThinAssets);
                if (this.rarity.toUpperCase() === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.vipThinAssets);
                }
            }
        } else {
            assetsIds = currentSlot.basicThickAssets;
            if (this.rarity.toUpperCase() === "UBERNORM" || this.rarity.toUpperCase() === "HI-CLONE") {
                assetsIds = assetsIds.concat(currentSlot.middleThickAssets);
                if (this.rarity.toUpperCase() === "HI-CLONE") {
                    assetsIds = assetsIds.concat(currentSlot.vipThickAssets);
                }
            }
        }

        let newIndex = 0;
        if (assetsIds) {
            newIndex = assetsIds[0];
        }

        for (let j = 0; j < assetsIds.length; ++j) {

            if (currentSlot.line === "facialmark") {
                if (j < currentSlot.numAssets) {
                    newIndex = this.searchAttachmentIndex(assetsIds, index, arr, j, left);

                } else {
                    newIndex = this.searchAttachmentIndex(assetsIds, index2, arr, j, left);
                }

            } else {
                newIndex = this.searchAttachmentIndex(assetsIds, index, arr, j, left);
            }
            if (newIndex !== -1) {
                break;
            }
        }

        if (newIndex === -1) {
            newIndex = assetsIds[0];
        }

        if (currentSlot.line === "facialmark") {
            this.character.skeleton.slots[index2].data.attachmentName = arr[newIndex];
            this.character.skeleton.slots[index].data.attachmentName = arr[newIndex];

            if (newIndex < currentSlot.numAssets) {

                let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                s.color.a = 1;
                s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                s.color.a = 0;

                this.setCharacterAttachment(this.characterData[slotName].line, arr[newIndex]);

            } else {

                let s = this.character.skeleton.findSlot(this.characterData[slotName].line);
                s.color.a = 0;

                s = this.character.skeleton.findSlot(this.characterData[slotName].fill);
                s.color.a = 1;
                this.setCharacterAttachment(this.characterData[slotName].fill, arr[newIndex]);

            }

        } else {

            this.character.skeleton.slots[index].data.attachmentName = arr[newIndex];

            this.setCharacterAttachment(this.characterData[slotName].line, arr[newIndex]);


            this.changeFill(arr, newIndex, this.characterData[slotName]);

            this.changeSecondSlot(arr, newIndex, this.characterData[slotName]);
        }

        if (slotName === "TOP" && this.isThin && newIndex > 7) {
            this.hideSlot("CAPE");
        } else if (slotName === "TOP" && this.isThin) {
            this.showSlot("CAPE");
        }

        this.specificChanges(this.characterData[slotName], arr[newIndex], newIndex, null);
    }

    private setSlotColor(slot: string, parent: string, hexa: string): string {
        // 0 < r, g, b > 1
        let s: any;
        s = this.character.skeleton.findSlot(slot);

        let arr = this.characterData[parent].colors;
        arr = arr.concat(this.characterData[parent].vipColors);

        let name: string;
        let i = 0;
        for (i; i < arr.length; ++i) {
            if (hexa === arr[i].hexa) {
                name = arr[i].name;
                break;
            } else if (i === arr.length - 1) {
                return null;
            }
        }
        let isInvisble = false;
        if (s.color.a === 0) {
            isInvisble = true;
        }
        if (slot === "accesory") {
            if (s) {

                s.color.setFromString(hexa);

                let dColor = this.characterData[parent].darkColor[i];

                if (s.darkColor) {
                    s.darkColor.setFromString(dColor.hexa);
                }
            }
        } else {
            if (s) {
                s.color.setFromString(hexa);
            }
        }

        if (isInvisble) {
            s.color.a = 0;
        }

        return name;
    }

    private searchAttachmentIndex(assetsIds: any, index: number, arr: any, j: number, left: boolean): number {

        if (this.character.skeleton.slots[index].data.attachmentName === arr[assetsIds[j]]) {
            if (left) {
                if (j === 0) {
                    j = assetsIds.length;
                }
                return assetsIds[j - 1];
            } else {
                if (j > assetsIds.length - 2) {
                    j = -1;
                }
                return assetsIds[j + 1];
            }
        }
        return -1;
    }
}
