import { GameConstants } from "../../../GameConstants";
import { GameManager } from "../../../GameManager";
import { GameVars } from "../../../GameVars";
import { Button } from "../../../utils/Button";
import { ButtonInfo } from "../ButtonInfo";

export class DrinkMachine extends Phaser.GameObjects.Container {

    public btnClaim: Button;
    public btnBattery: ButtonInfo;
    public btnLoad: ButtonInfo;
    public index: number;

    private interval: NodeJS.Timer;
    private toolData: any;
    private configData: any;
    private tool: Phaser.GameObjects.Container;
    private claim: Phaser.GameObjects.Container;
    private uiLayer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, index: number, uiLayer: Phaser.GameObjects.Container) {

        super(scene);

        this.uiLayer = uiLayer;

        this.toolData = GameVars.stakedDrink[index];
        this.configData = !GameVars.visitMode ? GameVars.toolsConfigInfo.find(x => x.template_id === this.toolData.template_id) : { rarity: GameVars.stakedDrink[index].rarity };

        this.index = index;

        this.x = - 770 - index * 310;

        this.tool = new Phaser.GameObjects.Container(this.scene);
        this.add(this.tool);

        this.drawTool();

        this.claim = new Phaser.GameObjects.Container(this.scene);
        this.uiLayer.add(this.claim);
        this.claim.setPosition(this.x, this.y);

        this.checkClaim();

        if (!GameVars.visitMode) {
            this.btnBattery = new ButtonInfo(this.scene, this.x - 45, this.y - 180, "battery", this.toolData.max_battery, this.toolData.battery, () => { this.onClickBattery(this.btnBattery); }, this);
            this.uiLayer.add(this.btnBattery);

            this.btnLoad = new ButtonInfo(this.scene, this.x + 45, this.y - 180, "load", this.toolData.max_integrity, this.toolData.integrity, () => { this.onClickLoad(this.btnLoad); }, this);
            this.uiLayer.add(this.btnLoad);
        }
        this.once(Phaser.GameObjects.Events.DESTROY, this.onDestroy, this);
    }



    public enterEditMode(): void {

        this.alpha = .3;

        this.btnClaim.setVisible(false);
        this.btnBattery.setVisible(false);
        this.btnLoad.setVisible(false);
    }

    public exitEditMode(): void {

        this.alpha = 1;

        this.btnClaim.setVisible(true);
        this.btnBattery.setVisible(true);
        this.btnLoad.setVisible(true);
    }

    public enableClaim(): void {
        this.btnClaim.input.enabled = true;
        this.btnClaim.alpha = 1;
        GameManager.drinkClaimErrorAnimation();
    }

    public onClaimEnd(): void {

        GameManager.drinkClaimAnimation(this.configData.rarity, this.index);

        this.btnClaim.input.enabled = false;
        this.btnClaim.alpha = .25;
    }

    private onDestroy(): void {
        clearInterval(this.interval);
        this.uiLayer.remove([this.claim, this.btnBattery, this.btnLoad], true);
    }

    private addButtonClaim(): void {
        if (this.btnClaim) {
            this.btnClaim.parentContainer.remove(this.btnClaim);
        }

        this.btnClaim = new Button(this.scene, 0, 225, "texture_atlas_1", "btn_collect_idle", "btn_collect_over");
        this.btnClaim.onDown(() => {
            this.onClickClaim(this.btnClaim);
        }, this);
        this.claim.add(this.btnClaim);

        if (this.toolData.integrity < this.configData.integrity_consumed || this.toolData.battery < this.configData.battery_consumed) {
            this.btnClaim.alpha = .25;
            this.btnClaim.input.enabled = false;
        }
    }

    private addCarbzCans(rarity: string, integrity: number, max_integrity: number): void {

        if (rarity === GameConstants.RARITY_UNCOMMON) {

            let numCans = Math.ceil((integrity / max_integrity) * 8);

            for (let i = 0; i < 2; i++) {
                for (let j = 0; j < 4; j++) {

                    if (j + i * 4 >= numCans) {
                        break;
                    }

                    let can = new Phaser.GameObjects.Image(this.scene, -22 + j * 20, - 35 + (1 - i) * 45, "texture_atlas_1", "carbz_can");
                    this.tool.add(can);
                }
            }

        } else {

            let numCans = Math.ceil((integrity / max_integrity) * 20);

            for (let i = 0; i < 4; i++) {
                for (let j = 0; j < 5; j++) {

                    if (j + i * 5 >= numCans) {
                        break;
                    }

                    let can = new Phaser.GameObjects.Image(this.scene, -27 + j * 20, - 55 + (3 - i) * 45, "texture_atlas_1", "carbz_can");
                    this.tool.add(can);
                }
            }
        }
    }

    private async onClickClaim(btn: Button): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .25;
        btn.input.enabled = false;

        GameManager.showClaimLayer(this.configData, 1000 * this.index, this.index);

        GameManager.drinkStartClaimAnimation(this.configData.rarity, this.index);
    }

    private async onClickBattery(btn: ButtonInfo): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .5;
        btn.setInputEnabled(false);
        btn.showRepairButton();

        let result = await GameManager.repairBattery(GameVars.stakedDrink[this.index].asset_id, (this.btnBattery.maxValue - this.btnBattery.currentValue) / GameVars.configInfo.jigowatts_to_battery);

        if (!result) {
            btn.alpha = 1;
            btn.setInputEnabled(true);
        } else {
            btn.alpha = 1;
            btn.setMaxValue();

            await GameManager.sleep(1000);
            await GameManager.updateBalances();
            await GameManager.getStakedTools();

            this.toolData = GameVars.stakedDrink[this.index];
            this.drawTool();
            this.checkClaim();
            this.recoverBatteryEffect();
        }

        GameVars.onAnimation = false;
    }

    private recoverBatteryEffect(): void {

        let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "restore_fx_01");
        this.scene.add.existing(fx);
        fx.setScale(2);
        fx.play("restore_fx");
        fx.setTint(0x44FFF8);
        fx.on("animationcomplete", () => {
            this.remove(fx);
        }, this);
        this.add(fx);
    }

    private async onClickLoad(btn: ButtonInfo): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .5;
        btn.setInputEnabled(false);
        btn.showRepairButton();

        let result = await GameManager.repairIntegrity(GameVars.stakedDrink[this.index].asset_id, (this.btnLoad.maxValue - this.btnLoad.currentValue) / GameVars.configInfo.credits_to_integrity);

        if (!result) {
            btn.alpha = 1;
            btn.setInputEnabled(true);
        } else {
            btn.alpha = 1;
            btn.setMaxValue();

            await GameManager.sleep(1000);
            await GameManager.updateBalances();
            await GameManager.getStakedTools();

            this.toolData = GameVars.stakedDrink[this.index];
            this.drawTool();
            this.checkClaim();
            this.recoverLoadEffect();
        }

        GameVars.onAnimation = false;
    }

    private recoverLoadEffect(): void {

        let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "restore_fx_01");
        this.scene.add.existing(fx);
        fx.setScale(2);
        fx.play("restore_fx");
        fx.setTint(0xFFF000);
        fx.on("animationcomplete", () => {
            this.remove(fx);
        }, this);
        this.add(fx);
    }

    private checkClaim(): void {
        clearInterval(this.interval);
        this.claim.removeAll(true);

        var now = new Date;
        var current_timestamp = Math.floor(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(),
            now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds(), now.getUTCMilliseconds()) / 1000);

        if (!GameVars.visitMode) {
            if (!GameVars.citizenData) {

                this.btnClaim = new Button(this.scene, 0, 225, "texture_atlas_1", "btn_collect_idle", "btn_collect_over");
                this.claim.add(this.btnClaim);

                this.btnClaim.alpha = .25;
                this.btnClaim.input.enabled = false;


            } else if (this.toolData.last_claim + this.configData.cooldown <= current_timestamp) {
                this.addButtonClaim();
            } else {

                let bck = new Phaser.GameObjects.Image(this.scene, 0, 225, "texture_atlas_1", "btn_collect_idle_empty");
                bck.alpha = .25;
                this.claim.add(bck);

                let time = (this.toolData.last_claim + this.configData.cooldown) - current_timestamp;
                let cooldown = new Phaser.GameObjects.Text(this.scene, 0, 223, new Date(time * 1000).toISOString().substr(11, 8), { fontFamily: "Oswald-Medium", fontSize: "25px", color: "#000033", align: "center" });
                cooldown.setOrigin(.5);
                cooldown.alpha = .5;
                this.claim.add(cooldown);
                this.btnClaim?.destroy();
                this.interval = setInterval(() => {
                    var now = new Date;
                    var current_timestamp = Math.floor(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(),
                        now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds(), now.getUTCMilliseconds()) / 1000);

                    if (this.toolData.last_claim + this.configData.cooldown <= current_timestamp) {
                        clearInterval(this.interval);
                        cooldown.destroy();
                        bck.destroy();
                        this.addButtonClaim();
                    } else {
                        let time = (this.toolData.last_claim + this.configData.cooldown) - current_timestamp;
                        cooldown.text = new Date(time * 1000).toISOString().substr(11, 8);
                    }
                }, 1000);
            }
        }

    }

    private drawTool(): void {

        this.tool.removeAll(true);

        if (this.configData.rarity === GameConstants.RARITY_COMMON) {
            let drink = new Phaser.GameObjects.Image(this.scene, 0, 55, "texture_atlas_1", "carbz_1a");
            this.tool.add(drink);

            if (!GameVars.visitMode) {
                drink.y += (this.toolData.max_integrity - this.toolData.integrity);
            }

            let base = new Phaser.GameObjects.Image(this.scene, 0, 55, "texture_atlas_1", "carbz_1b");
            this.tool.add(base);

            let circuit = new Phaser.GameObjects.Image(this.scene, 0, 55, "texture_atlas_1", "carbz_1c");
            this.tool.add(circuit);
            this.scene.add.tween({
                targets: circuit,
                alpha: 0,
                yoyo: true,
                repeat: -1,
                duration: 1000
            });
        } else if (this.configData.rarity === GameConstants.RARITY_UNCOMMON) {

            let drink = new Phaser.GameObjects.Image(this.scene, 0, 50, "texture_atlas_1", "carbz_2a");
            this.tool.add(drink);

            if (!GameVars.visitMode) {
                this.addCarbzCans(this.configData.rarity, this.toolData.integrity, this.toolData.max_integrity);
            } else {
                this.addCarbzCans(this.toolData.rarity, 1, 1);
            }

            let base = new Phaser.GameObjects.Image(this.scene, 0, 50, "texture_atlas_1", "carbz_2b");
            this.tool.add(base);
        } else if (this.configData.rarity === GameConstants.RARITY_RARE) {
            let drink = new Phaser.GameObjects.Image(this.scene, -30, 15, "texture_atlas_1", "carbz_3a");
            this.tool.add(drink);

            if (!GameVars.visitMode) {
                this.addCarbzCans(this.configData.rarity, this.toolData.integrity, this.toolData.max_integrity);
            } else {
                this.addCarbzCans(this.toolData.rarity, 1, 1);
            }

            let base = new Phaser.GameObjects.Image(this.scene, -30, 15, "texture_atlas_1", "carbz_3b");
            this.tool.add(base);
        }
    }
}
