import { GameConstants } from "../../GameConstants";
import { ExchangeLayer } from "../exchange/ExchangeLayer";
import { GigaSwapManager } from "./GigaSwapManager";

export class SelectedMovesContainer extends Phaser.GameObjects.Container {

    private move1: Phaser.GameObjects.Image;
    private move2: Phaser.GameObjects.Image;
    private move3: Phaser.GameObjects.Image;
    private blinkingTween: Phaser.Tweens.Tween;
    private isPlayer: boolean;
    
    constructor(scene: Phaser.Scene, isPlayer: boolean) {

        super(scene);

        this.y = -150;
        this.isPlayer = isPlayer;

        this.move1 = new Phaser.GameObjects.Image(this.scene, -60, 0, "texture_atlas_1", "ed_void");
        this.add(this.move1);

        this.move2 = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "ed_void");
        this.add(this.move2);

        this.move3 = new Phaser.GameObjects.Image(this.scene, 60, 0, "texture_atlas_1", "ed_void");
        this.add(this.move3);

        if (this.isPlayer) {
            this.blinkingTween = this.scene.tweens.add({
                targets: this.move1,
                alpha: .5,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 250,
                yoyo: true,
                repeat: -1
            });
        }
    }

    public onMoveSelected(move: number): void {

        let moveImg: Phaser.GameObjects.Image;

        if (GigaSwapManager.playerMoves.length === 1) {

            moveImg = this.move1;

            this.blinkingTween.remove();
            this.move1.alpha = 1;

            this.blinkingTween = this.scene.tweens.add({
                targets: this.move2,
                alpha: .5,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 250,
                yoyo: true,
                repeat: -1
            });

        } else if (GigaSwapManager.playerMoves.length === 2) {

            moveImg = this.move2;

            this.blinkingTween.remove();
            this.move2.alpha = 1;

            this.blinkingTween = this.scene.tweens.add({
                targets: this.move3,
                alpha: .5,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 250,
                yoyo: true,
                repeat: -1
            });

        } else {

            moveImg = this.move3;

            this.blinkingTween.remove();
            this.move3.alpha = 1;
        }

        moveImg.setFrame(this.getImageName(move));

        if (GigaSwapManager.playerMoves.length === 3) {

            const gui = ExchangeLayer.currentInstance.gigaSwapContainer.gigaSwapGUI;
            gui.disableButtons();

            GigaSwapManager.onGigaSwapMovesSelected();
        }
    }

    public onMovesTransactionCompleted(): void {

        this.scene.tweens.add({
            targets: this,
            x: -290,
            y: 440,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 850,
            delay: 750
        });
    }

    public onMoveExecuted(): void {

        const round = GigaSwapManager.round;
    
        let moveImg: Phaser.GameObjects.Image;

        switch (round) {

            case 1:
                moveImg = this.move1;
                break;

            case 2:
                moveImg = this.move2;
                break;

            case 3:
                moveImg = this.move3;
                break;
        
            default:
                break;
        }

        const result = GigaSwapManager.roundResults[round - 1];
        
        let resultImage: Phaser.GameObjects.Image;

        if (this.isPlayer) {

            resultImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", result === "player" ? "move_ok" : "move_not_ok");

        } else {

            const move = GigaSwapManager.adversaryMoves[round - 1];
            moveImg.setFrame(this.getImageName(move));

            resultImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", result === "adversary" ? "move_ok" : "move_not_ok");
        }

        resultImage.x = moveImg.x;
        resultImage.visible = false;
        this.add(resultImage);

        this.scene.time.delayedCall(400, function(): void { 
            resultImage.visible = true;
        }, [], this);
    }

    private getImageName(move: number): string {

        let imgName: string;
        
        if (move === GameConstants.ROCK) {
            imgName = "ed_rock";
        } else if (move === GameConstants.PAPER) {
            imgName = "ed_paper";
        } else {
            imgName = "ed_scissors";
        }

        return imgName;
    }
}
