import { GameConstants } from "../../GameConstants";
import { GameVars } from "../../GameVars";
import { Button } from "../../utils/Button";

export class TrialCitizen extends Phaser.GameObjects.Container {

    public character: any;
    public stakeContainer: Phaser.GameObjects.Container;

    private credits: number;
    private moveTween: Phaser.Tweens.Tween;

    constructor(scene: Phaser.Scene, x: number, y: number) {

        super(scene);

        this.setPosition(x, y);

        this.credits = parseInt(GameVars.trialInfo.credits);

        const arrOfDigits = Array.from(String(this.credits), Number);

        while (arrOfDigits.length < 3) {
            arrOfDigits.unshift(0);
        }

        this.character = (this as any).scene.add.spine(0, 0, "trial");
        this.character.skeleton.setToSetupPose();
        this.character.setSkinByName("default");
        this.add(this.character);

        this.character.play(GameVars.trialInfo.full ? "full" : "idle", true, true);

        this.character.skeleton.setAttachment("numbers1", "GUEST/numbers/num_0" + arrOfDigits[0]);
        this.character.skeleton.setAttachment("numbers2", "GUEST/numbers/num_0" + arrOfDigits[1]);
        this.character.skeleton.setAttachment("numbers3", "GUEST/numbers/num_0" + arrOfDigits[2]);

        this.stakeContainer = new Phaser.GameObjects.Container(this.scene, 100, 30);
        this.stakeContainer.setVisible(!GameVars.visitMode);
        this.add(this.stakeContainer);

        const messageBackground = new Phaser.GameObjects.Graphics(this.scene);
        messageBackground.fillStyle(0x000000, .5);
        messageBackground.fillRoundedRect(-340, -18, 540, 36, 10);
        this.stakeContainer.add(messageBackground);

        const messageText = new Phaser.GameObjects.Text(this.scene, -70, 0, "STAKE A CITIZEN TO CLAIM YOUR EARNINGS AND UNLOCK THE FREE TIER", { fontFamily: "Oswald-Light", fontSize: "20px", color: "#FFFFFF" });
        messageText.setOrigin(.5);
        this.stakeContainer.add(messageText);

        const btnStake = new Button(this.scene, -230, -50, "texture_atlas_1", "btn_stake_yellow", "btn_stake_yellow_over");
        btnStake.setOrigin(.5);
        btnStake.setVisible(!GameVars.visitMode);
        messageText.setVisible(!GameVars.visitMode);
        messageBackground.setVisible(!GameVars.visitMode);
        btnStake.onDown(() => {
            this.onClickStake(btnStake);
        }, this);
        this.stakeContainer.add(btnStake);

        const capacityImage = new Phaser.GameObjects.Image(this.scene, -100, -385, "texture_atlas_1", "btn_info_capacity");
        capacityImage.setOrigin(.5);
        this.stakeContainer.add(capacityImage);

        const percent = (this.credits / GameConstants.MAX_LUDIO_DUMMY_CAPACITY);

        const bar = new Phaser.GameObjects.Graphics(this.scene);
        bar.setPosition(-100, -385);
        bar.lineStyle(2, 0x22F722);
        bar.lineBetween(-40, -2, -40 + 80 * percent, -2);
        this.stakeContainer.add(bar);

        const text = new Phaser.GameObjects.Text(this.scene, -100, -397, this.credits + "/" + GameConstants.MAX_LUDIO_DUMMY_CAPACITY, { fontFamily: "Oswald-Light", fontSize: "15px", color: "#000011" });
        text.setOrigin(.5);
        this.stakeContainer.add(text);

        this.setMixes();

        setTimeout(() => {
            if (this.scene) {
                let random = Math.floor(Math.random() * 8 + 1);
                this.character.play("life_" + random);
            }
        }, 5000);

        this.character.state.listeners[0].complete = (trackIndex: any) => {

            if (trackIndex.animation.name !== "full" && trackIndex.animation.name !== "idle" && trackIndex.animation.name !== "walking") {

                this.character.play(GameVars.trialInfo.full ? "full" : "idle", true, true);

                setTimeout(() => {
                    if (this.moveTween && this.moveTween.isPlaying) {
                        return;
                    }
                    if (this.scene) {
                        let random = Math.floor(Math.random() * 8 + 1);
                        this.character.play("life_" + random);
                    }
                }, 4000 + Math.random() * 4000);
            }
        };
    }

    public move(x: number, finalLeft: boolean, type: string): void {

        if (this.moveTween) {
            this.moveTween.stop();
            this.moveTween = null;
        }

        let time = Math.abs(this.x - x) * 3;

        this.stakeContainer.visible = false;

        this.moveTween = this.scene.tweens.add({
            targets: this,
            x: x,
            ease: Phaser.Math.Easing.Linear,
            duration: time,
            delay: 500,
            onStart: () => {

                this.character.play("walking", true, true);

                if (x < this.x) {
                    this.turnLeft();
                } else {
                    this.turnRigth();
                }
            },
            onStartScope: this,
            onComplete: () => {

                if (type === "center") {
                    this.stakeContainer.visible = true;
                }

                this.character.play(GameVars.trialInfo.full ? "full" : "idle", true, true);

                if (finalLeft) {
                    this.turnLeft();
                } else {
                    this.turnRigth();
                }
            },
            onCompleteScope: this
        });
    }

    public turnLeft(): void {

        this.character.setScale(-1, 1);
    }

    public turnRigth(): void {

        this.character.setScale(1);
    }

    private async onClickStake(btn: Button): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "scroll", data: null } });
    }

    private setMixes(): void {

        this.character.setMix("idle", "life_1", .25);
        this.character.setMix("idle", "life_2", .25);
        this.character.setMix("idle", "life_3", .25);
        this.character.setMix("idle", "life_4", .25);
        this.character.setMix("idle", "life_5", .25);
        this.character.setMix("idle", "life_6", .25);
        this.character.setMix("idle", "life_7", .25);
        this.character.setMix("idle", "life_8", .25);

        this.character.setMix("life_1", "idle", .25);
        this.character.setMix("life_2", "idle", .25);
        this.character.setMix("life_3", "idle", .25);
        this.character.setMix("life_4", "idle", .25);
        this.character.setMix("life_5", "idle", .25);
        this.character.setMix("life_6", "idle", .25);
        this.character.setMix("life_7", "idle", .25);
        this.character.setMix("life_8", "idle", .25);

    }
}
