import { GameVars } from "../../GameVars";
import { Button } from "../../utils/Button";

export class ButtonInfo extends Phaser.GameObjects.Container {

    public maxValue: number;
    public currentValue: number;
    
    private buttonType: string;
    private btnContainer: Phaser.GameObjects.Container;
    private btn: Button;
    private text: Phaser.GameObjects.Text;
    private bar: Phaser.GameObjects.Graphics;
    private warningMark: Phaser.GameObjects.Image;
    private repairButton: Phaser.GameObjects.Container;
    private infoText: Phaser.GameObjects.Text;
    private warningTween: Phaser.Tweens.Tween;

    constructor(scene: Phaser.Scene, x: number, y: number, buttonType: string, maxValue: number, currentValue: number, onClickFunction: Function, onClickFunctionContext: any) {

        super(scene);

        this.setPosition(x, y);

        this.buttonType = buttonType;
        this.maxValue = maxValue;
        this.currentValue = currentValue;

        this.btnContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.btnContainer);

        this.btn = new Button(this.scene, 0, 0, "texture_atlas_1", "btn_info_" + this.buttonType, "btn_info_" + this.buttonType + "_over");
        this.btn.onDown(this.showRepairButton, this);
        this.btnContainer.add(this.btn);

        this.text = new Phaser.GameObjects.Text(this.scene, 0, -12, this.currentValue + "/" + this.maxValue, {fontFamily: "Oswald-Light", fontSize: "15px", color: "#000011"});
        this.text.setOrigin(.5);
        this.btnContainer.add(this.text);

        let percent = (this.currentValue / this.maxValue);

        this.bar = new Phaser.GameObjects.Graphics(this.scene);
        this.bar.lineStyle(2, 0x22F722);
        this.bar.lineBetween(-40, -2, -40 + 80 * percent, -2);
        this.btnContainer.add(this.bar);

        this.warningMark = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "red_mark");
        this.warningMark.visible = false;
        this.btnContainer.add(this.warningMark);

        if (percent === 1) {
            this.btn.input.enabled = false;
        }

        this.repairButton = new Phaser.GameObjects.Container(this.scene, 0, 45);
        this.repairButton.visible = false;
        this.add(this.repairButton);

        let bck = new Button(this.scene, 0, 0, "texture_atlas_1", "btn_carbz");
        bck.onDown(onClickFunction, onClickFunctionContext);
        bck.onOver(() => {
            this.repairButton.setScale(1.05);
        }, this);
        bck.onOut(() => {
            this.repairButton.setScale(1);
        }, this);
        this.repairButton.add(bck);

        let repairText = new Phaser.GameObjects.Text(this.scene, 0, -9, "", {fontFamily: "Oswald-Light", fontSize: "15px", color: "#000011", align: "center"});
        repairText.setOrigin(.5);
        this.repairButton.add(repairText);

        this.infoText = new Phaser.GameObjects.Text(this.scene, 0, 9, "", {fontFamily: "Oswald-Light", fontSize: "15px", color: "#000011", align: "center"});
        this.infoText.setOrigin(1, .5);
        this.infoText.setWordWrapWidth(100);
        this.repairButton.add(this.infoText);

        let icon = new Phaser.GameObjects.Image(this.scene, 3, 9, "texture_atlas_1", "icon_dark_carbz");
        icon.setOrigin(0, .5);
        this.repairButton.add(icon);

        if (this.buttonType === "stamina") {
            bck.setFrame("btn_carbz");
            icon.setFrame("icon_dark_carbz");
            repairText.text = "RESTORE";
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.carbz_to_stamina + ""; 
        } else if (this.buttonType === "battery") {
            bck.setFrame("btn_jigos");
            icon.setFrame("icon_dark_jigo");
            repairText.text = "RECHARGE";
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.jigowatts_to_battery + "";
        } else if (this.buttonType === "load") {
            bck.setFrame("btn_credits");
            icon.setFrame("icon_dark_credits");
            repairText.text = "REFILL";
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.credits_to_integrity + "";
        } else if (this.buttonType === "integrity") {
            bck.setFrame("btn_credits");
            icon.setFrame("icon_dark_credits");
            repairText.text = "REPAIR";
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.credits_to_integrity + "";
        }

        this.showWarning();

        if (GameVars.visitMode) {
            this.btn.input.enabled = false;
        }
    }

    public setMaxValue(): void {

        let tween = this.scene.tweens.addCounter({
            from: this.currentValue,
            to: this.maxValue,
            duration: 1000,
            onUpdate: () => {

                this.text.text = Math.round(tween.getValue()) + "/" + this.maxValue;

                let percent = (Math.round(tween.getValue()) / this.maxValue);
                this.bar.clear();
                this.bar.lineStyle(2, 0x22F722);
                this.bar.lineBetween(-40, -2, -40 + 80 * percent, -2);
            },
            onUpdateScope: this
        });

        this.disableWarning();
    }

    public setInputEnabled(value: boolean): void {

        this.btn.input.enabled = value;
    }

    public updateValue(value: number): void {

        this.repairButton.visible = false;

        let tween = this.scene.tweens.addCounter({
            from: this.currentValue,
            to: value,
            duration: 1000,
            onUpdate: () => {

                this.text.text = Math.round(tween.getValue()) + "/" + this.maxValue;

                let percent = (Math.round(tween.getValue()) / this.maxValue);
                this.bar.clear();
                this.bar.lineStyle(2, 0x22F722);
                this.bar.lineBetween(-40, -2, -40 + 80 * percent, -2);
            },
            onUpdateScope: this
        });

        this.currentValue = value;

        if (this.buttonType === "stamina") {
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.carbz_to_stamina + ""; 
        } else if (this.buttonType === "battery") {
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.jigowatts_to_battery + "";
        } else if (this.buttonType === "load") {
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.credits_to_integrity + "";
        } else if (this.buttonType === "integrity") {
            this.infoText.text = (this.maxValue - this.currentValue) / GameVars.configInfo.credits_to_integrity + "";
        }

        if (this.currentValue === this.maxValue) {
            this.setInputEnabled(false);
        } else {
            this.setInputEnabled(true);
        }

        this.showWarning();
    }

    public showRepairButton(): void {
        
        this.repairButton.visible = !this.repairButton.visible;
    }

    private showWarning(): void {

        if (this.currentValue / this.maxValue < .1) {

            if (!this.warningTween) {

                this.warningMark.visible = true;

                this.warningTween = this.scene.tweens.add({
                    targets: this.btnContainer,
                    scaleX: 1.05,
                    scaleY: 1.05,
                    duration: 500,
                    repeat: -1,
                    yoyo: true,
                    ease: Phaser.Math.Easing.Cubic.Out
                    
                });
            }
        } else {
            if (this.warningTween) {
                this.warningTween.stop();
                this.warningTween = null;

                this.warningMark.visible = false;

                this.btnContainer.setScale(1);
            }
        }
    }

    private disableWarning(): void {

        if (this.warningTween) {
            this.warningTween.stop();
            this.warningTween = null;

            this.warningMark.visible = false;

            this.btnContainer.setScale(1);
        }
    }
}