import { GameConstants } from "./../../../GameConstants";
import { ArcadeModding } from "./ArcadeModding";
import { GameVars } from "../../../GameVars";
import { Button } from "../../../utils/Button";
import { GameBox } from "../GameBox/GameBox";

export class Arcade extends Phaser.GameObjects.Container {

    private arcadeShadow: Phaser.GameObjects.Image;
    private arcadeSideBack: Phaser.GameObjects.Image;
    private arcadeTop: Phaser.GameObjects.Image;
    private arcadeMid: Phaser.GameObjects.Image;
    private arcadeLogo: Phaser.GameObjects.Image;
    private arcadeSideFront: Phaser.GameObjects.Image;

    private moddingSideBack: ArcadeModding;
    private moddingMid: ArcadeModding;
    private moddingTop: ArcadeModding;
    private moddingSideFront: ArcadeModding;

    private decorationData: any;

    private btnPlay: Button;
    private uiLayer: Phaser.GameObjects.Container;
    private gameBox: GameBox;
    private gameBoxBackground: Phaser.GameObjects.Graphics;

    constructor(scene: Phaser.Scene, uiLayer: Phaser.GameObjects.Container) {

        super(scene);
        this.uiLayer = uiLayer;

        this.setPosition(220, 0);

        this.decorationData = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? require("../../../../assets/decoration_testnet.json") : require("../../../../assets/decoration.json");

        this.arcadeShadow = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "arcade_shadow");
        this.add(this.arcadeShadow);

        this.arcadeSideBack = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "arcade_side_back");
        this.add(this.arcadeSideBack);

        this.moddingSideBack = new ArcadeModding(this.scene);
        this.add(this.moddingSideBack);

        this.arcadeMid = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "arcade_screen");
        this.add(this.arcadeMid);

        this.moddingMid = new ArcadeModding(this.scene);
        this.add(this.moddingMid);

        this.arcadeLogo = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "arcade_logo");
        this.add(this.arcadeLogo);

        const screenAnimation = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "arcade_logo");
        this.scene.add.existing(screenAnimation);
        screenAnimation.play("arcade_screen");
        screenAnimation.setBlendMode(Phaser.BlendModes.OVERLAY);
        screenAnimation.alpha = 45;
        this.add(screenAnimation);

        this.arcadeTop = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "arcade_top");
        this.add(this.arcadeTop);

        this.moddingTop = new ArcadeModding(this.scene);
        this.add(this.moddingTop);

        this.arcadeSideFront = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "arcade_side_front");
        this.add(this.arcadeSideFront);

        this.moddingSideFront = new ArcadeModding(this.scene);
        this.add(this.moddingSideFront);

        if (!GameVars.visitMode) {
            this.btnPlay = new Button(this.scene, this.x - 25, this.y + 225, "texture_atlas_1", "btn_play_idle", "btn_play_hover");
            this.btnPlay.onDown(() => {
                this.showGamebox();
            }, this);
            this.uiLayer.add(this.btnPlay);
        } else {

            const isFriend = GameVars.socialData && GameVars.socialData.fr && GameVars.socialData.fr[GameVars.userName];

            if (isFriend) {
                this.btnPlay = new Button(this.scene, this.x - 25, this.y + 225, "texture_atlas_1", "btn_challenge_idle", "btn_challenge_hover");
                this.btnPlay.onDown(() => {
                    this.showGamebox();
                }, this);
                this.uiLayer.add(this.btnPlay);
            } else {
                this.btnPlay = new Button(this.scene, this.x - 25, this.y + 225, "texture_atlas_1", "btn_add_friend", "btn_add_friend_hover");
                this.btnPlay.onDown(() => {
                    this.addFriend();
                }, this);
                this.uiLayer.add(this.btnPlay);
            }
        }


        this.gameBoxBackground = new Phaser.GameObjects.Graphics(this.scene, { fillStyle: { color: 0, alpha: 0.3 } });
        this.gameBoxBackground.fillRect(-GameConstants.GAME_WIDTH / 2, -GameConstants.GAME_HEIGHT / 2, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        this.gameBoxBackground.setInteractive({ hitArea: new Phaser.Geom.Rectangle(-GameConstants.GAME_WIDTH / 2, -GameConstants.GAME_HEIGHT / 2, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT), hitAreaCallback: Phaser.Geom.Rectangle.Contains });

        this.once(Phaser.GameObjects.Events.DESTROY, this.destroyed, this);
    }

    public enterEditMode(): void {

        this.alpha = .3;
        this.btnPlay.setVisible(false);
        if (this.gameBox) {
            this.uiLayer.remove(this.gameBoxBackground);
            this.uiLayer.remove(this.gameBox);
            this.gameBox.removeAllListeners();
            this.gameBox = null;
        }
    }

    public exitEditMode(): void {

        this.alpha = 1;
        this.btnPlay.setVisible(true);
    }

    public addMods(): void {

        this.moddingMid.visible = false;
        this.moddingTop.visible = false;
        this.moddingSideBack.visible = false;
        this.moddingSideFront.visible = false;

        this.arcadeMid.visible = true;
        this.arcadeTop.visible = true;
        this.arcadeSideBack.visible = true;
        this.arcadeSideFront.visible = true;

        if (GameVars.stakedDecoration) {
            for (let i = 0; i < GameVars.stakedDecoration.length; i++) {
                if (this.decorationData.arcade[GameVars.stakedDecoration[i].template_id]) {

                    let data = this.decorationData.arcade[GameVars.stakedDecoration[i].template_id];

                    if (data.type === "mid") {
                        this.arcadeMid.visible = false;
                        this.moddingMid.setMod(data, "mid");
                    } else if (data.type === "top") {
                        this.arcadeTop.visible = false;
                        this.moddingTop.setMod(data, "top");
                    } else if (data.type === "side") {
                        this.arcadeSideBack.visible = false;
                        this.arcadeSideFront.visible = false;
                        this.moddingSideBack.setMod(data, "side_back");
                        this.moddingSideFront.setMod(data, "side_front");
                    }
                }
            }
        }
    }

    private addFriend(): void {

        GameVars.api.addFriend({ username: GameVars.reactClass.state.wax.userAccount, adversary: GameVars.userName });
    }

    private showGamebox(): void {

        if (!!this.gameBox) {
            this.gameBox.destroy();
            this.gameBox = null;
        }

        this.uiLayer.add(this.gameBoxBackground);
        this.gameBox = new GameBox(this.scene, 0, 0, 500, 3);
        this.uiLayer.add(this.gameBox);

        this.scene.add.tween({
            targets: this.gameBox,
            alpha: { from: 0, to: 1 },
            scale: { from: 0.9, to: 1 },
            duration: 200
        });

        this.gameBox.once(GameBox.CLICKED_OUTSIDE, () => {
            this.scene.add.tween({
                targets: this.gameBox,
                y: 20,
                alpha: 0,
                scale: 0.8,
                duration: 100,
                onComplete: () => {
                    this.uiLayer.remove(this.gameBoxBackground);
                    this.gameBox.destroy();
                },
                onCompleteScope: this
            });
        }, this);
    }

    private destroyed(): void {
        this.btnPlay.removeAllListeners();
        this.uiLayer.remove(this.btnPlay);

        this.moddingMid.destroy();
        this.moddingSideBack.destroy();
        this.moddingSideFront.destroy();
        this.moddingTop.destroy();
        this.arcadeMid.destroy();
        this.arcadeShadow.destroy();
        this.arcadeSideBack.destroy();
        this.arcadeSideFront.destroy();
        this.arcadeTop.destroy();

    }
}
