import { GameVars } from "../../GameVars";

export class Background extends Phaser.GameObjects.Container {

    private wall: Phaser.GameObjects.Image;
    private floor: Phaser.GameObjects.Image;
    private decorationContainer: Phaser.GameObjects.Container;
    private wallStuffContainer: Phaser.GameObjects.Container;
    private floorStuffContainer: Phaser.GameObjects.Container;
    private decorationData: any;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.decorationData = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? require("../../../assets/decoration_testnet.json") : require("../../../assets/decoration.json");

        this.wall = new Phaser.GameObjects.Image(this.scene, -1536, -250, "texture_atlas_3", this.decorationData.walls[GameVars.backgroundValues.wall].img);
        this.wall.setOrigin(0, 0);
        this.wall.scaleX = 3072 / this.wall.width;
        this.add(this.wall);


        this.floor = new Phaser.GameObjects.Image(this.scene, -1536, 250, "texture_atlas_3", this.decorationData.floors[GameVars.backgroundValues.floor].img);
        this.floor.setOrigin(0, 1);
        this.floor.scaleX = this.wall.scaleX;
        this.add(this.floor);


        this.wallStuffContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.wallStuffContainer);

        this.floorStuffContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.floorStuffContainer);

        // this.addWallStuff(GameVars.backgroundValues.wall);
        // this.addFloorStuff(GameVars.backgroundValues.floor);

        this.decorationContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.decorationContainer);
    }

    public undoBackground(): void {

        this.editColorSelected("wall", GameVars.backgroundValues.wall);
        this.editColorSelected("floor", GameVars.backgroundValues.floor);
    }

    public setBackground(): void {

        this.editColorSelected("wall", GameVars.backgroundValues.wall);
        this.editColorSelected("floor", GameVars.backgroundValues.floor);
    }

    public editColorSelected(type: string, value: number): void {

        if (type === "wall") {
            this.wall.setFrame(this.decorationData.walls[value].img);
            // this.addWallStuff(value);
        } else if (type === "floor") {
            this.floor.setFrame(this.decorationData.floors[value].img);
            // this.addFloorStuff(value);
        }
    }
}
