import { GameConstants } from "../../GameConstants";
import { ExchangeLayer } from "../exchange/ExchangeLayer";
import { CitizenContainer } from "./CitizenContainer";

export class HandContainer extends Phaser.GameObjects.Container {

    private citizenContainer: CitizenContainer;
    private handImg: Phaser.GameObjects.Image;
    private handHalo: Phaser.GameObjects.Image;
    private tokenBagContainer: Phaser.GameObjects.Container;
    private handPatch: Phaser.GameObjects.Image;
    private handWithBagContainer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, citizenContainer: CitizenContainer) {

        super(scene);

        this.citizenContainer = citizenContainer;

        this.handHalo = new Phaser.GameObjects.Image(this.scene, 0, -285, "texture_atlas_2", "hand_halo");
        this.handHalo.visible = false;
        this.add(this.handHalo);

        this.handImg = new Phaser.GameObjects.Image(this.scene, 0, -235, "texture_atlas_2", "hand_idle");
        this.handImg.setOrigin(.25, .75);
        this.handImg.visible = false;
        this.add(this.handImg);

        if (this.citizenContainer.isPlayer) {

            this.handImg.x = -200;
            this.handHalo.x = -305;

            this.tokenBagContainer = new Phaser.GameObjects.Container(this.scene);
            this.tokenBagContainer.visible = false;
            this.add(this.tokenBagContainer);

            const tokenBag = new Phaser.GameObjects.Image(this.scene, -80, -235, "texture_atlas_2", "sack");
            this.tokenBagContainer.add(tokenBag);

            // poner los 3 tokens q se ha jugado
            const jigoIcon = new Phaser.GameObjects.Image(this.scene, -80, -235, "texture_atlas_1", "sack_jigo");
            this.tokenBagContainer.add(jigoIcon);

            const carbzIcon = new Phaser.GameObjects.Image(this.scene, -80, -235, "texture_atlas_1", "sack_carbz");
            this.tokenBagContainer.add(carbzIcon);

            const creditsIcon = new Phaser.GameObjects.Image(this.scene, -80, -235, "texture_atlas_1", "sack_credits");
            this.tokenBagContainer.add(creditsIcon);

            this.handPatch = new Phaser.GameObjects.Image(this.scene, -218, -294, "texture_atlas_2", "hand_patch");
            this.handPatch.setScale(1.03);
            this.handPatch.visible = false;
            this.add(this.handPatch);

        } else {

            this.handImg.x = 200;
            this.handImg.scaleX = -1;

            this.handHalo.x = 305;
            this.handHalo.scaleX = -1;

            this.tokenBagContainer = null;
            this.handPatch = null;
        }
    }

    public onGameEnded(result: string): void {

        this.handHalo.alpha = 0;
        this.handHalo.visible = true;

        this.handImg.x = -140;
        this.handImg.setFrame("hand_rock");
        this.handImg.alpha = 0;
        this.handImg.visible = true;

        this.tokenBagContainer.alpha = 0;
        this.tokenBagContainer.visible = true;

        this.handPatch.alpha = 0;
        this.handPatch.visible = true;

        this.handWithBagContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.handWithBagContainer);

        this.handWithBagContainer.add(this.handImg);
        this.handWithBagContainer.add(this.tokenBagContainer);
        this.handWithBagContainer.add(this.handPatch);

        this.scene.tweens.add({
            targets: [this.handHalo, this.handImg, this.tokenBagContainer, this.handPatch],
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 250,
            onComplete: function(): void {

                if (result === GameConstants.RESULT_PLAYER) {
                    this.animateBag();
                } else if (result === GameConstants.RESULT_ADVERSARY) {
                    this.openHand();
                }
            },
            onCompleteScope: this
        });
    }

    public animateBag(): void {

        this.scene.tweens.add({
            targets: this.tokenBagContainer,
            x: 10,
            y: 25,
            scaleX: 1.1,
            scaleY: 1.1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 600,
            onComplete: function(): void {
                this.scene.tweens.add({
                    targets: this.tokenBagContainer,
                    x: 15,
                    y: 50,
                    scaleX: 1.2,
                    scaleY: 1.2,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 600,
                    onComplete: function(): void {
                        this.scene.tweens.add({
                            targets: this.tokenBagContainer,
                            x: 30,
                            y: 100,
                            scaleX: 1.4,
                            scaleY: 1.4,
                            ease: Phaser.Math.Easing.Cubic.Out,
                            duration: 1200,
                            delay: 200
                        });
                    },
                    onCompleteScope: this,
                    delay: 200
                });

                const fireContainer = ExchangeLayer.currentInstance.gigaSwapContainer.fireContainer;
                fireContainer.onBagGrowing();
            },
            onCompleteScope: this,
            delay: 800
        });
    }

    public openHand(): void {

        this.scene.tweens.add({
            targets: this.handWithBagContainer,
            angle: -6,
            x: 20,
            y: -50,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 800,
            onComplete: function(): void {

                this.handImg.setFrame("hand_paper");
                this.handPatch.destroy();
        
                this.scene.tweens.add({
                    targets: this.tokenBagContainer,
                    y: this.tokenBagContainer.y + GameConstants.GAME_HEIGHT,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 500,
                    onComplete: function(): void {
                        this.tokenBagContainer.destroy();
                    },
                    onCompleteScope: this
                });

                this.scene.time.delayedCall(250, function(): void { 

                    const fireContainer = ExchangeLayer.currentInstance.gigaSwapContainer.fireContainer;
                    fireContainer.onBagContainerFallen();
                
                }, [], this);
            },
            onCompleteScope: this
        });

        // volvemos la mano a la posición normal
        this.scene.time.delayedCall(1500, function(): void { 

            this.handImg.setFrame("hand_idle");

            this.scene.tweens.add({
                targets: this.handWithBagContainer,
                angle: 0,
                x: 0,
                y: 0,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 2250
            });
        
        }, [], this);
    }

    public executeMove(move: number): void {

        this.handHalo.visible = true;
        this.handHalo.alpha = 0;
        
        this.handImg.visible = true;
        this.handImg.alpha = 0;

        this.handImg.scaleX = this.citizenContainer.isPlayer ? 1 : -1;
        this.handImg.scaleY = 1;

        let angle = this.citizenContainer.isPlayer ? - 15 : 15;
        let scaleX = this.citizenContainer.isPlayer ? 1.05 : -1.05;

        this.scene.tweens.add({
            targets: [this.handImg, this.handHalo],
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 250,
            onComplete: function(): void {

                if (!this.scene) {
                    return;
                }

                this.scene.tweens.add({
                    targets: this.handImg,
                    angle: angle,
                    duration: 850,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    onComplete: function(): void {

                        this.handImg.angle = 0;

                        let imgName: string;

                        switch (move) {
                            case GameConstants.ROCK:
                                imgName = "hand_rock";
                                break;
                            case GameConstants.PAPER:
                                imgName = "hand_paper";
                                break;
                            case GameConstants.SCISSORS:
                                imgName = "hand_scissors";
                                break;
                            default:
                        }

                        this.handImg.setFrame(imgName);

                        this.scene.tweens.add({
                            targets: this.handImg,
                            scaleX: scaleX,
                            scaleY: 1.05,
                            ease: Phaser.Math.Easing.Elastic.Out,
                            duration: 250
                        });

                        this.scene.tweens.add({
                            targets: [this.handImg, this.handHalo],
                            alpha: 0,
                            ease: Phaser.Math.Easing.Cubic.Out,
                            duration: 350,
                            delay: 1500,
                            onComplete: function(): void {
                                this.handImg.visible = false;
                                this.handImg.setFrame("hand_idle");
                            },
                            onCompleteScope: this
                        });

                        this.citizenContainer.onMoveExecuted();
                    },
                    onCompleteScope: this,
                    delay: 500
                });
            },
            onCompleteScope: this
        });
    }
}

