import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { ButtonWithText } from "../../utils/ButtonWithText";
import { GigaSwapManager } from "./GigaSwapManager";

export class GigaSwapRetryMenu extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene, message: string) {

        super(scene);

        this.x = 0,
        this.y = 270;

        const background = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "info_box");
        background.setScale(.6);
        this.add(background);

        const okButtonImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_generic");
        okButtonImage.setScale(.425);
        const okButtonLabel = new Phaser.GameObjects.Text(this.scene, 0, 0, "RETRY", {fontFamily: "Futura-Condensed", fontSize: "19px", color: "#44FFF8", align: "center"});
        okButtonLabel.setOrigin(.5);
        okButtonLabel.tint = 0x361601;

        const okButton = new ButtonWithText(this.scene, okButtonImage, okButtonLabel, this.onClickRetry, this);
        okButton.y = 88;
        this.add(okButton);

        const title = new Phaser.GameObjects.Text(this.scene, 0, -80, "ERROR", {fontFamily: "Futura-Condensed", fontSize: "28px", color: "#44FFF8", align: "center"});
        title.setOrigin(.5);
        this.add(title);

        const subtitle = new Phaser.GameObjects.Text(this.scene, 0, -50, message, {fontFamily: "Futura-Condensed", fontSize: "22px", color: "#44FFF8", align: "center"});
        subtitle.setOrigin(.5, 0);
        subtitle.setWordWrapWidth(200);
        this.add(subtitle);

        this.alpha = 0;

        this.scene.tweens.add({
            targets: this,
            y: 240,
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 350,
            delay: 500
        });
    }

    private onClickRetry(): void {
        
        GigaSwapManager.onGigaSwapMovesSelected();
        this.destroy();
    }
}
