import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { Button } from "../../utils/Button";
import { GigaSwapContainer } from "../gigaswap/GigaSwapContainer";
import { InputFieldsContainer } from "./InputFieldsContainer";

export class ExchangeLayer extends Phaser.GameObjects.Container {

    public static currentInstance: ExchangeLayer;

    private static WITHDRAW_MODE = "Withdraw";
    private static DEPOSIT_MODE = "Deposit";
    private static GIGASWAP_MODE = "GigaSwap";

    public gigaSwapContainer: GigaSwapContainer;

    private token1Icon: Phaser.GameObjects.Image;
    private token2Icon: Phaser.GameObjects.Image;
    private token3Icon: Phaser.GameObjects.Image;
    private token1Label: Phaser.GameObjects.Text;
    private token2Label: Phaser.GameObjects.Text;
    private token3Label: Phaser.GameObjects.Text;
    private loadIcon1: Phaser.GameObjects.Image;
    private loadIcon2: Phaser.GameObjects.Image;
    private loadIcon3: Phaser.GameObjects.Image;
    private withdrawBtn: Button;
    private depositBtn: Button;
    private cancelBtn: Button;
    private confirmBtn: Button;
    private transparentBackground: Phaser.GameObjects.Graphics;
    private closeMenuLabel: Phaser.GameObjects.Text;
    private inputFieldsContainer: InputFieldsContainer;
    private currentMode: string;
    private playerTokens: {cdcarbz: number, ludio: number, cdjigo: number};
    
    constructor(scene: Phaser.Scene) {

        super(scene);

        ExchangeLayer.currentInstance = this;

        this.x = GameConstants.GAME_WIDTH / 2;
        this.gigaSwapContainer = null;
        this.currentMode = ExchangeLayer.WITHDRAW_MODE;

        this.transparentBackground = new Phaser.GameObjects.Graphics(this.scene);
        this.transparentBackground.fillStyle(0x01183c);
        this.transparentBackground.alpha = .45;
        this.transparentBackground.fillRect(-GameConstants.GAME_WIDTH / 2, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        this.add(this.transparentBackground);

        const gradient = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "white-gradient");
        gradient.tint = 0x01183c;
        gradient.setOrigin(.5, 0);
        gradient.alpha = .85;
        gradient.setScale(GameConstants.GAME_WIDTH / 50, 2.5);
        this.add(gradient);

        const background = new Phaser.GameObjects.Graphics(this.scene);
        background.fillStyle(0x01183c);
        background.fillRect(-GameConstants.GAME_WIDTH / 2, 0, GameConstants.GAME_WIDTH, 38);
        this.add(background);

        this.setIconsAndLabels();

        if (!GameVars.gameData.clickLabelSeen) {
            this.closeMenuLabel = new Phaser.GameObjects.Text(this.scene, 0, GameConstants.GAME_HEIGHT / 2, "CLICK ANYWHERE TO CLOSE\nTHE EXCHANGE MENU", {fontFamily: "Futura-Condensed", fontSize: "50px", color: "#FFFFFF", align: "center"});
            this.closeMenuLabel.setOrigin(.5);
            this.add(this.closeMenuLabel);
        }

        this.inputFieldsContainer = new InputFieldsContainer(this.scene);
        this.inputFieldsContainer.hide();

        this.visible = false;

        // la animacion de las llamas
        this.scene.anims.create({key: "flames", frames: this.scene.anims.generateFrameNames("texture_atlas_1", { prefix: "fire_", start: 1, end: 12, zeroPad: 2, suffix: ""}), frameRate: 20, repeat: -1});
    }

    public show(): void {

        this.visible = true;

        this.inputFieldsContainer.show();

        this.changeMode(ExchangeLayer.GIGASWAP_MODE);
    }

    public hide(): void {

        this.visible = false;
        
        this.inputFieldsContainer.hide();

        if (this.closeMenuLabel) {
            this.closeMenuLabel.destroy();
        }

        if (this.gigaSwapContainer) {
            this.gigaSwapContainer.destroy();
            this.gigaSwapContainer = null;
        }
    }

    public onEnterDown(): void {

        // DE MOMENTO NADA
    }

    public onWithdrawCompleted(error: boolean): void {

        this.token1Label.text = GameVars.formatNumber(GameVars.playerInfo.carbs);
        this.token2Label.text = GameVars.formatNumber(GameVars.playerInfo.credits);
        this.token3Label.text = GameVars.formatNumber(GameVars.playerInfo.jigowatts);

        this.inputFieldsContainer.resetIntputFields();
        this.inputFieldsContainer.enable();

        this.transparentBackground.alpha = .45;

        this.withdrawBtn.enable();

        if (!error) {
            this.scene.time.delayedCall(1000, function(): void { 
                GameManager.hideExchangeLayer();
            }, [], this);
        }
    }

    public onWithdrawCompletedWithError(): void {

        this.withdrawBtn.enable();
    }

    public showGigaSwapContainer(carbz: number, credits: number, jigo: number): void {

        this.inputFieldsContainer.hide();
        
        this.gigaSwapContainer = new GigaSwapContainer(this.scene, carbz, credits, jigo);
        this.add(this.gigaSwapContainer);
    }

    public hideGigaSwapContainer(): void {
        
        this.inputFieldsContainer.show();

        this.gigaSwapContainer.destroy();
        this.gigaSwapContainer = null;
    }

    public onDepositCompleted(error: boolean): void {

        this.token1Label.text = GameVars.formatNumber(GameVars.playerTokens.cdcarbz);
        this.token2Label.text = GameVars.formatNumber(GameVars.playerTokens.ludio);
        this.token3Label.text = GameVars.formatNumber(GameVars.playerTokens.cdjigo);

        this.inputFieldsContainer.resetIntputFields();
        this.inputFieldsContainer.enable();

        this.transparentBackground.alpha = .45;

        this.depositBtn.enable();

        if (!error) {
            this.scene.time.delayedCall(1000, function(): void { 
                GameManager.hideExchangeLayer();
            }, [], this);
        }
    }

    private setIconsAndLabels(): void {

        this.token1Icon = new Phaser.GameObjects.Image(this.scene, -320, 19, "texture_atlas_1", "icon_carbz");
        this.token1Icon.setInteractive({ cursor: "pointer" });
        this.token1Icon.on("pointerdown", this.onDownToken1, this);
        this.add( this.token1Icon);

        this.token1Label = new Phaser.GameObjects.Text(this.scene, this.token1Icon.x + 22, 19, "0", {fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#8AF722"});
        this.token1Label.setOrigin(0, .5);
        this.token1Label.setInteractive({ cursor: "pointer" });
        this.token1Label.on("pointerdown", this.onDownToken1, this);
        this.add(this.token1Label);

        this.token2Icon = new Phaser.GameObjects.Image(this.scene, this.token1Icon.x + 288, 19, "texture_atlas_1", "icon_credits");
        this.token2Icon.setInteractive({ cursor: "pointer" });
        this.token2Icon.on("pointerdown", this.onDownToken2, this);
        this.add(this.token2Icon);

        this.token2Label = new Phaser.GameObjects.Text(this.scene, this.token2Icon.x + 22, 19, "0", {fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#FFF000"});
        this.token2Label.setOrigin(0, .5);
        this.token2Label.setInteractive({ cursor: "pointer" });
        this.token2Label.on("pointerdown", this.onDownToken2, this);
        this.add(this.token2Label);

        this.token3Icon = new Phaser.GameObjects.Image(this.scene, this.token2Icon.x + 288, 19, "texture_atlas_1", "icon_jigowatts");
        this.token3Icon.setInteractive({ cursor: "pointer" });
        this.token3Icon.on("pointerdown", this.onDownToken3, this);
        this.add( this.token3Icon);

        this.token3Label = new Phaser.GameObjects.Text(this.scene, this.token3Icon.x + 22, 19, "0", {fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#44FFF8"});
        this.token3Label.setOrigin(0, .5);
        this.token3Label.setInteractive({ cursor: "pointer" });
        this.token3Label.on("pointerdown", this.onDownToken3, this);
        this.add(this.token3Label);

        this.withdrawBtn = new Button(this.scene, 460, 20, "texture_atlas_1", "btn_withdraw_off", "btn_withdraw_on");
        this.withdrawBtn.onDown(this.onDownWithdraw, this);
        this.add(this.withdrawBtn);

        this.depositBtn = new Button(this.scene, 460, 20, "texture_atlas_1", "btn_deposit_off", "btn_deposit_on");
        this.depositBtn.onDown(this.onDownDeposit, this);
        this.add(this.depositBtn);

        this.cancelBtn = new Button(this.scene, 460, 20, "texture_atlas_1", "btn_cancel_off", "btn_cancel_on");
        this.cancelBtn.onDown(this.onDownCancel, this);
        this.add(this.cancelBtn);

        this.confirmBtn = new Button(this.scene, 460, 20, "texture_atlas_1", "btn_confirm_off", "btn_confirm_on");
        this.confirmBtn.onDown(this.onDownGigaSwap, this);
        this.add(this.confirmBtn);

        this.loadIcon1 = new Phaser.GameObjects.Image(this.scene, this.token1Icon.x + 50, 19, "texture_atlas_1", "load_icon");
        this.loadIcon1.setScale(.6);
        this.add(this.loadIcon1);

        this.loadIcon2 = new Phaser.GameObjects.Image(this.scene, this.token2Icon.x + 50, 19, "texture_atlas_1", "load_icon");
        this.loadIcon2.setScale(.6);
        this.add(this.loadIcon2);

        this.loadIcon3 = new Phaser.GameObjects.Image(this.scene, this.token3Icon.x + 50, 19, "texture_atlas_1", "load_icon");
        this.loadIcon3.setScale(.6);
        this.add(this.loadIcon3);

        this.scene.tweens.add({
            targets: [this.loadIcon1, this.loadIcon2, this.loadIcon3],
            angle: 360,
            ease: Phaser.Math.Easing.Linear,
            duration: 1500,
            repeat: -1
        });
    }

    private changeMode(mode: string): void {

        this.currentMode = mode;

        this.inputFieldsContainer.resetIntputFields();

        if (this.currentMode === ExchangeLayer.WITHDRAW_MODE || this.currentMode === ExchangeLayer.GIGASWAP_MODE) {

            this.token1Label.visible = true;
            this.token2Label.visible = true;
            this.token3Label.visible = true;

            this.token1Label.text = GameVars.formatNumber(GameVars.playerInfo.carbs);
            this.token2Label.text = GameVars.formatNumber(GameVars.playerInfo.credits);
            this.token3Label.text = GameVars.formatNumber(GameVars.playerInfo.jigowatts);

            this.token1Icon.setFrame("icon_carbz");
            this.token2Icon.setFrame("icon_credits");
            this.token3Icon.setFrame("icon_jigowatts");

            this.loadIcon1.visible = false;
            this.loadIcon2.visible = false;
            this.loadIcon3.visible = false;
 
            this.depositBtn.visible = false;
            this.cancelBtn.visible = false;
           
            if (this.currentMode === ExchangeLayer.WITHDRAW_MODE) {
                this.withdrawBtn.visible = true;
                this.confirmBtn.visible = false;
            } else {
                this.withdrawBtn.visible = false;
                this.confirmBtn.visible = true;
            }

            this.inputFieldsContainer.enable();

        } else {

            this.token1Label.visible = false;
            this.token2Label.visible = false;
            this.token3Label.visible = false;

            this.token1Icon.setFrame("cdcarbz_icon");
            this.token2Icon.setFrame("ludio_icon");
            this.token3Icon.setFrame("cdjigo_icon");

            this.loadIcon1.visible = true;
            this.loadIcon2.visible = true;
            this.loadIcon3.visible = true;

            this.withdrawBtn.visible = false;
            this.depositBtn.visible = true;
            this.cancelBtn.visible = false;
            this.confirmBtn.visible = false;

            this.inputFieldsContainer.disable();

            (async () => {

                this.playerTokens = await GameManager.getPlayerTokens();

                if (this.currentMode === ExchangeLayer.DEPOSIT_MODE) {

                    this.token1Label.visible = true;
                    this.token2Label.visible = true;
                    this.token3Label.visible = true;
    
                    this.token1Label.text = GameVars.formatNumber(this.playerTokens.cdcarbz);
                    this.token2Label.text = GameVars.formatNumber(this.playerTokens.ludio);
                    this.token3Label.text = GameVars.formatNumber(this.playerTokens.cdjigo);
    
                    this.loadIcon1.visible = false;
                    this.loadIcon2.visible = false;
                    this.loadIcon3.visible = false;

                    this.inputFieldsContainer.enable();
                }

            })();
        }
    }

    private onDownToken1(): void {

        let value: number;

        if (this.currentMode === ExchangeLayer.WITHDRAW_MODE || this.currentMode === ExchangeLayer.GIGASWAP_MODE) {
            value = GameVars.playerInfo.carbs;
        } else {
            value = this.playerTokens.cdcarbz;
        }

        this.inputFieldsContainer.setValue(1, value);
    }

    private onDownToken2(): void {
        
        let value: number;

        if (this.currentMode === ExchangeLayer.WITHDRAW_MODE || this.currentMode === ExchangeLayer.GIGASWAP_MODE) {
            value = GameVars.playerInfo.credits;
        } else {
            value = this.playerTokens.ludio;
        }

        this.inputFieldsContainer.setValue(2, value);
    }

    private onDownToken3(): void {
        
        let value: number;

        if (this.currentMode === ExchangeLayer.WITHDRAW_MODE || this.currentMode === ExchangeLayer.GIGASWAP_MODE) {
            value = GameVars.playerInfo.jigowatts;
        } else {
            value = this.playerTokens.cdjigo;
        }

        this.inputFieldsContainer.setValue(3, value);
    }

    private onDownWithdraw(): void {

        const tokens = this.inputFieldsContainer.getValues();

        let carbz = tokens.token1;
        let credits = tokens.token2;
        let jigo = tokens.token3;

        if ((carbz === 0 && credits === 0 && jigo === 0) || carbz < 0 || credits < 0 || jigo < 0 || isNaN(carbz) || isNaN(credits) || isNaN(jigo)) {
            this.showErrorMessage("PLEASE ENTER A VALID TOKEN QUANTITY");
            return;
        } 

        if (carbz > GameVars.playerInfo.carbs) {
            carbz = GameVars.playerInfo.carbs;
            this.inputFieldsContainer.setValue(1, carbz);
        }

        if (credits > GameVars.playerInfo.credits) {
            credits = GameVars.playerInfo.credits;
            this.inputFieldsContainer.setValue(2, credits);
        }

        if (jigo > GameVars.playerInfo.jigowatts) {
            jigo = GameVars.playerInfo.jigowatts;
            this.inputFieldsContainer.setValue(3, jigo);
        }

        this.withdrawBtn.disable();
        this.inputFieldsContainer.disable();

        this.transparentBackground.alpha = .75;

        GameManager.withdrawTokens(carbz, credits, jigo); 
    }

    private onDownDeposit(): void {
        
        const tokens = this.inputFieldsContainer.getValues();

        let cdcarbz = tokens.token1;
        let ludio = tokens.token2;
        let cdjigo = tokens.token3;

        if ((cdcarbz === 0 && ludio === 0 && cdjigo === 0) || cdcarbz < 0 || ludio < 0 || cdjigo < 0 || isNaN(cdcarbz) || isNaN(ludio) || isNaN(cdjigo)) {
            this.showErrorMessage("PLEASE ENTER A VALID TOKEN QUANTITY");
            return;
        } 

        if (cdcarbz > GameVars.playerTokens.cdcarbz) {
            cdcarbz = GameVars.playerTokens.cdcarbz;
            this.inputFieldsContainer.setValue(1, cdcarbz);
        }

        if (ludio > GameVars.playerTokens.ludio) {
            ludio = GameVars.playerTokens.ludio;
            this.inputFieldsContainer.setValue(2, ludio);
        }

        if (cdjigo > GameVars.playerTokens.cdjigo) {
            cdjigo = GameVars.playerTokens.cdjigo;
            this.inputFieldsContainer.setValue(3, cdjigo);
        }

        this.depositBtn.disable();
        this.inputFieldsContainer.disable();

        this.transparentBackground.alpha = .75;

        GameManager.deposiTokens(cdcarbz, ludio, cdjigo); 
    }

    private onDownGigaSwap(): void {

        const tokens = this.inputFieldsContainer.getValues();

        let carbz = tokens.token1;
        let credits = tokens.token2;
        let jigo = tokens.token3;

        if ((carbz === 0 && credits === 0 && jigo === 0) || carbz < 0 || credits < 0 || jigo < 0 || isNaN(carbz) || isNaN(credits) || isNaN(jigo)) {
            this.showErrorMessage("PLEASE ENTER A VALID TOKEN QUANTITY");
            return;
        } 

        if (carbz > 500 || credits > 500 || jigo > 500) {
            this.showErrorMessage("AMOUNTS OVER 500 ARE NOT ALLOWED");
            return;
        } 
        
        GameManager.enterGigaSwap(carbz, credits, jigo);
    }

    private onDownCancel(): void {
        //
    }

    private showErrorMessage(error: string): void {

        const errorLabel = new Phaser.GameObjects.Text(this.scene, GameConstants.GAME_WIDTH / 2 - 10, 65, error, {fontFamily: "Oswald-ExtraLight", fontSize: "24px", color: "#FFFFFF"});
        errorLabel.setOrigin(1, .5);
        this.add(errorLabel);

        this.scene.time.delayedCall(2000, function(): void { 
            errorLabel.destroy();
        }, [], this);
    }
}
