import { ClaimLayer } from "./ClaimLayer";
import { HUD } from "./HUD";
import { BaseContainer } from "./BaseContainer";
import { GameConstants } from "./../../GameConstants";
import { GameVars } from "../../GameVars";
import { GUI } from "./GUI";
import { GameManager } from "../../GameManager";
import { ExchangeLayer } from "../exchange/ExchangeLayer";
import { LoadLayer } from "./LoadLayer";
import { TransitionLayer } from "./TransitionLayer";
import { DraggableDecorationItem } from "./DraggableDecorationItem";

export class MainScene extends Phaser.Scene {

    public static currentInstance: MainScene;

    public baseContainer: BaseContainer;
    public gui: GUI;
    public hud: HUD;
    public blockingLayer: Phaser.GameObjects.Graphics;
    public claimLayer: ClaimLayer;

    private loadLayer: LoadLayer;
    private exchangeLayer: ExchangeLayer;

    constructor() {

        super("MainScene");
    }

    public create(): void {

        MainScene.currentInstance = this;

        GameManager.setCurrentScene(this);

        this.createAnimations();

        this.baseContainer = new BaseContainer(this);
        this.add.existing(this.baseContainer);

        this.hud = new HUD(this);
        this.hud.visible = false;
        this.add.existing(this.hud);

        this.gui = new GUI(this);
        this.gui.visible = false;
        this.add.existing(this.gui);

        this.blockingLayer = new Phaser.GameObjects.Graphics(this);
        this.blockingLayer.visible = false;
        this.blockingLayer.fillStyle(0x000000, .25);
        this.blockingLayer.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        this.blockingLayer.setInteractive(new Phaser.Geom.Rectangle(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT), Phaser.Geom.Rectangle.Contains);
        this.add.existing(this.blockingLayer);

        this.exchangeLayer = new ExchangeLayer(this);
        this.add.existing(this.exchangeLayer);

        this.input.on("pointerdown", this.onDownInScene, this);

        (async () => {

            if (this.loadLayer) {
                this.loadLayer.destroy();
            }
            this.loadLayer = null;

            this.loadLayer = new LoadLayer(this);
            this.add.existing(this.loadLayer);

            GameManager.loadBlockchainValues();
        })();

        let transitionLayer = new TransitionLayer(this, true);
        this.add.existing(transitionLayer);

    }

    public getDecorationItems(): DraggableDecorationItem[] {

        return this.baseContainer.getDecorationItems();
    }

    public closeRoom(): void {

        let transitionLayer = new TransitionLayer(this, false);
        this.add.existing(transitionLayer);
    }

    public reloadToolsZones(): void {

        this.baseContainer.reloadToolsZones();
    }

    public reloadDecoration(): void {

        this.gui.showEditButton();
        this.baseContainer.reloadDecoration();
    }

    public showClaimLayer(data: any, time: number, index?: number): void {

        if (this.claimLayer) {
            this.claimLayer.destroy();
        }

        this.claimLayer = new ClaimLayer(this, data, time, index);
        this.add.existing(this.claimLayer);
    }

    public showStakeCitizen(citizens: any): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
        }

        this.baseContainer.showStakeCitizen(citizens);
    }

    public showBuyCitizen(): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
        }

        this.baseContainer.showBuyCitizen();
    }

    public showTrialCitizen(): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
            this.gui.showButtons();
        }

        this.baseContainer.showTrialCitizen();
    }

    public blockchainValuesLoaded(visit?: boolean): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
        }
        this.baseContainer.blockchainValuesLoaded(visit);
        this.hud.updateBalances();
        this.gui.showButtons();
    }

    public updatePlayerValues(): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
        }

        this.hud.updateBalances();
        this.baseContainer.updatePlayerValues();
    }

    public updateBalances(): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
        }

        this.hud.updateBalances();
    }

    public moveCitizen(type: string): void {

        if (this.loadLayer) {
            this.loadLayer.visible = false;
            this.hud.visible = true;
            this.gui.visible = true;
        }

        this.baseContainer.moveCitizen(type);
    }

    public showLeftZone(): void {

        this.gui.showLeftZone();
    }

    public showRigthZone(): void {

        this.gui.showRightZone();
    }

    public enterEditMode(): void {

        this.gui.enterEditMode();
        this.baseContainer.enterEditMode();
    }

    public exitEditMode(undo: boolean): void {

        this.gui.exitEditMode();
        this.baseContainer.exitEditMode(undo);
    }

    public undoBackground(): void {

        this.baseContainer.undoBackground();
    }

    public editColorSelected(type: string, value: number): void {

        this.baseContainer.editColorSelected(type, value);
    }

    public piggyClaimAnimation(): void {

        this.baseContainer.piggyClaimAnimation();
    }

    public onPiggyClaimAnimationEnd(): void {

        this.baseContainer.onPiggyClaimAnimationEnd();
        this.showClaimLayer(null, 0);
    }

    public enablePiggybankClaim(): void {

        this.baseContainer.enablePiggybankClaim();
    }

    public enableTreadmillClaim(index: number): void {

        this.baseContainer.enableTreadmillClaim(index);
    }

    public enableDrinkMachineClaim(index: number): void {

        this.baseContainer.enableDrinkMachineClaim(index);
    }

    public onClaimTransactionEnd(): void {

        this.baseContainer.onClaimTransactionEnd();
    }

    public onClaimTreadmillTransactionEnd(index: number): void {

        this.baseContainer.onClaimTreadmillTransactionEnd(index);
    }

    public onClaimDrinkMachineTransactionEnd(index: number): void {

        this.baseContainer.onClaimDrinkMachineTransactionEnd(index);
    }

    public drinkClaimAnimation(rarity: string, index: number): void {

        this.baseContainer.drinkClaimAnimation(rarity, index);
    }

    public drinkClaimErrorAnimation(): void {

        this.baseContainer.drinkClaimErrorAnimation();
    }

    public drinkStartClaimAnimation(rarity: string, index: number): void {

        this.baseContainer.drinkStartClaimAnimation(rarity, index);
    }

    public onDrinkClaimAnimationEnd(): void {

        this.baseContainer.onDrinkClaimAnimationEnd();
    }

    public treadmillClaimAnimation(rarity: string, index: number): void {

        this.baseContainer.treadmillClaimAnimation(rarity, index);
    }

    public treadmillStartClaimAnimation(rarity: string, index: number): void {

        this.baseContainer.treadmillStartClaimAnimation(rarity, index);
    }

    public onTreadmillClaimAnimationEnd(): void {

        this.baseContainer.onTreadmillClaimAnimationEnd();
    }

    public updateTools(): void {

        this.baseContainer.updateTools();
    }

    public showBlockingLayer(): void {

        this.blockingLayer.visible = true;
    }

    public hideBlockingLayer(): void {

        this.blockingLayer.visible = false;
    }

    public showExchangeLayer(): void {

        this.exchangeLayer.show();
        this.gui.setVisible(false);
    }

    public hideExchangeLayer(): void {

        this.exchangeLayer.hide();
        this.gui.setVisible(true);
    }

    public changeLoadText(text: string): void {

        if (this.loadLayer) {
            this.loadLayer.changeText(text);
        }
    }

    private onDownInScene(pointer: Phaser.Input.Pointer): void {

        if (this.exchangeLayer && pointer.y > 60 && !GameVars.paused && ExchangeLayer.currentInstance.gigaSwapContainer === null) {
            GameManager.hideExchangeLayer();
        }
    }

    private createAnimations(): void {

        this.anims.create({ key: "pig_anim", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "piggy_", start: 1, end: 8, zeroPad: 2 }), frameRate: 30, repeat: -1 });

        this.anims.create({ key: "lights_anim", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "arcade_lights_", start: 1, end: 2, zeroPad: 1 }), frameRate: 2, repeat: -1 });
        this.anims.create({ key: "arcade_screen", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "arcade_screen_", start: 1, end: 9, zeroPad: 2 }), frameRate: 10, repeat: -1 });

        this.anims.create({ key: "treadmill_a", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "treadmill_a_battery_lights_", start: 1, end: 5, zeroPad: 0 }), frameRate: 3, repeat: -1 });
        this.anims.create({ key: "treadmill_b", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "treadmill_b_light_", start: 1, end: 4, zeroPad: 0 }), frameRate: 10, repeat: -1 });
        this.anims.create({ key: "treadmill_b_2", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "treadmill_b_light_", start: 1, end: 4, zeroPad: 0 }), frameRate: 4, repeat: -1 });
        this.anims.create({ key: "treadmill_c", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "treadmill_c_fx_", start: 1, end: 10, zeroPad: 0 }), frameRate: 30, repeat: -1 });
        this.anims.create({ key: "treadmill_c_2", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "treadmill_c_fx_", start: 1, end: 10, zeroPad: 0 }), frameRate: 15, repeat: -1 });

        this.anims.create({ key: "restore_fx", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "restore_fx_", start: 1, end: 15, zeroPad: 0 }), frameRate: 30, repeat: 7 });

        this.anims.create({ key: "spark", frames: this.anims.generateFrameNames("texture_atlas_1", { prefix: "spark_", start: 1, end: 9, zeroPad: 0 }), frameRate: 30 });
    }
}
