import { GameConstants } from "../../../GameConstants";
import { GameManager } from "../../../GameManager";
import { GameVars } from "../../../GameVars";
import { Button } from "../../../utils/Button";
import { ButtonInfo } from "../ButtonInfo";

export class Treadmill extends Phaser.GameObjects.Container {

    public btnClaim: Button;
    public btnIntegrity: ButtonInfo;

    public index: number;

    private interval: NodeJS.Timer;

    private toolData: any;
    private configData: any;

    private tool: Phaser.GameObjects.Container;
    private fxContainer: Phaser.GameObjects.Container;
    private fxContainer2: Phaser.GameObjects.Container;
    private claim: Phaser.GameObjects.Container;

    private sparks: Phaser.GameObjects.Sprite[];

    private uiLayer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, index: number, uiLayer: Phaser.GameObjects.Container) {

        super(scene);
        this.uiLayer = uiLayer;

        this.toolData = GameVars.stakedTreadmill[index];
        this.configData = !GameVars.visitMode ? GameVars.toolsConfigInfo.find(x => x.template_id === this.toolData.template_id) : { rarity: GameVars.stakedTreadmill[index].rarity };

        this.index = index;

        this.x = 755 + index * 300;

        this.tool = new Phaser.GameObjects.Container(this.scene);
        this.add(this.tool);

        this.drawTool();

        this.claim = new Phaser.GameObjects.Container(this.scene);
        this.uiLayer.add(this.claim);
        this.claim.setPosition(this.x, this.y);

        this.checkClaim();

        if (!GameVars.visitMode) {
            this.btnIntegrity = new ButtonInfo(this.scene, this.x + 0, this.y - 180, "integrity", this.toolData.max_integrity, this.toolData.integrity, () => { this.onClickIntegrity(this.btnIntegrity); }, this);
            this.uiLayer.add(this.btnIntegrity);
        }

        this.once(Phaser.GameObjects.Events.DESTROY, this.onDestroy, this);
    }



    public enterEditMode(): void {

        this.alpha = .3;
        this.btnClaim.setVisible(false);
        this.btnIntegrity.setVisible(false);
    }

    public exitEditMode(): void {

        this.alpha = 1;
        this.btnClaim.setVisible(true);
        this.btnIntegrity.setVisible(true);
    }

    public onClaimEnd(): void {

        GameManager.treadmillClaimAnimation(this.configData.rarity, this.index);

        this.btnClaim.input.enabled = false;
        this.btnClaim.alpha = .25;
    }

    public enableClaim(): void {

        this.btnClaim.input.enabled = false;
        this.btnClaim.alpha = .25;
    }

    private addButtonClaim(): void {

        this.btnClaim = new Button(this.scene, 0, 225, "texture_atlas_1", "btn_charge_idle", "btn_charge_over");
        this.btnClaim.onDown(() => {
            this.onClickClaim(this.btnClaim);
        }, this);
        this.claim.add(this.btnClaim);

        if (this.toolData.integrity < this.configData.integrity_consumed || GameVars.playerInfo.stamina < this.configData.stamina_consumed) {
            this.btnClaim.alpha = .25;
            this.btnClaim.input.enabled = false;
        }
    }

    private onDestroy(): void {
        clearInterval(this.interval);
        this.uiLayer.remove([this.claim, this.btnIntegrity]);
    }

    private async onClickClaim(btn: Button): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .25;
        btn.input.enabled = false;
        this.fxContainer.visible = true;
        this.fxContainer2.visible = false;

        GameManager.treadmillStartClaimAnimation(this.configData.rarity, this.index);

        GameManager.showClaimLayer(this.configData, 1000 * this.index, this.index);
    }

    private async onClickIntegrity(btn: ButtonInfo): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.onAnimation = true;

        btn.alpha = .5;
        btn.setInputEnabled(false);
        btn.showRepairButton();

        let result = await GameManager.repairIntegrity(GameVars.stakedTreadmill[this.index].asset_id, (this.btnIntegrity.maxValue - this.btnIntegrity.currentValue) / GameVars.configInfo.credits_to_integrity);

        if (!result) {
            btn.alpha = 1;
            btn.setInputEnabled(true);
        } else {
            btn.alpha = 1;
            btn.setMaxValue();

            await GameManager.sleep(1000);
            await GameManager.updateBalances();
            await GameManager.getStakedTools();

            this.toolData = GameVars.stakedTreadmill[this.index];
            this.drawTool();
            this.checkClaim();
            this.recoverIntegrityEffect();
        }

        GameVars.onAnimation = false;
    }

    private recoverIntegrityEffect(): void {

        let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "restore_fx_01");
        this.scene.add.existing(fx);
        fx.setScale(2);
        fx.play("restore_fx");
        fx.setTint(0xFFF000);
        fx.on("animationcomplete", () => {
            this.remove(fx);
        }, this);
        this.add(fx);
    }

    private checkClaim(): void {

        clearInterval(this.interval);
        this.claim.removeAll(true);

        var now = new Date;
        var current_timestamp = Math.floor(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(),
            now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds(), now.getUTCMilliseconds()) / 1000);

        if (!GameVars.visitMode) {
            if (!GameVars.citizenData) {

                this.btnClaim = new Button(this.scene, 0, 225, "texture_atlas_1", "btn_charge_idle", "btn_charge_over");
                this.claim.add(this.btnClaim);

                this.btnClaim.alpha = .25;
                this.btnClaim.input.enabled = false;

            } else if (this.toolData.last_claim + this.configData.cooldown <= current_timestamp) {
                this.addButtonClaim();
            } else {
                let bck = new Phaser.GameObjects.Image(this.scene, 0, 225, "texture_atlas_1", "btn_charge_idle_empty");
                bck.alpha = .25;
                this.claim.add(bck);

                let time = (this.toolData.last_claim + this.configData.cooldown) - current_timestamp;
                let cooldown = new Phaser.GameObjects.Text(this.scene, 0, 223, new Date(time * 1000).toISOString().substr(11, 8), { fontFamily: "Oswald-Medium", fontSize: "25px", color: "#000033", align: "center" });
                cooldown.alpha = .5;
                cooldown.setOrigin(.5);
                this.claim.add(cooldown);
                this.btnClaim?.destroy();
                this.interval = setInterval(() => {

                    var now = new Date;
                    var current_timestamp = Math.floor(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate(),
                        now.getUTCHours(), now.getUTCMinutes(), now.getUTCSeconds(), now.getUTCMilliseconds()) / 1000);

                    if (this.toolData.last_claim + this.configData.cooldown <= current_timestamp) {
                        clearInterval(this.interval);
                        cooldown.destroy();
                        bck.destroy();

                        this.addButtonClaim();
                    } else {
                        let time = (this.toolData.last_claim + this.configData.cooldown) - current_timestamp;
                        cooldown.text = new Date(time * 1000).toISOString().substr(11, 8);
                    }
                }, 1000);
            }
        }
    }

    private drawTool(): void {

        this.tool.removeAll(true);

        let state = 1;

        if (!GameVars.visitMode) {
            state = 5 - Math.ceil((this.toolData.integrity / this.toolData.max_integrity) * 4);
        }

        if (state === 5) {
            state = 4;
        }

        if (this.configData.rarity === GameConstants.RARITY_COMMON) {

            let base = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_a_base");
            this.tool.add(base);

            let extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_a" + state);
            this.tool.add(extra);

            this.fxContainer = new Phaser.GameObjects.Container(this.scene, 0, 20);
            this.tool.add(this.fxContainer);

            let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "treadmill_a_battery_lights_0");
            this.scene.add.existing(fx);
            fx.play("treadmill_a");
            this.fxContainer.add(fx);

            this.fxContainer2 = new Phaser.GameObjects.Container(this.scene, 0, 20);
            this.tool.add(this.fxContainer2);

        } else if (this.configData.rarity === GameConstants.RARITY_UNCOMMON) {

            let base = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_b_base");
            this.tool.add(base);

            let extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_b" + state);
            this.tool.add(extra);

            extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_b_light_1");
            this.tool.add(extra);

            this.fxContainer = new Phaser.GameObjects.Container(this.scene, 0, 20);
            this.tool.add(this.fxContainer);

            let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "treadmill_b_light_2");
            this.scene.add.existing(fx);
            fx.play("treadmill_b");
            this.fxContainer.add(fx);

            fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "treadmill_b_lights_bottom");
            this.fxContainer.add(fx);

            fx = new Phaser.GameObjects.Sprite(this.scene, 97, -127, "texture_atlas_1", "treadmill_b_battery_fx");
            this.fxContainer.add(fx);

            this.scene.tweens.add({
                targets: fx,
                angle: 360,
                ease: Phaser.Math.Easing.Linear,
                duration: 500,
                repeat: -1
            });

            this.fxContainer2 = new Phaser.GameObjects.Container(this.scene, 0, 20);
            this.tool.add(this.fxContainer2);

            fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "treadmill_b_light_2");
            this.scene.add.existing(fx);
            fx.play("treadmill_b_2");
            this.fxContainer2.add(fx);

        } else if (this.configData.rarity === GameConstants.RARITY_RARE) {

            let base = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_c_base");
            this.tool.add(base);

            let extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_c" + state);
            this.tool.add(extra);

            if (state === 1) {
                extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_c_screen_on");
                this.tool.add(extra);
            } else if (state === 2) {
                extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_c_screen_on");
                this.tool.add(extra);

                extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_c_screen_broken");
                this.tool.add(extra);
            } else {
                extra = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "treadmill_c_screen_off");
                this.tool.add(extra);
            }

            this.fxContainer = new Phaser.GameObjects.Container(this.scene, 0, 20);
            this.tool.add(this.fxContainer);

            let fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "treadmill_b_light_1");
            this.scene.add.existing(fx);
            fx.play("treadmill_c");
            this.fxContainer.add(fx);

            fx = new Phaser.GameObjects.Sprite(this.scene, 103, 55, "texture_atlas_1", "treadmill_b_battery_fx");
            this.fxContainer.add(fx);

            this.scene.tweens.add({
                targets: fx,
                angle: 360,
                ease: Phaser.Math.Easing.Linear,
                duration: 500,
                repeat: -1
            });

            this.fxContainer2 = new Phaser.GameObjects.Container(this.scene, 0, 20);
            this.tool.add(this.fxContainer2);

            fx = new Phaser.GameObjects.Sprite(this.scene, 0, 0, "texture_atlas_1", "treadmill_b_light_1");
            this.scene.add.existing(fx);
            fx.play("treadmill_c_2");
            this.fxContainer2.add(fx);
        }

        this.sparks = [];

        let positions = [
            { x: -70, y: -20 },
            { x: 80, y: -25 },
            { x: 0, y: 150 }
        ];

        for (let i = 0; i < 3; i++) {
            let spark = new Phaser.GameObjects.Sprite(this.scene, positions[i].x, positions[i].y, "texture_atlas_1", "spark_1");
            this.scene.add.existing(spark);
            spark.play("spark");
            spark.on("animationcomplete", () => {
                spark.alpha = 0;
                setTimeout(() => {
                    if ((spark as any).scene) {
                        spark.alpha = 1;
                        spark.play("spark");
                    }
                }, Math.round(Math.random() * 2500) + 500);
            }, this);
            this.tool.add(spark);
            this.sparks.push(spark);

            if (state - 1 <= i) {
                spark.visible = false;
            }
        }
        if (this.fxContainer) {
            this.fxContainer.visible = false;
        }
    }
}
