import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";
import { ButtonWithText } from "../../utils/ButtonWithText";
import { GigaSwapManager } from "./GigaSwapManager";

export class GigaSwapGameOverMenu extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene, result: string) {

        super(scene);

        this.x = 150,
        this.y = 270;

        const background = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "info_box");
        background.setScale(.6);
        this.add(background);

        let titleImgName: string;
        let subtitleStr: string;
        let tokenFontColor: string;
        let tokenPrefix: string;
        let multiplier: number;

        switch (result) {

            case GameConstants.RESULT_PLAYER:

                titleImgName = "gigaswap_victory";
                subtitleStr = "YOUR EARNINGS";

                tokenPrefix = "+";
                tokenFontColor = "#8AF722";

                multiplier = 2;

                break;

            case GameConstants.RESULT_ADVERSARY:

                titleImgName = "gigaswap_defeat";
                subtitleStr = "TOKENS BURNT";

                tokenPrefix = "-";
                tokenFontColor = "#ff0430";

                multiplier = 1;

                break;

            case GameConstants.RESULT_TIE:

                titleImgName = "gigaswap_tie";
                subtitleStr = "TOKENS AT STAKE";

                tokenPrefix = "";
                tokenFontColor = "#8AF722";

                const replayButtonImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_generic");
                replayButtonImage.setScale(.425);
                const replayButtonLabel = new Phaser.GameObjects.Text(this.scene, 0, 0, "REPLAY", {fontFamily: "Futura-Condensed", fontSize: "19px", color: "#44FFF8", align: "center"});
                replayButtonLabel.setOrigin(.5);
                replayButtonLabel.tint = 0x361601;
        
                const replayButton = new ButtonWithText(this.scene, replayButtonImage, replayButtonLabel, this.onClickReplay, this);
                replayButton.x = -50;
                replayButton.y = 88;
                this.add(replayButton);

                const withdrawButtonImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_generic");
                withdrawButtonImage.setScale(.425);
                const withdrawButtonLabel = new Phaser.GameObjects.Text(this.scene, 0, 0, "WITHDRAW", {fontFamily: "Futura-Condensed", fontSize: "19px", color: "#44FFF8", align: "center"});
                withdrawButtonLabel.setOrigin(.5);
                withdrawButtonLabel.tint = 0x361601;
        
                const withdrawButton = new ButtonWithText(this.scene, withdrawButtonImage, withdrawButtonLabel, this.onClickWithdraw, this);
                withdrawButton.x = 50;
                withdrawButton.y = 88;
                this.add(withdrawButton);

                multiplier = 1;

                break;
        
            default:

                break;
        }

        if (result === GameConstants.RESULT_PLAYER || result === GameConstants.RESULT_ADVERSARY) {

            const okButtonImage = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "btn_generic");
            okButtonImage.setScale(.425);
            const okButtonLabel = new Phaser.GameObjects.Text(this.scene, 0, 0, "OK", {fontFamily: "Futura-Condensed", fontSize: "19px", color: "#44FFF8", align: "center"});
            okButtonLabel.setOrigin(.5);
            okButtonLabel.tint = 0x361601;
    
            const okButton = new ButtonWithText(this.scene, okButtonImage, okButtonLabel, this.onClickOK, this);
            okButton.y = 88;
            this.add(okButton);
        }

        const title = new Phaser.GameObjects.Image(this.scene, 0, -72, "texture_atlas_1", titleImgName);
        this.add(title);

        const subtitle = new Phaser.GameObjects.Text(this.scene, 0, -34, subtitleStr, {fontFamily: "Futura-Condensed", fontSize: "18px", color: "#44FFF8", align: "center"});
        subtitle.setOrigin(.5);
        this.add(subtitle);

        // LOS ICONOS DE LOS TOKENS
        const cdcarbzIcon = new Phaser.GameObjects.Image(this.scene, 30, -5, "texture_atlas_1", "cdcarbz_icon");
        cdcarbzIcon.setScale(.7);
        this.add(cdcarbzIcon);

        const ludioIcon = new Phaser.GameObjects.Image(this.scene, 30, 20, "texture_atlas_1", "ludio_icon");
        ludioIcon.setScale(.7);
        this.add(ludioIcon);

        const cdjigoIcon = new Phaser.GameObjects.Image(this.scene, 30, 45, "texture_atlas_1", "cdjigo_icon");
        cdjigoIcon.setScale(.7);
        this.add(cdjigoIcon);

        let cdcarbzStr: string;

        if (GigaSwapManager.carbz !== 0) {
            cdcarbzStr = tokenPrefix + GameVars.formatNumber(multiplier * GigaSwapManager.carbz);
        } else {
            cdcarbzStr = "0";
        }

        const cdcarbzLabel = new Phaser.GameObjects.Text(this.scene, cdcarbzIcon.x - 15, cdcarbzIcon.y, cdcarbzStr, {fontFamily: "Futura-Condensed", fontSize: "15px", color: tokenFontColor});
        cdcarbzLabel.setOrigin(1, .5);
        this.add(cdcarbzLabel);

        let ludioStr: string = tokenPrefix + GameVars.formatNumber(GigaSwapManager.credits);

        if (GigaSwapManager.credits !== 0) {
            ludioStr = tokenPrefix + GameVars.formatNumber(multiplier * GigaSwapManager.credits);
        } else {
            ludioStr = "0";
        }

        const ludioLabel = new Phaser.GameObjects.Text(this.scene, ludioIcon.x - 15, ludioIcon.y, ludioStr, {fontFamily: "Futura-Condensed", fontSize: "15px", color: tokenFontColor});
        ludioLabel.setOrigin(1, .5);
        this.add(ludioLabel);

        let cdjigoStr: string;

        if (GigaSwapManager.jigo !== 0) {
            cdjigoStr = tokenPrefix + GameVars.formatNumber(multiplier * GigaSwapManager.jigo);
        } else {
            cdjigoStr = "0";
        }

        const cdjigoLabel = new Phaser.GameObjects.Text(this.scene, cdjigoIcon.x - 15, cdjigoIcon.y, cdjigoStr, {fontFamily: "Futura-Condensed", fontSize: "15px", color: tokenFontColor});
        cdjigoLabel.setOrigin(1, .5);
        this.add(cdjigoLabel);

        // EN EL CASO DE EMPATE HAY QUE PONER OTROS 3 ICONOS
        if (result === GameConstants.RESULT_TIE) {
            
            let x = cdcarbzLabel.x - cdcarbzLabel.width - 20;

            if (ludioLabel.x - ludioLabel.width - 20 < x) {
                x = ludioLabel.x - ludioLabel.width - 20;
            }  

            if (cdjigoLabel.x - cdjigoLabel.width - 20 < x) {
                x = cdjigoLabel.x - cdjigoLabel.width - 20;
            } 

            const carbzIcon = new Phaser.GameObjects.Image(this.scene, x, cdcarbzIcon.y, "texture_atlas_1", "icon_carbz");
            carbzIcon.setScale(.6);
            this.add(carbzIcon);

            let arrowLabel = new Phaser.GameObjects.Text(this.scene, x + 16, cdcarbzIcon.y, ">", {fontFamily: "Oswald-ExtraLight", fontSize: "20px", color: "#44FFF8"});
            arrowLabel.setOrigin(.5);
            this.add(arrowLabel);

            const creditsIcon = new Phaser.GameObjects.Image(this.scene, x, ludioIcon.y, "texture_atlas_1", "icon_credits");
            creditsIcon.setScale(.6);
            this.add(creditsIcon);

            arrowLabel = new Phaser.GameObjects.Text(this.scene, x + 16, creditsIcon.y, ">", {fontFamily: "Oswald-ExtraLight", fontSize: "20px", color: "#44FFF8"});
            arrowLabel.setOrigin(.5);
            this.add(arrowLabel);

            const jigoIcon = new Phaser.GameObjects.Image(this.scene, x, cdjigoIcon.y, "texture_atlas_1", "icon_jigowatts");
            jigoIcon.setScale(.6);
            this.add(jigoIcon);

            arrowLabel = new Phaser.GameObjects.Text(this.scene, x + 16, cdjigoIcon.y, ">", {fontFamily: "Oswald-ExtraLight", fontSize: "20px", color: "#44FFF8"});
            arrowLabel.setOrigin(.5);
            this.add(arrowLabel);
        }

        this.alpha = 0;

        this.scene.tweens.add({
            targets: this,
            y: 240,
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 350,
            delay: 3000
        });
    }

    private onClickOK(): void {
        
        GameManager.hideExchangeLayer();
        GameManager.updateBalances();
    }

    private onClickReplay(): void {
        
        GigaSwapManager.replay();
    }

    private onClickWithdraw(): void {
        
        GameManager.withdrawTokensTieGigaSwap();
    }
}
