import { GameConstants } from "../../GameConstants";
import { ExchangeLayer } from "../exchange/ExchangeLayer";

export class CurtainsContainer extends Phaser.GameObjects.Container {

    private upperCurtain: Phaser.GameObjects.Graphics;
    private lowerCurtain: Phaser.GameObjects.Graphics;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.x = -GameConstants.GAME_WIDTH / 2;

        this.upperCurtain = new Phaser.GameObjects.Graphics(this.scene);
        this.upperCurtain.fillStyle(0x01183c);
        this.upperCurtain.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT / 2);
        this.upperCurtain.y = -GameConstants.GAME_HEIGHT;
        this.add(this.upperCurtain);

        this.lowerCurtain = new Phaser.GameObjects.Graphics(this.scene);
        this.lowerCurtain.fillStyle(0x01183c);
        this.lowerCurtain.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT / 2);
        this.lowerCurtain.y = GameConstants.GAME_HEIGHT;
        this.add(this.lowerCurtain);

        this.scene.tweens.add({
            targets: this.upperCurtain,
            y: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 450
        });

        this.scene.tweens.add({
            targets: this.lowerCurtain,
            y: (GameConstants.GAME_HEIGHT / 2),
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 450,
            onComplete: this.onCloseTweenComplete,
            onCompleteScope: this
        });
    }

    private onCloseTweenComplete(): void {

        const gigaSwapContainer = ExchangeLayer.currentInstance.gigaSwapContainer;
        gigaSwapContainer.show();

        this.scene.tweens.add({
            targets: this.upperCurtain,
            y: -GameConstants.GAME_HEIGHT,
            ease: Phaser.Math.Easing.Cubic.In,
            duration: 450,
            delay: 400
        });

        this.scene.tweens.add({
            targets: this.lowerCurtain,
            y: GameConstants.GAME_HEIGHT,
            ease: Phaser.Math.Easing.Cubic.In,
            duration: 450,
            onComplete: this.destroy,
            onCompleteScope: this,
            delay: 400
        });
    }
}
