import { Column } from "./../Column";
import { GameConstants } from "../../../GameConstants";
import { GameVars } from "../../../GameVars";
import { Button } from "../../../utils/Button";

export class CarbzSlot extends Phaser.GameObjects.Container {

    private column: Column;
    private tmpBackground: Phaser.GameObjects.Graphics;
    private midContainer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, i: number, locked: boolean, lockedType?: number) {

        super(scene);

        this.x = -786 - 300 * i;

        if (locked) {
            this.showLocked(lockedType);
        } else if (!GameVars.unstakedDrink.length) {
            this.showBuyOnAtomic();
        } else {
            this.showStake();
        }
    }

    public setColumnColors(value?: number): void {

        if (this.column) {
            this.column.setColors(value);
        }
    }

    private showLocked(lockedType: number): void {

        this.column = new Column(this.scene, 150, 0);
        this.column.setColors();
        this.column.scaleX = -1;
        this.add(this.column);

        let w = 300;
        let h = GameConstants.GAME_HEIGHT;

        this.tmpBackground = new Phaser.GameObjects.Graphics(this.scene);
        this.tmpBackground.fillStyle(0x000044, .5);
        this.tmpBackground.fillRect(-w / 2 - 28, -h / 2, w, h);
        this.add(this.tmpBackground);

        this.midContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.midContainer);

        if (!GameVars.visitMode) {
            if (lockedType === 0) {

                let empty_area = new Phaser.GameObjects.Image(this.scene, 0, 20, "texture_atlas_1", "empty_area_slot_carbz");
                this.midContainer.add(empty_area);

                let textLocked = new Phaser.GameObjects.Text(this.scene, 0, -70, "LOCKED AREA", { fontFamily: "Oswald-Medium", fontSize: "25px", color: "#ff003b", align: "center" });
                textLocked.setShadow(2, 2, "#000000", 5);
                textLocked.setOrigin(.5);
                this.midContainer.add(textLocked);

                if (GameVars.unstakedSlots.length) {
                    let btnGet = new Button(this.scene, 0, -10, "texture_atlas_1", "btn_plus_green", "btn_plus_green_over");
                    btnGet.onDown(() => {
                        this.onClickStakeSlot();
                    }, this);
                    this.midContainer.add(btnGet);

                    let textGet = new Phaser.GameObjects.Text(this.scene, 0, 70, "STAKE AN AREA SLOT TO UNLOCK", { fontFamily: "Oswald-Medium", fontSize: "20px", color: "#66FF66", align: "center" });
                    textGet.setShadow(0, 0, "#00AA00", 5);
                    textGet.setWordWrapWidth(100);
                    textGet.setOrigin(.5);
                    this.midContainer.add(textGet);
                } else {
                    let btnGet = new Button(this.scene, 0, -10, "texture_atlas_1", "btn_plus_green", "btn_plus_green_over");
                    btnGet.onDown(this.onClickGet, this);
                    this.midContainer.add(btnGet);

                    let textGet = new Phaser.GameObjects.Text(this.scene, 0, 70, "CRAFT AN AREA SLOT TO UNLOCK", { fontFamily: "Oswald-Medium", fontSize: "20px", color: "#66FF66", align: "center" });
                    textGet.setShadow(0, 0, "#00AA00", 5);
                    textGet.setWordWrapWidth(100);
                    textGet.setOrigin(.5);
                    this.midContainer.add(textGet);
                }
            }
        }
    }

    private showBuyOnAtomic(): void {

        if (!GameVars.visitMode) {
            let textGet = new Phaser.GameObjects.Text(this.scene, 0, 40, "BUY A VENDING MACHINE", { fontFamily: "Oswald-Medium", fontSize: "22px", color: "#66FF66", align: "center" });
            textGet.setShadow(0, 0, "#00AA00", 5);
            textGet.setWordWrapWidth(100);
            textGet.setOrigin(.5);
            this.add(textGet);

            let btnGet = new Button(this.scene, 0, -40, "texture_atlas_1", "btn_plus_green", "btn_plus_green_over");
            btnGet.onDown(this.onClickGet, this);
            this.add(btnGet);
        }
    }

    private showStake(): void {

        if (!GameVars.visitMode) {
            let textGet = new Phaser.GameObjects.Text(this.scene, 0, 40, " STAKE A VENDING MACHINE", { fontFamily: "Oswald-Medium", fontSize: "22px", color: "#66FF66", align: "center" });
            textGet.setShadow(0, 0, "#00AA00", 5);
            textGet.setWordWrapWidth(100);
            textGet.setOrigin(.5);
            this.add(textGet);

            let btnGet = new Button(this.scene, 0, -40, "texture_atlas_1", "btn_plus_green", "btn_plus_green_over");
            btnGet.onDown(() => {
                this.onClickStake(btnGet);
            }, this);
            this.add(btnGet);
        }
    }

    private onClickGet(): void {

        if (GameVars.onAnimation) {
            return;
        }
        window.open("https://wax.atomichub.io/market?collection_name=clashdomenft&order=desc&schema_name=tool&sort=created&symbol=WAX", "_blank");
        GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "scroll-craft", data: null } });
    }

    private async onClickStake(btn: Button): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "scroll", data: null } });
    }

    private async onClickStakeSlot(): Promise<void> {

        if (GameVars.onAnimation) {
            return;
        }

        GameVars.clashdomeClient.dispatchMessageToWeb({ id: "token-mining-game", payload: { type: "scroll", data: null } });
    }
}
