import { GameManager } from "./../../GameManager";
import { GameVars } from "../../GameVars";

export class GUIColorSelector extends Phaser.GameObjects.Container {

    public selectedColor: number;

    private arrowLeft: Phaser.GameObjects.Image;
    private arrowRight: Phaser.GameObjects.Image;
    private colors: Phaser.GameObjects.Image[];

    private colorType: string;

    constructor(scene: Phaser.Scene, type: string, x: number, y: number) {

        super(scene);

        this.setPosition(x, y);

        this.colorType = type;
        this.selectedColor = GameVars.backgroundValues[this.colorType];

        console.log(this.selectedColor);

        this.arrowLeft = new Phaser.GameObjects.Image(this.scene, -200, 0, "texture_atlas_1", "btn_arrow");
        this.arrowLeft.setInteractive({ pixelPerfect: true, cursor: "pointer" });
        this.arrowLeft.on("pointerover", () => {
            this.arrowLeft.setScale(1.1);
        }, this);
        this.arrowLeft.on("pointerout", () => {
            this.arrowLeft.setScale(1);
        }, this);
        this.arrowLeft.on("pointerdown", () => {
                
            this.selectedColor = this.selectedColor === 0 ? 8 : this.selectedColor - 1;
            this.resetColors();
            GameManager.editColorSelected(this.colorType, this.selectedColor);

        }, this);
        this.add(this.arrowLeft);

        this.arrowRight = new Phaser.GameObjects.Image(this.scene, 200, 0, "texture_atlas_1", "btn_arrow");
        this.arrowRight.scaleX = -1;
        this.arrowRight.setInteractive({ pixelPerfect: true, cursor: "pointer" });
        this.arrowRight.on("pointerover", () => {
            this.arrowRight.setScale(-1.1, 1.1);
        }, this);
        this.arrowRight.on("pointerout", () => {
            this.arrowRight.setScale(-1, 1);
        }, this);
        this.arrowRight.on("pointerdown", () => {
                
            this.selectedColor = this.selectedColor === 8 ? 0 : this.selectedColor + 1;
            this.resetColors();
            GameManager.editColorSelected(this.colorType, this.selectedColor);

        }, this);
        this.add(this.arrowRight);

        this.colors = [];

        for (let i = 0; i < 9; i++) {

            let color = new Phaser.GameObjects.Image(this.scene, -160 + i * 40, 0, "texture_atlas_1", "btn_edit_color_" + (i + 1));
            color.name = (i + 1) + "";
            color.setInteractive({ pixelPerfect: true, cursor: "pointer" });
            color.on("pointerover", () => {
                color.setScale(i === this.selectedColor ? 1.2 : 1.1);
            }, this);
            color.on("pointerout", () => {
                color.setScale(i === this.selectedColor ? 1.2 : 1);
            }, this);
            color.on("pointerdown", () => {
                
                this.selectedColor = i;
                this.resetColors();
                GameManager.editColorSelected(this.colorType, i);

            }, this);
            this.add(color);
            this.colors.push(color);

            if (i === this.selectedColor) {
                color.setScale(1.2);
            }
        }
    }

    public setSelectedColor(): void {
        
        this.selectedColor = GameVars.backgroundValues[this.colorType];
    }

    public resetColors(): void {
        
        for (let i = 0; i < this.colors.length; i++) {
            this.colors[i].setScale(i === this.selectedColor ? 1.2 : 1);
        }
    }
}
