import { Background } from "./Background";
import { TrialCitizen } from "./TrialCitizen";
import { Arcade } from "./Arcade/Arcade";
import { Citizen } from "./Citizen";
import { Piggybank } from "./Piggybank";
import { GameConstants } from "../../GameConstants";
import { CarbzZone } from "./CarbzZone/CarbzZone";
import { JigosZone } from "./JigosZone/JigosZone";
import { GameVars } from "../../GameVars";
import { DraggableDecorationItem } from "./DraggableDecorationItem";
import { DraggableDecorationItemArea } from "./DraggableDecorationItemArea";
import { MainScene } from "./MainScene";

export class BaseContainer extends Phaser.GameObjects.Container {

    private background: Background;
    private citizen: Citizen;
    private trialCitizen: TrialCitizen;
    private arcade: Arcade;
    private piggybank: Piggybank;
    private carbzZone: CarbzZone;
    private jigosZone: JigosZone;
    private wallBehindItemsContainer: Phaser.GameObjects.Container;
    private wallStuffContainer: Phaser.GameObjects.Container;
    private ceilingStuffContainer: Phaser.GameObjects.Container;
    private floorStuffContainer: Phaser.GameObjects.Container;
    private floorBehindItemsContainer: Phaser.GameObjects.Container;
    private floorInfrontItemsContainer: Phaser.GameObjects.Container;
    private objectsContainer: Phaser.GameObjects.Container;
    private decorationData: any;
    private wallStuffItems: Phaser.GameObjects.Image[];
    private decorationItems: DraggableDecorationItem[];
    private frontWall: Phaser.GameObjects.Image;
    private frontFloor: Phaser.GameObjects.Image;
    private uiLayer: Phaser.GameObjects.Container;
    private dragAreas: { wall: Phaser.Geom.Rectangle, floorBehind: Phaser.Geom.Rectangle, floorInFront: Phaser.Geom.Rectangle };

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.decorationData = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? require("../../../assets/decoration_testnet.json") : require("../../../assets/decoration.json");

        this.dragAreas = {
            wall: new Phaser.Geom.Rectangle(),
            floorBehind: new Phaser.Geom.Rectangle(),
            floorInFront: new Phaser.Geom.Rectangle(),
        };

        this.setPosition(GameConstants.GAME_WIDTH / 2, GameConstants.GAME_HEIGHT / 2);

        this.background = new Background(this.scene);
        this.add(this.background);

        this.wallStuffContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.wallStuffContainer);

        this.wallBehindItemsContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.wallBehindItemsContainer);

        this.ceilingStuffContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.ceilingStuffContainer);

        this.frontWall = new Phaser.GameObjects.Image(this.scene, -1536, -250, "texture_atlas_3", this.decorationData.walls[GameVars.backgroundValues.wall].img);
        this.frontWall.setOrigin(0, 0);
        this.frontWall.setAlpha(.5);
        this.frontWall.setScale(3072 / this.frontWall.width, 1);
        this.add(this.frontWall);

        this.floorStuffContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.floorStuffContainer);

        this.frontFloor = new Phaser.GameObjects.Image(this.scene, -1536, 250, "texture_atlas_3", this.decorationData.floors[GameVars.backgroundValues.floor].img);
        this.frontFloor.setOrigin(0, 1);
        this.frontFloor.setAlpha(.5);
        this.frontFloor.setScale(3072 / this.frontFloor.width, 1);
        this.add(this.frontFloor);

        this.floorBehindItemsContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.floorBehindItemsContainer);

        this.objectsContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.objectsContainer);

        this.floorInfrontItemsContainer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.floorInfrontItemsContainer);

        this.uiLayer = new Phaser.GameObjects.Container(this.scene);
        this.add(this.uiLayer);

        this.wallStuffItems = [];
        this.decorationItems = [];

        const b = this.frontWall.getBounds();
        b.x = this.frontWall.x;
        b.y = this.frontWall.y;
        b.width = this.frontWall.displayWidth;
        b.height = this.frontWall.displayHeight;
        this.dragAreas.wall = b;
        this.dragAreas.floorBehind.setTo(b.x, this.dragAreas.wall.y + this.dragAreas.wall.height, b.width, 15);
        this.dragAreas.floorInFront.setTo(b.x, this.dragAreas.floorBehind.y + this.dragAreas.floorBehind.height + 35, b.width, 50);
    }

    public getDecorationItems(): DraggableDecorationItem[] {

        return this.decorationItems;
    }

    public reloadToolsZones(): void {

        if (this.carbzZone) {
            this.carbzZone.destroy();
        }

        this.carbzZone = new CarbzZone(this.scene, this.uiLayer);
        this.objectsContainer.add(this.carbzZone);

        if (this.jigosZone) {
            this.jigosZone.destroy();
        }

        this.jigosZone = new JigosZone(this.scene, this.uiLayer);
        this.objectsContainer.add(this.jigosZone);

        this.objectsContainer.bringToTop(this.citizen);
    }

    public reloadDecoration(): void {

        this.background.setBackground();
        this.frontWall.setFrame(this.decorationData.walls[GameVars.backgroundValues.wall].img);
        this.frontFloor.setFrame(this.decorationData.floors[GameVars.backgroundValues.floor].img);


        this.addDecoration();
        this.arcade.addMods();
        this.carbzZone.setColumns();
        this.jigosZone.setColumns();

        this.updateWallStuff(GameVars.backgroundValues.wall);
        this.updateFloorStuff(GameVars.backgroundValues.floor);

        if (this.citizen) {
            this.citizen.addAvatarDecoration();
        }
    }

    public enterEditMode(): void {

        if (this.citizen) {
            this.citizen.setVisible(false);
        }

        if (this.trialCitizen) {
            this.trialCitizen.setVisible(false);
        }
        this.enableDecorationItemDragging();
        this.piggybank?.enterEditMode();
        this.arcade?.enterEditMode();
        this.carbzZone?.enterEditMode();
        this.jigosZone?.enterEditMode();
    }

    public exitEditMode(undo: boolean): void {

        if (this.citizen) {
            this.citizen.setVisible(true);
        }

        if (this.trialCitizen) {
            this.trialCitizen.setVisible(true);
        }
        this.disableDecorationItemDragging();
        if (undo) {
            this.decorationItems.forEach(d => d.reset());
        }
        this.piggybank?.exitEditMode();
        this.arcade?.exitEditMode();
        this.carbzZone?.exitEditMode();
        this.jigosZone?.exitEditMode();
        MainScene.currentInstance.updateBalances();
    }

    public undoBackground(): void {

        this.background.undoBackground();
        this.frontWall.setFrame(this.decorationData.walls[GameVars.backgroundValues.wall].img);
        this.frontFloor.setFrame(this.decorationData.floors[GameVars.backgroundValues.floor].img);
        this.updateWallStuff(GameVars.backgroundValues.wall);
        this.updateFloorStuff(GameVars.backgroundValues.floor);

        this.carbzZone?.setColumns();
        this.jigosZone?.setColumns();
    }

    public editColorSelected(type: string, value: number): void {

        this.background.editColorSelected(type, value);

        if (type === "wall") {
            this.updateWallStuff(value);
            this.carbzZone.setColumns(value);
            this.jigosZone.setColumns(value);
            this.frontWall.setFrame(this.decorationData.walls[value].img);
        } else {
            this.frontFloor.setFrame(this.decorationData.floors[value].img);
            this.updateFloorStuff(value);
        }
    }

    public showStakeCitizen(citizensData: any): void {

        if (this.citizen) {
            this.citizen.destroy();
        }

        this.citizen = new Citizen(this.scene, -200, 190, false);
        this.objectsContainer.add(this.citizen);

        this.citizen.showStake(citizensData);

        if (this.arcade) {
            this.arcade.destroy();
        }

        this.arcade = new Arcade(this.scene, this.uiLayer);
        this.objectsContainer.add(this.arcade);
    }

    public showBuyCitizen(): void {

        if (this.citizen) {
            this.citizen.destroy();
        }

        this.citizen = new Citizen(this.scene, -200, 190, false);
        this.objectsContainer.add(this.citizen);

        this.citizen.showBuy();

        if (this.arcade) {
            this.arcade.destroy();
        }

        this.arcade = new Arcade(this.scene, this.uiLayer);
        this.objectsContainer.add(this.arcade);
    }

    public showTrialCitizen(): void {

        if (this.citizen) {
            this.citizen.destroy();
        }

        if (this.trialCitizen) {
            this.trialCitizen.destroy();
        }

        if (this.arcade) {
            this.arcade.destroy();
        }

        this.arcade = new Arcade(this.scene, this.uiLayer);
        this.objectsContainer.add(this.arcade);

        this.trialCitizen = new TrialCitizen(this.scene, -200, 190);
        this.objectsContainer.add(this.trialCitizen);
    }

    public blockchainValuesLoaded(visit: boolean): void {

        if (!visit) {
            if (this.arcade) {
                this.arcade.destroy();
            }

            this.arcade = new Arcade(this.scene, this.uiLayer);
            this.objectsContainer.add(this.arcade);

            if (this.piggybank) {
                this.piggybank.destroy();
            }

            this.piggybank = new Piggybank(this.scene, this.uiLayer);
            if (GameVars.votingAmount && GameVars.votingAmount > 0) {
                this.piggybank.updatePiggyAmount(GameVars.votingAmount);
                GameVars.votingAmount = 0;
            }
            this.objectsContainer.add(this.piggybank);

            if (this.carbzZone) {
                this.carbzZone.destroy();
            }

            this.carbzZone = new CarbzZone(this.scene, this.uiLayer);
            this.objectsContainer.add(this.carbzZone);

            if (this.jigosZone) {
                this.jigosZone.destroy();
            }

            this.jigosZone = new JigosZone(this.scene, this.uiLayer);
            this.objectsContainer.add(this.jigosZone);
        }

        if (GameVars.citizenData) {

            if (this.citizen) {
                this.citizen.destroy();
            }

            this.citizen = new Citizen(this.scene, -170, 190, true, visit);
            this.objectsContainer.add(this.citizen);
        }

        if (!visit) {
            this.background.setBackground();
            this.frontWall.setFrame(this.decorationData.walls[GameVars.backgroundValues.wall].img);
            this.frontFloor.setFrame(this.decorationData.floors[GameVars.backgroundValues.floor].img);
            this.updateWallStuff(GameVars.backgroundValues.wall);
            this.updateFloorStuff(GameVars.backgroundValues.floor);
            this.addDecoration();
            this.arcade.addMods();
            this.carbzZone.setColumns();
            this.jigosZone.setColumns();
        }
        this.decorationItems.forEach(d => {
            d.blockchainValuesLoaded(visit);
        });

    }

    public updatePlayerValues(): void {

        this.citizen.updateValues();
        this.piggybank.updateBattery();
    }

    public moveCitizen(type: string): void {

        if (this.trialCitizen) {

            if (type === "left") {
                this.trialCitizen.x = -400;
                this.trialCitizen.move(-600, true, type);
            } else if (type === "rigth") {
                this.trialCitizen.x = 400;
                this.trialCitizen.move(600, false, type);
            } else if (type === "center") {
                this.trialCitizen.move(-200, false, type);
            }

        } else {
            if (!this.citizen.staked) {
                return;
            }

            if (type === "left") {
                this.citizen.x = -400;
                this.citizen.move(-600, true);
            } else if (type === "rigth") {
                this.citizen.x = 400;
                this.citizen.move(600, false);
            } else if (type === "center") {
                this.citizen.move(-170, false);
            }
        }
    }

    public piggyClaimAnimation(): void {

        this.citizen.piggyClaimAnimation();
    }

    public onPiggyClaimAnimationEnd(): void {

        this.piggybank.onPiggyClaimAnimationEnd();
    }

    public enablePiggybankClaim(): void {

        this.piggybank.enableClaim();
    }

    public enableTreadmillClaim(index: number): void {

        this.citizen.treadmillClaimAnimation();
        this.jigosZone.enableClaim(index);
    }

    public enableDrinkMachineClaim(index: number): void {

        this.carbzZone.enableClaim(index);
    }

    public onClaimTransactionEnd(): void {

        this.piggybank.onClaimTransactionEnd();
    }

    public onClaimTreadmillTransactionEnd(index: number): void {

        this.jigosZone.onClaimTreadmillTransactionEnd(index);
    }

    public onClaimDrinkMachineTransactionEnd(index: number): void {

        this.carbzZone.onClaimDrinkMachineTransactionEnd(index);
    }

    public drinkClaimAnimation(rarity: string, index: number): void {

        this.citizen.drinkClaimAnimation(rarity, index);
    }

    public drinkClaimErrorAnimation(): void {

        this.citizen.walkBack();
    }

    public drinkStartClaimAnimation(rarity: string, index: number): void {

        this.citizen.drinkStartClaimAnimation(rarity, index);
    }

    public onDrinkClaimAnimationEnd(): void {

        this.carbzZone.onDrinkClaimAnimationEnd();
    }

    public treadmillClaimAnimation(rarity: string, index: number): void {

        this.citizen.treadmillClaimAnimation();
    }

    public treadmillStartClaimAnimation(rarity: string, index: number): void {

        this.citizen.treadmillStartClaimAnimation(rarity, index);
    }

    public onTreadmillClaimAnimationEnd(): void {

        this.jigosZone.onTreadmillClaimAnimationEnd();
    }

    public updateTools(): void {

        this.jigosZone.onTreadmillClaimAnimationEnd();
        this.carbzZone.onDrinkClaimAnimationEnd();
    }

    private updateWallStuff(value: number) {

        this.wallStuffContainer.removeAll();
        this.ceilingStuffContainer.removeAll();
        this.wallStuffItems = [];

        // DIVIDER

        const dividers_x = [20, 320, 620, 2452, 2752, 3052];

        for (let i = 0; i < dividers_x.length; i++) {
            let divider = new Phaser.GameObjects.Image(this.scene, -1536 + dividers_x[i], -249, "texture_atlas_3", "divider");
            divider.setOrigin(0);
            divider.setTint(Number(this.decorationData.walls[value].tint));
            this.wallStuffContainer.add(divider);
        }

        let addPlinth = true;
        let length = GameVars.stakedDecoration?.length || 0;
        for (let i = 0; i < length; i++) {
            if (this.decorationData.textures.walls[GameVars.stakedDecoration[i].template_id]) {
                let imgs = this.decorationData.textures.walls[GameVars.stakedDecoration[i].template_id].imgs
                for (let j = 0; j < imgs.length; j++) {
                    let ts = new Phaser.GameObjects.TileSprite(this.scene, 0, -90, 3100, 355, "texture_atlas_4", imgs[j]);
                    ts.setTint(Number(this.decorationData.walls[value].tint));
                    this.wallStuffContainer.add(ts);
                }
            } else if (this.decorationData.textures.ceiling[GameVars.stakedDecoration[i].template_id]) {
                let imgs = this.decorationData.textures.ceiling[GameVars.stakedDecoration[i].template_id].imgs
                for (let j = 0; j < imgs.length; j++) {
                    let img = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_4", imgs[j]);
                    let ts = new Phaser.GameObjects.TileSprite(this.scene, 0, - GameConstants.GAME_HEIGHT / 2, 3100, img.height, "texture_atlas_4", imgs[j]);
                    ts.setOrigin(.5, 0);
                    this.ceilingStuffContainer.add(ts);
                }
            }
        }

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_01", -1536 + 240, -259, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_02", -1536 + 891, -259, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_03", -1536 + 1142, -250 + 192, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_04", -1536 + 1169, -250 + 188, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_05", -1536 + 1892, -250 + 32, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_06", -1536 + 2180, -250 + 21, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_06", -1536 + 2480, -250 + 21, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_06", -1536 + 2780, -250 + 21, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_07", -1536 + 2500, -250 + 98, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_07", -1536 + 2500, -250 + 98, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_07", -1536 + 1395, -250, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_07", -1536 + 433, -250, 0, Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_08", -1536 + 557, -250 + 225, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_08", -1536 + 1518, -250 + 225, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_09", -1536 + 1208, -250 + 336, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        this.addWallStuffBorderAndFill("texture_atlas_3", "stuff_10", -1536 + 1893, -250 + 340, Number(this.decorationData.walls[value].outline), Number(this.decorationData.walls[value].tint));

        // PLINTH
        length = GameVars.stakedDecoration?.length || 0;
        for (let i = 0; i < length; i++) {
            if (this.decorationData.textures.baseboards[GameVars.stakedDecoration[i].template_id]) {
                let imgs = this.decorationData.textures.baseboards[GameVars.stakedDecoration[i].template_id].imgs
                addPlinth = false;
                for (let j = 0; j < imgs.length; j++) {
                    let ts = new Phaser.GameObjects.TileSprite(this.scene, 0, 102, 3100, 96, "texture_atlas_4", imgs[j]);
                    ts.setTint(Number(j === 0 ? this.decorationData.walls[value].outline : this.decorationData.walls[value].tint));
                    this.wallStuffContainer.add(ts);
                }
            }
        }

        if (addPlinth) {
            let ts = new Phaser.GameObjects.TileSprite(this.scene, 0, 113, 3100, 77, "texture_atlas_3", "plinth_fill");
            this.wallStuffContainer.add(ts);
            ts.setTint(Number(this.decorationData.walls[value].outline));

            ts = new Phaser.GameObjects.TileSprite(this.scene, 0, 113, 3100, 77, "texture_atlas_3", "plinth");
            this.wallStuffContainer.add(ts);
            ts.setTint(Number(this.decorationData.walls[value].tint));
        }
    }

    private updateFloorStuff(value: number) {

        this.floorStuffContainer.removeAll();
        let length = GameVars.stakedDecoration?.length || 0;
        for (let i = 0; i < length; i++) {

            if (this.decorationData.textures.floors[GameVars.stakedDecoration[i].template_id]) {

                let imgs = this.decorationData.textures.floors[GameVars.stakedDecoration[i].template_id].imgs

                for (let j = 0; j < imgs.length; j++) {

                    let stuff = new Phaser.GameObjects.TileSprite(this.scene, 0, 200, 3100, 100, "texture_atlas_4", imgs[j]);
                    stuff.setTint(Number(this.decorationData.floors[value].tint));
                    this.floorStuffContainer.add(stuff);
                }
            }
        }
    }

    private addWallStuffBorderAndFill(atlas: string, stuff: string, x: number, y: number, tintFill: number, tintOutline: number) {
        if (tintFill !== 0) {
            let s = new Phaser.GameObjects.Image(this.scene, x, y, atlas, `${stuff}_fill`);
            s.setOrigin(0);
            s.setTint(tintFill);
            this.wallStuffContainer.add(s);
            this.wallStuffItems.push(s);
        }

        if (tintOutline !== 0) {
            let b = new Phaser.GameObjects.Image(this.scene, x, y, atlas, `${stuff}`);
            b.setOrigin(0);
            b.setTint(tintOutline);
            this.wallStuffContainer.add(b);
            this.wallStuffItems.push(b);
        }
    }




    private addDecoration(): void {

        this.wallBehindItemsContainer.removeAll();
        this.floorBehindItemsContainer.removeAll();
        this.floorInfrontItemsContainer.removeAll();

        let positions = [];

        for (let j = 0; j < this.wallStuffItems.length; j++) {
            this.wallStuffItems[j].visible = true;
        }

        let itemsToAdd = [];

        let firstTime = true;

        if (GameVars.stakedDecoration) {
            for (let i = 0; i < GameVars.stakedDecoration.length; i++) {
                if (this.decorationData.items[GameVars.stakedDecoration[i].template_id]) {
                    itemsToAdd.push(GameVars.stakedDecoration[i]);
                    if (GameVars.stakedDecoration[i].data) {
                        firstTime = false;
                    }
                }
            }
        }

        this.decorationItems = [];

        if (firstTime) {
            positions = [
                { x: -420, y: -80 + Math.random() * 30 - 15 },
                { x: -250, y: -80 + Math.random() * 30 - 15 },
                { x: -80, y: -80 + Math.random() * 30 - 15 },
                { x: 90, y: -80 + Math.random() * 30 - 15 },
                { x: 260, y: -80 + Math.random() * 30 - 15 },
                { x: 430, y: -80 + Math.random() * 30 - 15 },
                { x: 600, y: -80 + Math.random() * 30 - 15 }
            ];
        }
        const addedItems = [];
        for (let i = 0; i < itemsToAdd.length; i++) {

            let posData = itemsToAdd[i].data ? JSON.parse(itemsToAdd[i].data) : null;

            const xx = posData?.x || positions[i]?.x || 0;
            const yy = posData?.y || positions[i]?.y || 0;

            let placeableAt = DraggableDecorationItemArea.WALL;
            let ox = 0.5;
            let oy = 0.5;

            const data = JSON.parse(JSON.stringify(this.decorationData.items[itemsToAdd[i].template_id]));
            data.asset_id = itemsToAdd[i].asset_id;

            let pumpkinTemplate = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? 527297 : 614596;
            let columnTemplate = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? 522363 : 605608;

            let aquariumTemplate = !GameVars.reactClass || !GameVars.reactClass.state || GameVars.reactClass.state.serverType === "testnet" ? 596004 : 643141;

            if (itemsToAdd[i].template_id === pumpkinTemplate) {
                data.atlas = "halloween";
                data.img = "halloween_pumpkins.png";
                placeableAt = DraggableDecorationItemArea.FLOOR_BEHIND | DraggableDecorationItemArea.FLOOR_INFRONT;
                ox = 0.5;
                oy = 0.8;
            } else if (itemsToAdd[i].template_id === columnTemplate) {
                placeableAt = DraggableDecorationItemArea.FLOOR_INFRONT;
                ox = 0.5;
                oy = 0.9;
            } else if (itemsToAdd[i].template_id === aquariumTemplate) {
                placeableAt = DraggableDecorationItemArea.WALL;
                ox = 0.5;
                oy = 0.5;
            }

            let item: DraggableDecorationItem;

            if (placeableAt & DraggableDecorationItemArea.WALL) {
                item = new DraggableDecorationItem(this.scene, xx, yy, data, this.wallBehindItemsContainer);
            } else if (placeableAt & DraggableDecorationItemArea.FLOOR_BEHIND) {
                item = new DraggableDecorationItem(this.scene, xx, yy, data, this.floorBehindItemsContainer);
            } else if (placeableAt & DraggableDecorationItemArea.FLOOR_INFRONT) {
                item = new DraggableDecorationItem(this.scene, xx, yy, data, this.floorInfrontItemsContainer);
            }
            item.placeableAt = placeableAt;
            item.dragRelativeX = ox;
            item.dragRelativeY = oy;
            this.wallBehindItemsContainer.add(item);
            this.decorationItems.push(item);
            this.onDraggableDecorationItemDragEnd(null, item);
            item.save();
            addedItems.push({ item, z: posData?.z || 0 });
            for (let j = 0; j < this.wallStuffItems.length; j++) {
                if (this.wallStuffItems[j].x < (item.x - item.width / 2) + item.width &&
                    this.wallStuffItems[j].x + this.wallStuffItems[j].width > (item.x - item.width / 2) &&
                    this.wallStuffItems[j].y < (item.y - item.height / 2) + item.height &&
                    this.wallStuffItems[j].y + this.wallStuffItems[j].height > (item.y - item.height / 2)) {
                    this.wallStuffItems[j].visible = false;
                }
            }
        }

        addedItems.sort((a, b) => a.z - b.z);

        for (let i = 0; i < addedItems.length; i++) {
            const o = addedItems[i];
            o.item.updateDepth(o.z);
        }
    }

    private enableDecorationItemDragging(): void {

        this.decorationItems.forEach(w => {
            w.enableInput(true);
            w.addListener(Phaser.Input.Events.GAMEOBJECT_POINTER_OVER, () => { this.onDraggableDecorationItemOver(w); }, this);
            w.addListener(Phaser.Input.Events.GAMEOBJECT_POINTER_OUT, () => { this.onDraggableDecorationItemOut(w); }, this);
            this.scene.input.setDraggable(w, true);
        });
        this.scene.input.on(Phaser.Input.Events.DRAG, this.onDraggableDecorationItemDrag, this);
        this.scene.input.on(Phaser.Input.Events.DRAG_END, this.onDraggableDecorationItemDragEnd, this);
        this.scene.input.on(Phaser.Input.Events.DRAG_START, this.onDraggableDecorationItemDragStart, this);
    }

    private onDraggableDecorationItemOver(target: DraggableDecorationItem): void {

        target.enableHighlight(true);
    }

    private onDraggableDecorationItemOut(target: DraggableDecorationItem): void {

        target.enableHighlight(false);
    }

    private onDraggableDecorationItemDrag(_: any, g: DraggableDecorationItem, x: number, y: number): void {

        g.updatePosition(x, y, false);
    }

    private onDraggableDecorationItemDragStart(_: any, g: DraggableDecorationItem, x: number, y: number): void {

        g.dragStarted();
    }

    private onDraggableDecorationItemDragEnd(_: any, g: DraggableDecorationItem): void {

        const dx = g.getDropReferenceX(g.x);
        const dy = g.getDropReferenceY(g.y);

        const areas = [];
        if (g.placeableAt & DraggableDecorationItemArea.WALL) {
            areas.push(this.dragAreas.wall);
        }
        if (g.placeableAt & DraggableDecorationItemArea.FLOOR_BEHIND) {
            areas.push(this.dragAreas.floorBehind);
        }
        if (g.placeableAt & DraggableDecorationItemArea.FLOOR_INFRONT) {
            areas.push(this.dragAreas.floorInFront);
        }

        const closest = areas.map(a => { return { rect: a, point: this.getClosestPointInsideRectangle(a, { x: dx, y: dy }) }; });
        closest.sort((a, b) => Phaser.Math.Distance.BetweenPointsSquared(a.point, { x: dx, y: dy }) - Phaser.Math.Distance.BetweenPointsSquared(b.point, { x: dx, y: dy }));

        g.updatePosition(closest[0].point.x, closest[0].point.y);
        if (closest[0].rect === this.dragAreas.wall) {
            g.updateContainers(this.wallBehindItemsContainer);
            g.solveIntersection(this.dragAreas.wall);
        } else if (closest[0].rect === this.dragAreas.floorInFront) {
            g.updateContainers(this.floorInfrontItemsContainer);
        } else if (closest[0].rect === this.dragAreas.floorBehind) {
            g.updateContainers(this.floorBehindItemsContainer);
        }
        g.dragEnded();
    }

    private getClosestPointInsideRectangle(rectangle: Phaser.Geom.Rectangle, point: Phaser.Types.Math.Vector2Like): Phaser.Types.Math.Vector2Like {

        if (rectangle.contains(point.x, point.y)) {
            return point;
        }

        const topLeft = rectangle.getPoint(0);
        const bottomRight = rectangle.getPoint(0.5);
        const topRight = { x: bottomRight.x, y: topLeft.y };
        const bottomLeft = { x: topLeft.x, y: bottomRight.y };

        let x = point.x;
        let y = point.y;
        if (point.x > topLeft.x && point.x < topRight.x) {
            if (point.y < topLeft.y) {
                y = topLeft.y;
            } else if (point.y > bottomLeft.y) {
                y = bottomLeft.y;
            }
        } else if (point.x < topLeft.x) {
            if (point.y < topLeft.y) {
                x = topLeft.x;
                y = topLeft.y;
            } else if (point.y > bottomLeft.y) {
                x = bottomLeft.x;
                y = bottomLeft.y;
            }
        } else if (point.x > topRight.x) {
            if (point.y < topRight.y) {
                x = topRight.x;
                y = topRight.y;
            } else if (point.y > bottomRight.y) {
                x = bottomRight.x;
                y = bottomRight.y;
            }
        }
        return { x, y };
    }

    private disableDecorationItemDragging(): void {

        this.decorationItems.forEach(w => {
            w.enableInput(false);
            w.removeAllListeners(Phaser.Input.Events.GAMEOBJECT_POINTER_OVER);
            w.removeAllListeners(Phaser.Input.Events.GAMEOBJECT_POINTER_OUT);
            this.scene.input.setDraggable(w, false);
        });
        this.scene.input.off(Phaser.Input.Events.DRAG, this.onDraggableDecorationItemDrag, this);
        this.scene.input.off(Phaser.Input.Events.DRAG_END, this.onDraggableDecorationItemDragEnd, this);
        this.scene.input.off(Phaser.Input.Events.DRAG_START, this.onDraggableDecorationItemDragStart, this);
    }
}
