import { CurtainsContainer } from "./CurtainsContainer";
import { FireContainer } from "./FireContainer";
import { GigaSwapGUI } from "./GigaSwapGUI";
import { CitizenContainer } from "./CitizenContainer";
import { GigaSwapManager } from "./GigaSwapManager";
import { GigaSwapHUD } from "./GigaSwapHUD";
import { GameConstants } from "../../GameConstants";
import { GigaSwapGameOverMenu } from "./GigaSwapGameOverMenu";
import { GigaSwapRetryMenu } from "./GigaSwapRetryMenu";

export class GigaSwapContainer extends Phaser.GameObjects.Container {

    public gigaSwapGUI: GigaSwapGUI;
    public playerContainer: CitizenContainer;
    public adversaryContainer: CitizenContainer;
    public gigaSwapHUD: GigaSwapHUD;
    public fireContainer: FireContainer;

    private curtainsContainer: CurtainsContainer;
    private itemsContainer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, carbz: number, credits: number, jigo: number) {

        super(scene);

        GigaSwapManager.init(carbz, credits, jigo);

        this.itemsContainer = new Phaser.GameObjects.Container(this.scene);
        this.itemsContainer.visible = false;
        this.add(this.itemsContainer);

        const background = new Phaser.GameObjects.Image(this.scene, 0, GameConstants.GAME_HEIGHT / 2, "background_gigaswap");
        background.setInteractive();
        this.itemsContainer.add(background);

        this.gigaSwapHUD = new GigaSwapHUD(this.scene, carbz, credits, jigo);
        this.itemsContainer.add(this.gigaSwapHUD);

        this.playerContainer = new CitizenContainer(this.scene, true);
        this.itemsContainer.add(this.playerContainer);

        this.adversaryContainer = new CitizenContainer(this.scene, false);
        this.itemsContainer.add(this.adversaryContainer);

        this.fireContainer = new FireContainer(this.scene);
        this.itemsContainer.add(this.fireContainer);

        this.gigaSwapGUI = new GigaSwapGUI(this.scene);  
        this.itemsContainer.add(this.gigaSwapGUI);

        this.curtainsContainer = new CurtainsContainer(this.scene);
        this.add(this.curtainsContainer);
    }

    public show(): void {

        this.itemsContainer.visible = true;

        this.playerContainer.showCitizen(1200);
    }

    public showRetryLayer(error: string): void {

        const retryMenu = new GigaSwapRetryMenu(this.scene, error);
        this.add(retryMenu);
    }

    public onMovesTransactionCompleted(): void {
        
        this.gigaSwapGUI.onMovesTransactionCompleted();

        this.playerContainer.onMovesTransactionCompleted();
        this.adversaryContainer.onMovesTransactionCompleted();
    }

    public onGameEnded(result: string): void {

        setTimeout(() => {
            this.playerContainer.onGameEnded(result);
            this.adversaryContainer.onGameEnded(result);
            this.gigaSwapGUI.onGameEnded(result);
            this.gigaSwapHUD.onGameEnded(result);

            const gameOverMenu = new GigaSwapGameOverMenu(this.scene, result);
            this.add(gameOverMenu);
        }, 4000);
    }
}
