import { GameManager } from "../GameManager";
import { GameConstants } from "../GameConstants";
import { GameVars } from "../GameVars";

export class PreloadScene extends Phaser.Scene {

    public static currentInstance: PreloadScene;

    private progressBar: Phaser.GameObjects.Graphics;
    private scaledItemmContainer: Phaser.GameObjects.Container;

    constructor() {

        super("PreloadScene");

        PreloadScene.currentInstance = this;
    }

    public preload(): void {

        this.composeScene();

        this.loadAssets();
    }

    public create(): void {

        GameManager.setCurrentScene(this);

        if (!GameVars.reactClass) {
            GameVars.intitialised = true;
            GameManager.onGameAssetsLoaded();
        } else if (GameVars.reactClass.state.wax.userAccount) {
            GameVars.intitialised = true;
            GameManager.onGameAssetsLoaded();
        } else {
            GameVars.preloadReady = true;

            this.scaledItemmContainer.visible = false;
            this.add.text(GameConstants.GAME_WIDTH / 2, GameConstants.GAME_HEIGHT / 2, "WAITING FOR LOGIN...", { fontFamily: "Oswald-ExtraLight", fontSize: "40px", color: "#FFFFFF" }).setOrigin(.5);
        }
    }

    private composeScene(): void {

        this.add.text(-2000, -2000, "abcdefg", { fontFamily: "Futura-Condensed", fontSize: "25px", color: "#FFFFFF" });
        this.add.text(-2000, -2000, "abcdefg", { fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#FFFFFF" });
        this.add.text(-2000, -2000, "abcdefg", { fontFamily: "Oswald-Light", fontSize: "25px", color: "#FFFFFF" });
        this.add.text(-2000, -2000, "abcdefg", { fontFamily: "Oswald-Medium", fontSize: "25px", color: "#FFFFFF" });

        const wall = this.add.image(0, 0, "wall_01");
        wall.setOrigin(0, 0);
        wall.scaleX = GameConstants.GAME_WIDTH / wall.width;

        const floor = this.add.image(0, GameConstants.GAME_HEIGHT, "floor_06");
        floor.setOrigin(0, 1);
        floor.scaleX = GameConstants.GAME_WIDTH / floor.width;

        this.scaledItemmContainer = this.add.container(GameConstants.GAME_WIDTH / 2, GameConstants.GAME_HEIGHT / 2);

        const logo = new Phaser.GameObjects.Image(this, 0, 0, "logo");
        this.scaledItemmContainer.add(logo);

        this.progressBar = new Phaser.GameObjects.Graphics(this);
        this.progressBar.fillGradientStyle(0xFF0098, 0xFF0098, 0x00DBFF, 0x00DBFF);
        this.progressBar.fillRect(0, 0, 8, 400);
        this.progressBar.angle = -90;
        this.progressBar.x = -200;
        this.progressBar.y = 54;
        this.progressBar.scaleY = 0;
        this.scaledItemmContainer.add(this.progressBar);

        const text = new Phaser.GameObjects.Text(this, 0, 83, "COMMUNICATING WITH THE DOME", { fontFamily: "Oswald-Light", fontSize: "22px", color: "#01183c", align: "center" });
        text.setOrigin(.5);
        this.scaledItemmContainer.add(text);
    }

    private loadAssets(): void {

        this.load.crossOrigin = "anonymous";

        this.load.image("background_gigaswap", "assets/background_gigaswap.jpg");
        this.load.html("input", "assets/input.html");
        this.load.html("select", "assets/select.html");

        this.load.atlas("texture_atlas_1", "assets/texture_atlas_1.png", "assets/texture_atlas_1.json");
        this.load.atlas("texture_atlas_2", "assets/texture_atlas_2.png", "assets/texture_atlas_2.json");
        this.load.atlas("texture_atlas_3", "assets/texture_atlas_3.png", "assets/texture_atlas_3.json");
        this.load.atlas("texture_atlas_4", "assets/texture_atlas_4.png", "assets/texture_atlas_4.json");

        (this.load as any).spine("avatar", "assets/spine/avatars.json", ["assets/spine/avatars.atlas"], true);
        (this.load as any).spine("trial", "assets/spine/trial.json", ["assets/spine/trial.atlas"], true);
        this.load.bitmapFont("LED_counter_7", "assets/fonts/LED_counter_7.png", "assets/fonts/LED_counter_7.xml");

        this.load.atlas("halloween", "assets//halloween/halloween.png", "assets/halloween/halloween.json");

        this.load.on("progress", this.updateLoadedPercentage, this);
    }

    private updateLoadedPercentage(percentageLoaded: number): void {

        if (this.progressBar.scaleY < percentageLoaded) {
            this.progressBar.scaleY = percentageLoaded;
        }
    }
}
