import { Column } from "./../Column";
import { Treadmill } from "./Treadmill";
import { GameVars } from "../../../GameVars";
import { JigosSlot } from "./JigosSlot";
import { GameConstants } from "../../../GameConstants";

export class JigosZone extends Phaser.GameObjects.Container {

    private column: Column;
    private treadmills: Treadmill[];
    private slots: JigosSlot[];
    private uiLayer: Phaser.GameObjects.Container;

    constructor(scene: Phaser.Scene, uiLayer: Phaser.GameObjects.Container) {

        super(scene);
        this.uiLayer = uiLayer;

        this.treadmills = [];
        this.slots = [];

        let count = 0;

        if (GameVars.stakedTreadmill) {
            for (let i = 0; i < GameVars.stakedTreadmill.length; i++) {

                let treadmill = new Treadmill(this.scene, i, uiLayer);
                this.add(treadmill);
                this.treadmills.push(treadmill);
                count++;
            }
        }

        while (count < 3 && GameVars.stakedSlotsTreadmill && GameVars.stakedSlotsTreadmill.length >= count) {

            let jigosSlot = new JigosSlot(this.scene, count, false);
            this.uiLayer.add(jigosSlot);
            this.sendToBack(jigosSlot);
            this.slots.push(jigosSlot);
            count++;
        }

        if (count < 3) {

            let jigosSlot = new JigosSlot(this.scene, count, true, 0);
            this.uiLayer.add(jigosSlot);
            this.sendToBack(jigosSlot);
            this.slots.push(jigosSlot);
            count++;
        }

        if (count < 3) {
            let jigosSlot = new JigosSlot(this.scene, count, true, 1);
            this.uiLayer.add(jigosSlot);
            this.sendToBack(jigosSlot);
            this.slots.push(jigosSlot);
        }

        this.column = new Column(this.scene, GameConstants.GAME_WIDTH * (3 / 2), 0);
        this.column.setColors();
        this.add(this.column);
        this.sendToBack(this.column);
    }

    public setColumns(value?: number): void {

        this.column.setColors(value);

        for (let i = 0; i < this.slots.length; i++) {
            this.slots[i].setColumnColors(value);
        }
    }

    public enterEditMode(): void {

        for (let i = 0; i < this.slots.length; i++) {
            this.slots[i].setVisible(false);
        }

        for (let i = 0; i < this.treadmills.length; i++) {
            this.treadmills[i].enterEditMode();
        }
    }

    public exitEditMode(): void {

        for (let i = 0; i < this.slots.length; i++) {
            this.slots[i].setVisible(true);
        }

        for (let i = 0; i < this.treadmills.length; i++) {
            this.treadmills[i].exitEditMode();
        }
    }

    public onTreadmillClaimAnimationEnd(): void {

        for (let i = 0; i < this.treadmills.length; i++) {
            this.remove(this.treadmills[i], true);
        }

        this.treadmills = [];

        for (let i = 0; i < GameVars.stakedTreadmill.length; i++) {

            let treadmill = new Treadmill(this.scene, i, this.uiLayer);
            this.add(treadmill);
            this.treadmills.push(treadmill);
        }
    }

    public onClaimTreadmillTransactionEnd(index: number): void {

        this.treadmills[index].onClaimEnd();
    }

    public enableClaim(index: number): void {

        this.treadmills[index].enableClaim();
    }

    destroy(fromScene?: boolean): void {
        this.uiLayer.remove(this.slots);
        super.destroy(fromScene);
    }
}
