import { NinePatch } from "@koreez/phaser3-ninepatch";
import { Scene } from "phaser";
import { GameVars } from "../../../GameVars";
import { GameBoxButton } from "./GameBoxButton";

export class GameBox extends Phaser.GameObjects.Container {

    public static GAME_CLICK = "GAME_CLICK";
    public static CLICKED_OUTSIDE = "CLICKED_OUTSIDE";

    private box: NinePatch;

    constructor(scene: Scene, x: number, y: number, width: number, columns: number) {

        super(scene, x, y);

        this.box = new NinePatch(this.scene, 0, 0, 200, 200, "texture_atlas_1", "game_box_container", { top: 20, bottom: 20, left: 20, right: 20 });
        this.add(this.box);

        const games = [
            {
                frame: "title_ringydingy",
                ludio: this.getRewardsForGame("ringy-dingy").ludio,
                multiplier: this.getRewardsForGame("ringy-dingy").mult,
                callback: () => {
                    GameVars.api.launchGame(GameVars.visitMode ? "/ringy-dingy/play?duel=true&private=true&adversary=" + GameVars.userName : "/ringy-dingy/play?duel=true");
                    // GameVars.api.launchGame("/ringy-dingy/play?duel=true");
                }
            },
            {
                frame: "title_templok",
                ludio: this.getRewardsForGame("templok").ludio,
                multiplier: this.getRewardsForGame("templok").mult,
                callback: () => {
                    GameVars.api.launchGame(GameVars.visitMode ? "/templok/play?duel=true&private=true&adversary=" + GameVars.userName : "/templok/play?duel=true");
                    // GameVars.api.launchGame("/templok/play?duel=true");
                }
            },
            {
                frame: "title_endlesssiege",
                ludio: this.getRewardsForGame("endless-siege-2").ludio,
                multiplier: this.getRewardsForGame("endless-siege-2").mult,
                callback: () => {
                    GameVars.api.launchGame(GameVars.visitMode ? "/endless-siege-2/play?duel=true&private=true&adversary=" + GameVars.userName : "/endless-siege-2/play?duel=true");
                    // GameVars.api.launchGame("/endless-siege-2/play?duel=true");
                }
            },
            {
                frame: "title_candyfiesta",
                ludio: this.getRewardsForGame("candy-fiesta").ludio,
                multiplier: this.getRewardsForGame("candy-fiesta").mult,
                callback: () => {
                    GameVars.api.launchGame(GameVars.visitMode ? "/candy-fiesta/play?duel=true&private=true&adversary=" + GameVars.userName : "/candy-fiesta/play?duel=true");
                    // GameVars.api.launchGame("/candy-fiesta/play?duel=true");
                }
            },
            {
                frame: "title_rugpool",
                ludio: this.getRewardsForGame("rug-pool").ludio,
                multiplier: this.getRewardsForGame("rug-pool").mult,
                callback: () => {
                    GameVars.api.launchGame(GameVars.visitMode ? "/rug-pool/play?duel=true&private=true&adversary=" + GameVars.userName : "/rug-pool/play?duel=true");
                }
            },
            {
                frame: "title_maze",
                comingSoon: true,
                ludio: null,
                multiplier: null,
                disabled: true,
                callback: () => {
                    //
                }
            }
        ];
        const padding = 20;
        const gap = 10;
        const buttonSize = ((width - padding * 2) - ((columns - 1) * gap)) / columns;

        const rows = Math.ceil(games.length / columns);
        const height = (padding * 2) + ((rows - 1) * gap) + (buttonSize * rows);

        this.box.resize(width, height);

        for (let i = 0; i < games.length; i++) {
            let xx = (buttonSize + gap) * (i % columns) - width / 2 + buttonSize / 2 + padding;
            let yy = (buttonSize + gap) * Math.floor(i / columns) - height / 2 + buttonSize / 2 + padding;
            const gb = new GameBoxButton(this.scene, xx, yy, buttonSize, "texture_atlas_1", games[i]);
            this.add(gb);

        }

        this.addEvent();

        this.once(Phaser.GameObjects.Events.DESTROY, this.onDestroy, this);
    }

    private getRewardsForGame(gameName: string): { ludio: number, mult: number } {

        if (GameVars.visitMode) {
            return { ludio: 0, mult: 0 };
        }
        return GameVars.gameRewards.find(g => g.game === gameName);
    }

    private async addEvent(): Promise<void> {

        await new Promise(r => setTimeout(r, 500));
        this.scene.input.on(Phaser.Input.Events.POINTER_DOWN, this.onPointerUp, this);
    }

    private onPointerUp(_: any, g: any): void {

        if (g[0] instanceof GameBoxButton) {
            (g[0] as GameBoxButton).GameData.callback.call(this);
        } else {
            this.emit(GameBox.CLICKED_OUTSIDE);
        }
    }

    private onDestroy(): void {

        this.scene.input.off(Phaser.Input.Events.POINTER_DOWN, this.onPointerUp, this);
    }
}
