import { GameVars } from "../../GameVars";
import { DraggableDecorationItemArea } from "./DraggableDecorationItemArea";

export class DraggableDecorationItem extends Phaser.GameObjects.Container {

    public placeableAt: DraggableDecorationItemArea = DraggableDecorationItemArea.NONE;
    public dragRelativeX: number;
    public dragRelativeY: number;

    private img: Phaser.GameObjects.Image;
    private highlight: Phaser.GameObjects.Image;
    private moveables: (Phaser.GameObjects.Image | Phaser.GameObjects.Sprite)[];
    private moveablePositions: Phaser.Types.Math.Vector2Like[];
    private resetX: number;
    private resetY: number;
    private resetDepth: number;
    private bounds: Phaser.Geom.Rectangle;
    private resetContainer: Phaser.GameObjects.Container;
    private tweenAnimationData: TweenAnimationData[];
    private decorationData: any;

    private portraitImage: Phaser.GameObjects.Sprite;

    public get DecorationData() {
        return this.decorationData;
    }

    public get ResetX() {
        return this.resetX;
    }

    public get ResetY() {
        return this.resetY;
    }

    public get ResetDepth() {
        return this.highlight.parentContainer.getIndex(this.highlight);
    }

    constructor(scene: Phaser.Scene, x: number, y: number, data: any, container: Phaser.GameObjects.Container) {

        super(scene);

        this.setPosition(x, y);
        this.resetX = x;
        this.resetY = y;
        this.resetDepth = data.depth;
        this.resetContainer = container;

        data = JSON.parse(JSON.stringify(data));
        this.decorationData = data;

        this.img = new Phaser.GameObjects.Image(this.scene, x, y, data.atlas || "texture_atlas_3", data.img);
        this.highlight = new Phaser.GameObjects.Image(this.scene, x, y, data.atlas || "texture_atlas_3", data.img);
        this.highlight.setDisplaySize(this.highlight.displayWidth + 5, this.highlight.displayHeight + 5);
        this.highlight.setTintFill(0x0000ff);
        this.highlight.setVisible(false);
        this.moveables = [this.highlight, this.img];
        this.moveablePositions = this.moveables.map(m => { return { x: m.x - this.x, y: m.y - this.y }; });

        container.add(this.highlight);
        container.add(this.img);

        if (!!data.portrait) {
            this.img.setVisible(false);
            data.components.unshift({
                id: data.img,
                type: "base"
            });
            data.components.unshift({
                type: "portrait"
            });
        }

        this.tweenAnimationData = [];

        if (!data.components) {
            // this.img.setAngle(Math.random() * 5 - 2.5);
            this.highlight.setAngle(this.img.angle);
        } else {
            for (let i = 0; i < data.components.length; i++) {

                let component = data.components[i];

                let anim: Phaser.GameObjects.Sprite = null;

                if (component.type === "portrait") {

                    anim = new Phaser.GameObjects.Sprite(this.scene, x, y - 5, "texture_atlas_3", "dummy_avatar");
                    this.portraitImage = anim;
                    if (GameVars.citizenData?.avatar) {

                        this.scene.load.setPath("https://ipfs.io/ipfs/");
                        this.scene.load.image("avatar_portrait", GameVars.citizenData.avatar);
                        this.scene.load.once(Phaser.Loader.Events.COMPLETE, () => {
                            // texture loaded so use instead of the placeholder
                            anim.setTexture("avatar_portrait");
                        }, this)
                        this.scene.load.start();
                    }

                    anim.setScale(123 / anim.displayWidth, 130 / anim.displayHeight);
                } else if (component.type === "base") {
                    anim = new Phaser.GameObjects.Sprite(this.scene, x, y, "texture_atlas_3", data.img);
                } else {
                    anim = new Phaser.GameObjects.Sprite(this.scene, x, y, "texture_atlas_3", data.img + "_" + component.id);
                }
                this.moveables.push(anim);
                this.moveablePositions.push({ x: anim.x - this.x, y: anim.y - this.y });

                if (component.type === "animated") {
                    for (let j = 0; j < component.animations.length; j++) {
                        if (component.animations[j].type === "frames") {
                            this.scene.anims.create({ key: "animation_" + data.img + "_" + component.id, frames: this.scene.anims.generateFrameNames("texture_atlas_3", { prefix: data.img + "_" + component.id + "_", start: 1, end: component.animations[j].frames, zeroPad: 2 }), frameRate: component.animations[j].frameRate || 12, repeat: -1 });
                            anim.play("animation_" + data.img + "_" + component.id);
                            this.scene.add.existing(anim);
                        } else if (component.animations[j].type === "tween") {

                            const twd: TweenAnimationData = {
                                timeout: 0,
                                target: anim,
                                attribute: component.animations[j].attribute,
                                from: component.animations[j].from,
                                to: component.animations[j].to,
                                moveableIndex: j,
                                duration: component.animations[j].time,
                                repeatDelay: component.animations[j].delay ? component.animations[j].delay : 0,
                                hold: component.animations[j].delay ? component.animations[j].delay : 0,
                                delayInit: component.animations[j].delay_init
                            };
                            this.tweenAnimationData.push(twd);
                            this.reApplyTween(twd);
                        }
                    }
                }

                container.add(anim);
            }
        }
        this.bounds = this.img.getBounds();
    }

    updateContainers(container: Phaser.GameObjects.Container) {
        container.add(this.highlight);
        container.add(this.img);
        this.moveables.forEach(m => {
            container.add(m);
        });
    }

    updateDepth() {
        this.moveables.forEach(m => {
            m.parentContainer.bringToTop(m);
        });
    }

    blockchainValuesLoaded(visit: boolean) {
        if (visit && this.portraitImage) {
            if (GameVars.citizenData?.avatar) {
                this.scene.load.setPath("https://ipfs.io/ipfs/");
                this.scene.load.image("avatar_portrait", GameVars.citizenData.avatar);
                this.scene.load.once(Phaser.Loader.Events.COMPLETE, () => {
                    // texture loaded so use instead of the placeholder
                    this.portraitImage.setTexture("avatar_portrait");
                }, this)
                this.scene.load.start();
            }

            this.portraitImage.setScale(123 / this.portraitImage.displayWidth, 130 / this.portraitImage.displayHeight);
        }
    }

    enableInput(enable: boolean) {
        if (!enable) {
            this.disableInteractive();
            return;
        }
        const bounds = this.img.getBounds();

        this.setInteractive({
            hitArea: new Phaser.Geom.Rectangle(-bounds.width / 2, -bounds.height / 2, bounds.width, bounds.height),
            hitAreaCallback: Phaser.Geom.Rectangle.Contains,
            cursor: "pointer"
        });
    }

    public enableHighlight(enable: boolean): void {

        this.highlight.setVisible(enable);
    }

    public updatePosition(x: number, y: number, applyOffset: boolean = true): void {

        const ox = applyOffset ? (this.dragRelativeX - 0.5) * this.bounds.width : 0;
        const oy = applyOffset ? (this.dragRelativeY - 0.5) * this.bounds.height : 0;
        this.x = x - ox;
        this.y = y - oy;
        this.moveables.forEach((m, i) => {
            m.x = this.moveablePositions[i].x + this.x;
            m.y = this.moveablePositions[i].y + this.y;
        });
    }

    public solveIntersection(other: Phaser.Geom.Rectangle): void {

        let ox = this.x;
        let oy = this.y;
        const topLeft = other.getPoint(0);
        const bottomRight = other.getPoint(0.5);

        ox = Phaser.Math.Clamp(ox, topLeft.x + this.bounds.width / 2, bottomRight.x - this.bounds.width / 2);
        oy = Phaser.Math.Clamp(oy, topLeft.y + this.bounds.height / 2, bottomRight.y - this.bounds.height / 2);

        this.updatePosition(ox, oy);
    }

    public getDropReferenceX(x: number): number {

        return x + (this.dragRelativeX - 0.5) * this.bounds.width;
    }

    public getDropReferenceY(y: number): number {

        return y + (this.dragRelativeY - 0.5) * this.bounds.height;
    }

    public save(): void {
        this.resetX = this.x;
        this.resetY = this.y;
    }

    public reset(): void {

        this.updatePosition(this.resetX, this.resetY, false);
        this.updateContainers(this.resetContainer);
        this.dragEnded();
    }

    public dragStarted(): void {

        this.tweenAnimationData.forEach(twd => {
            window.clearTimeout(twd.timeout);
            this.scene.tweens.killTweensOf(twd.target);
        });
        this.moveables.forEach((m, i) => {
            m.parentContainer.bringToTop(m);
        });
    }

    public dragEnded(): void {
        this.tweenAnimationData.forEach(twd => {
            this.reApplyTween(twd);
        });
    }

    private reApplyTween(twd: TweenAnimationData): void {

        window.clearTimeout(twd.timeout);
        this.scene.tweens.killTweensOf(twd.target);

        let from: number = twd.from;
        let to: number = twd.to;
        if (twd.attribute === "x" || twd.attribute === "y") {
            from = twd.target[twd.attribute] + twd.from;
            to = twd.target[twd.attribute] + twd.to;
        }

        twd.target[twd.attribute] = from;
        const t = window.setTimeout(() => {
            if (!this.scene) {
                return;
            }

            this.scene.tweens.add({
                targets: twd.target,
                [twd.attribute]: { from, to },
                ease: Phaser.Math.Easing.Linear,
                duration: twd.duration,
                repeat: -1,
                repeatDelay: twd.repeatDelay,
                hold: twd.hold,
                yoyo: true
            });
        }, twd.delayInit);
        twd.timeout = t;
    }
}

type TweenAnimationData = {
    timeout: number,
    target: Phaser.GameObjects.Sprite;
    moveableIndex: number;
    attribute: string;
    from: number;
    to: number;
    duration: number;
    repeatDelay: number;
    hold: number;
    delayInit: number;
};
