import "phaser";
import { Game } from "./Game";
import { GameConstants } from "./GameConstants";
import { BootScene } from "./scenes/BootScene";
import { PreloadScene } from "./scenes/PreloadScene";
import { MainScene } from "./scenes/main-scene/MainScene";
import { GameManager } from "./GameManager";
import { GameVars } from "./GameVars";
import { ClashdomeMessageClient } from "./utils/ClashdomeMessageClient";
import { NinePatchPlugin } from "@koreez/phaser3-ninepatch";

let game: Game;

if (GameConstants.DEVELOPMENT) {
    window.onload = () => {
        const gameConfig = {

            version: GameConstants.VERSION,
            type: Phaser.AUTO,
            backgroundColor: "000000",
            dom: {
                createContainer: true
            },
            scale: {
                mode: Phaser.Scale.FIT,
                autoCenter: Phaser.Scale.CENTER_BOTH,
                parent: "content",
                width: GameConstants.GAME_WIDTH,
                height: GameConstants.GAME_HEIGHT
            },
            plugins: {
                scene: [
                    {
                        key: "SpineWebGLPlugin",
                        plugin: SpineWebGLPlugin,
                        mapping: "spine"
                    }
                ],
                global: [{ key: "NinePatchPlugin", plugin: NinePatchPlugin, start: true }],
            },
            scene: [
                BootScene,
                PreloadScene,
                MainScene
            ]
        };

        // If compilation error here, compare Phaser definitions file of working copy (phaser.d.ts, line 48040 on 27-05-2019)
        // Also make sure to delete all *.ts files in node_modules/trailz folder
        game = new Game(gameConfig);

        window.focus();
    };
} else {
    const onload = (reactClass: any) => {

        const gameConfig = {

            version: GameConstants.VERSION,
            type: Phaser.AUTO,
            backgroundColor: "000000",
            dom: {
                createContainer: true
            },
            scale: {
                mode: Phaser.Scale.FIT,
                autoCenter: Phaser.Scale.CENTER_BOTH,
                parent: "content",
                width: GameConstants.GAME_WIDTH,
                height: GameConstants.GAME_HEIGHT
            },
            plugins: {
                scene: [
                    {
                        key: "SpineWebGLPlugin",
                        plugin: SpineWebGLPlugin,
                        mapping: "spine"
                    }
                ]
            },
            scene: [
                BootScene,
                PreloadScene,
                MainScene
            ]
        };

        // If compilation error here, compare Phaser definitions file of working copy (phaser.d.ts, line 48040 on 27-05-2019)
        // Also make sure to delete all *.ts files in node_modules/trailz folder
        game = new Game(gameConfig, reactClass);

        window.focus();

        GameVars.clashdomeClient.dispatchGameStarted(game);
    };

    try {

        GameVars.clashdomeClient = new ClashdomeMessageClient({
            onLoginSuccess: () => {
                GameManager.onLoginSuccess();
            },
            onStartGame: (r: any) => {
                onload(r);
            }
        });

        console.log(GameVars.clashdomeClient);
    } catch (e) {
        console.log(e);
    }

}




