import { GameConstants } from "../../GameConstants";
import { GameVars } from "../../GameVars";

export class HUD extends Phaser.GameObjects.Container {

    private carbzLabel: Phaser.GameObjects.Text;
    private creditsLabel: Phaser.GameObjects.Text;
    private jigoWattsLabel: Phaser.GameObjects.Text;
    private fps: Phaser.GameObjects.Text;
    private showTokensBar: boolean;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.x = GameConstants.GAME_WIDTH / 2;

        this.showTokensBar = GameVars.visitMode;


        const bck = new Phaser.GameObjects.Graphics(this.scene);
        bck.fillStyle(0x01183c, .5);
        bck.fillRect(- 3072 / 2, 0, 3072, 38);
        this.add(bck);

        const carbzIcon = new Phaser.GameObjects.Image(this.scene, -250, 19, "texture_atlas_1", "icon_carbz");
        this.add(carbzIcon);

        this.carbzLabel = new Phaser.GameObjects.Text(this.scene, -230, 19, "0", { fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#8AF722" });
        this.carbzLabel.setOrigin(0, .5);
        this.add(this.carbzLabel);

        const creditsIcon = new Phaser.GameObjects.Image(this.scene, -50, 19, "texture_atlas_1", "icon_credits");
        this.add(creditsIcon);

        this.creditsLabel = new Phaser.GameObjects.Text(this.scene, -30, 19, "0", { fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#FFF000" });
        this.creditsLabel.setOrigin(0, .5);
        this.add(this.creditsLabel);

        const jigowattsIcon = new Phaser.GameObjects.Image(this.scene, 150, 19, "texture_atlas_1", "icon_jigowatts");
        this.add(jigowattsIcon);

        this.jigoWattsLabel = new Phaser.GameObjects.Text(this.scene, 170, 19, "0", { fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#44FFF8" });
        this.jigoWattsLabel.setOrigin(0, .5);
        this.add(this.jigoWattsLabel);

        this.fps = new Phaser.GameObjects.Text(this.scene, GameConstants.GAME_WIDTH / 2 - 40, 19, "60", { fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#8AF722" });
        this.fps.setOrigin(0, .5);
        this.fps.visible = false;
        this.add(this.fps);

        this.scene.input.keyboard.createCombo("FPS");

        this.scene.input.keyboard.on("keycombomatch", function (event) {
            this.fps.visible = !this.fps.visible;
            this.scene.input.keyboard.createCombo("FPS");
        }, this);

        this.scene.sys.updateList.add(this);

        bck.setVisible(this.showTokensBar);
        carbzIcon.setVisible(this.showTokensBar);
        this.carbzLabel.setVisible(this.showTokensBar);
        creditsIcon.setVisible(this.showTokensBar);
        this.creditsLabel.setVisible(this.showTokensBar);
        jigowattsIcon.setVisible(this.showTokensBar);
        this.jigoWattsLabel.setVisible(this.showTokensBar);


    }

    public preUpdate(time: number, delta: number): void {

        if (this.fps.visible) {
            this.fps.setText(Math.round(this.scene.game.loop.actualFps) + "");
        }
    }

    public updateBalances(): void {

        if (GameVars.playerInfo) {

            const currentCarbz = parseFloat(this.carbzLabel.text.replace(/,/g, ""));
            const currentCredits = parseFloat(this.creditsLabel.text.replace(/,/g, ""));
            const currentJigowatts = parseFloat(this.jigoWattsLabel.text.replace(/,/g, ""));

            if (currentCarbz !== GameVars.playerInfo.carbs) {
                const tween = this.scene.tweens.addCounter({
                    from: currentCarbz,
                    to: GameVars.playerInfo.carbs,
                    duration: 500,
                    onUpdate: () => {

                        this.carbzLabel.setText(GameVars.formatNumber(tween.getValue()));
                    },
                    onUpdateScope: this
                });
            }

            if (currentCredits !== GameVars.playerInfo.credits) {
                const tween = this.scene.tweens.addCounter({
                    from: currentCredits,
                    to: GameVars.playerInfo.credits,
                    duration: 500,
                    onUpdate: () => {

                        this.creditsLabel.setText(GameVars.formatNumber(tween.getValue()));
                    },
                    onUpdateScope: this
                });
            }

            if (currentJigowatts !== GameVars.playerInfo.jigowatts) {
                const tween = this.scene.tweens.addCounter({
                    from: currentJigowatts,
                    to: GameVars.playerInfo.jigowatts,
                    duration: 500,
                    onUpdate: () => {

                        this.jigoWattsLabel.setText(GameVars.formatNumber(tween.getValue()));
                    },
                    onUpdateScope: this
                });
            }
        }

    }
}
