import { GameConstants } from "../../GameConstants";
import { GameVars } from "../../GameVars";

export class GigaSwapHUD extends Phaser.GameObjects.Container {

    private carbzLabel: Phaser.GameObjects.Text;
    private creditsLabel: Phaser.GameObjects.Text;
    private jigoWattsLabel: Phaser.GameObjects.Text;

    constructor(scene: Phaser.Scene, carbz: number, credits: number, jigo: number) {

        super(scene);

        const background = new Phaser.GameObjects.Graphics(this.scene);
        background.fillStyle(0x000000);
        background.fillRect(-GameConstants.GAME_WIDTH / 2, 0, GameConstants.GAME_WIDTH, 38);
        this.add(background);

        const carbzIcon = new Phaser.GameObjects.Image(this.scene, -250, 19, "texture_atlas_1", "icon_carbz");
        this.add(carbzIcon);

        this.carbzLabel = new Phaser.GameObjects.Text(this.scene, -230, 19, GameVars.formatNumber(carbz), {fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#8AF722"});
        this.carbzLabel.setOrigin(0, .5);
        this.add(this.carbzLabel);

        const creditsIcon = new Phaser.GameObjects.Image(this.scene, -50, 19, "texture_atlas_1", "icon_credits");
        this.add(creditsIcon);

        this.creditsLabel = new Phaser.GameObjects.Text(this.scene, -30, 19, GameVars.formatNumber(credits), {fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#FFF000"});
        this.creditsLabel.setOrigin(0, .5);
        this.add(this.creditsLabel);

        const jigowattsIcon = new Phaser.GameObjects.Image(this.scene, 150, 19, "texture_atlas_1", "icon_jigowatts");
        this.add(jigowattsIcon);

        this.jigoWattsLabel = new Phaser.GameObjects.Text(this.scene, 170, 19, GameVars.formatNumber(jigo), {fontFamily: "Oswald-ExtraLight", fontSize: "25px", color: "#44FFF8"});
        this.jigoWattsLabel.setOrigin(0, .5);
        this.add(this.jigoWattsLabel);
    }

    public onGameEnded(result: string): void {
       
        this.scene.tweens.add({
            targets: this,
            y: -40,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 350,
            delay: 500,
            onComplete: function(): void {
                this.destroy();
            },
            onCompleteScope: this
        });
    }
}
