export class ButtonWithText extends Phaser.GameObjects.Container {

    private enabled: boolean;
    
    private originalScale: number;

    constructor(scene: Phaser.Scene, buttonImage: Phaser.GameObjects.Image, label: Phaser.GameObjects.Text, callback: Function, context: any) {
        
        super(scene);

        this.enabled = true;
        this.originalScale = 1;

        buttonImage.setInteractive({ useHandCursor: true });

        buttonImage.on("pointerover", () => { 
            if (this.enabled) {
                this.originalScale = this.scaleX;
                this.setScale(1.025 * this.originalScale);  
            } 
        }, this);
        
        buttonImage.on("pointerout", () => { 
            if (this.enabled) {
                this.setScale(this.originalScale); 
            } 
        }, this);

        buttonImage.on("pointerup", () => {
            if (this.enabled) {
                callback.apply(context);
                this.setScale(this.originalScale); 
            }
        }, this);
        this.add(buttonImage);

        this.add(label);
    }

    public enable(): void {

        this.alpha = 1;
        this.enabled = true;
    }

    public disable(): void {

        this.alpha = .5;
        this.enabled = false;
    }
}
