import { GUIColorSelector } from './GUIColorSelector';
import { MainScene } from "./MainScene";
import { GameConstants } from "../../GameConstants";
import { Button } from "../../utils/Button";
import { GameManager } from "../../GameManager";
import { GameVars } from "../../GameVars";

export class GUI extends Phaser.GameObjects.Container {

    public exchangeBtn: Button;
    public editBtn: Button;
    public cancelBtn: Button;
    public saveBtn: Phaser.GameObjects.Container;

    private wallColorSelector: GUIColorSelector;
    private floorColorSelector: GUIColorSelector;

    private leftBtn: Button;
    private rightBtn: Button;
    private leftAlert: Phaser.GameObjects.Image;
    private rightAlert: Phaser.GameObjects.Image;
    private tweening: boolean;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.x = GameConstants.GAME_WIDTH / 2;
        this.y = GameConstants.GAME_HEIGHT / 2;

        this.tweening = false;

        this.exchangeBtn = new Button(this.scene, -GameConstants.GAME_WIDTH / 2 + 50, -GameConstants.GAME_HEIGHT / 2 + 19, "texture_atlas_1", "btn_confirm_off", "btn_confirm_on");
        this.exchangeBtn.onDown(this.onDownExchange, this);
        this.add(this.exchangeBtn);

        this.editBtn = new Button(this.scene, this.exchangeBtn.x + this.exchangeBtn.width / 2 + 42, -GameConstants.GAME_HEIGHT / 2 + 19, "texture_atlas_1", "btn_edit", "btn_edit");
        this.editBtn.setVisible(false);
        this.editBtn.onDown(this.onDownEdit, this);
        this.add(this.editBtn);

        this.cancelBtn = new Button(this.scene, -GameConstants.GAME_WIDTH / 2 + 50, -GameConstants.GAME_HEIGHT / 2 + 19, "texture_atlas_1", "btn_cancel", "btn_cancel");
        this.cancelBtn.setVisible(false);
        this.cancelBtn.onDown(this.onDownCancel, this);
        this.add(this.cancelBtn);

        this.saveBtn = new Phaser.GameObjects.Container(this.scene, this.cancelBtn.x + this.cancelBtn.width / 2 + 62, -GameConstants.GAME_HEIGHT / 2 + 19);
        this.saveBtn.setVisible(false);
        this.add(this.saveBtn);

        let btn = new Button(this.scene, 0, 0, "texture_atlas_1", "btn_save_price", "btn_save_price");
        btn.onDown(this.onDownSave, this);
        this.saveBtn.add(btn);

        let text = new Phaser.GameObjects.Text(this.scene, 20, -0.5, "25", { fontFamily: "Oswald-Medium", fontSize: "18px", color: "#FFFFFF" });
        text.setOrigin(.5);
        this.saveBtn.add(text);

        const jigowattsIcon = new Phaser.GameObjects.Image(this.scene, 40, 0, "texture_atlas_1", "icon_jigowatts");
        jigowattsIcon.setScale(.6);
        jigowattsIcon.setTintFill(0xFFFFFF);
        this.saveBtn.add(jigowattsIcon);

        this.wallColorSelector = new GUIColorSelector(this.scene, "wall", 0, 112);
        this.wallColorSelector.setVisible(false);
        this.add(this.wallColorSelector);

        this.floorColorSelector = new GUIColorSelector(this.scene, "floor", 0, 200);
        this.floorColorSelector.setVisible(false);
        this.add(this.floorColorSelector);

        this.leftBtn = new Button(this.scene, -480, 200, "texture_atlas_1", "arrow_carbz", "arrow_carbz");
        this.leftBtn.scaleX = -1;
        this.leftBtn.onDown(this.onLeftDown, this);
        this.leftBtn.onOver(function (): void {
            this.leftBtn.setScale(-1.1, 1.1);
        }, this);
        this.leftBtn.onOut(function (): void {
            this.leftBtn.setScale(-1, 1);
        }, this);
        this.add(this.leftBtn);

        this.rightBtn = new Button(this.scene, 480, 200, "texture_atlas_1", "arrow_jigowatts", "arrow_jigowatts");
        this.rightBtn.onDown(this.onRightDown, this);
        this.rightBtn.onOver(function (): void {
            this.rightBtn.setScale(1.1);
        }, this);
        this.rightBtn.onOut(function (): void {
            this.rightBtn.setScale(1);
        }, this);
        this.add(this.rightBtn);

        this.leftAlert = new Phaser.GameObjects.Image(this.scene, this.leftBtn.x, this.leftBtn.y - 25, "texture_atlas_1", "alert");
        this.add(this.leftAlert);

        this.rightAlert = new Phaser.GameObjects.Image(this.scene, this.rightBtn.x, this.rightBtn.y - 25, "texture_atlas_1", "alert");
        this.add(this.rightAlert);

        this.scene.tweens.add({
            targets: this.leftBtn,
            scaleX: -1.1,
            scaleY: 1.1,
            ease: Phaser.Math.Easing.Linear,
            duration: 1000,
            yoyo: true,
            repeat: -1
        });

        this.scene.tweens.add({
            targets: this.rightBtn,
            scaleX: 1.1,
            scaleY: 1.1,
            ease: Phaser.Math.Easing.Linear,
            duration: 1000,
            yoyo: true,
            repeat: -1
        });

        this.leftBtn.visible = false;
        this.rightBtn.visible = false;

        this.leftAlert.visible = false;
        this.rightAlert.visible = false;

        if (GameVars.visitMode) {
            this.exchangeBtn.visible = false;
            this.leftBtn.x += 10;
            this.leftBtn.y -= 10;
            this.rightBtn.x -= 10;
            this.rightBtn.y -= 10;
        }
    }

    public showButtons(): void {

        this.wallColorSelector.setSelectedColor();
        this.floorColorSelector.setSelectedColor();

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH / 2) {
            this.leftBtn.visible = true;
            this.rightBtn.visible = true;

            if (!GameVars.trialInfo) {
                this.leftAlert.visible = this.leftBtn.visible && GameManager.showLeftAlert();
                this.rightAlert.visible = this.rightBtn.visible && GameManager.showRigthAlert();
            }
        } else if (MainScene.currentInstance.baseContainer.x === - GameConstants.GAME_WIDTH / 2) {
            this.leftBtn.visible = true;
            this.rightBtn.visible = false;

            this.leftAlert.visible = true;
            this.rightAlert.visible = false;
        } else if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH * 1.5) {
            this.leftBtn.visible = false;
            this.rightBtn.visible = true;

            this.leftAlert.visible = false;
            this.rightAlert.visible = true;
        }

        this.exchangeBtn.visible = GameVars.trialInfo || GameVars.visitMode ? false : true;
        this.editBtn.setVisible((!GameVars.visitMode) ? true : false);
    }

    public showEditButton(): void {

        this.editBtn.setVisible((!GameVars.visitMode) ? true : false);
    }

    public onLeftDown(): void {

        if (GameVars.onAnimation || GameVars.paused) {
            return;
        }

        if (this.tweening) {
            return;
        }

        this.tweening = true;
        GameVars.onAnimation = true;

        this.leftBtn.alpha = 0;
        this.rightBtn.alpha = 0;

        this.leftAlert.alpha = 0;
        this.rightAlert.alpha = 0;

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH / 2) {
            this.leftBtn.visible = false;
            this.leftAlert.visible = false;
            this.rightBtn.setFrames("arrow_credits", "arrow_credits");
            setTimeout(() => {
                GameManager.moveCitizen("left");
            }, 500);
        }

        if (MainScene.currentInstance.baseContainer.x === - GameConstants.GAME_WIDTH / 2) {
            this.rightBtn.visible = true;
            this.rightAlert.visible = GameManager.showRigthAlert();
            this.leftBtn.setFrames("arrow_carbz", "arrow_carbz");
            setTimeout(() => {
                GameManager.moveCitizen("center");
            }, 500);
        }

        this.scene.tweens.add({
            targets: MainScene.currentInstance.baseContainer,
            x: MainScene.currentInstance.baseContainer.x + GameConstants.GAME_WIDTH,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: () => {
                GameVars.onAnimation = false;
                this.tweening = false;
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 1;

                this.leftAlert.alpha = 1;
                this.rightAlert.alpha = 1;
            },
            onCompleteScope: this
        });
    }

    public showLeftZone(): void {

        if (GameVars.onAnimation || GameVars.paused) {
            return;
        }

        if (this.tweening) {
            return;
        }

        this.leftBtn.visible = false;
        this.leftAlert.visible = false;
        this.rightBtn.setFrames("arrow_credits", "arrow_credits");
        this.rightBtn.visible = true;

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH / 2) {
            MainScene.currentInstance.baseContainer.x = MainScene.currentInstance.baseContainer.x + GameConstants.GAME_WIDTH;
        }

        if (MainScene.currentInstance.baseContainer.x === - GameConstants.GAME_WIDTH / 2) {
            MainScene.currentInstance.baseContainer.x = MainScene.currentInstance.baseContainer.x + GameConstants.GAME_WIDTH * 2;
        }

        GameManager.moveCitizen("left");
    }

    public showRightZone(): void {

        if (GameVars.onAnimation || GameVars.paused) {
            return;
        }

        if (this.tweening) {
            return;
        }

        this.rightBtn.visible = false;
        this.rightAlert.visible = false;
        this.leftBtn.setFrames("arrow_credits", "arrow_credits");
        this.leftBtn.visible = true;

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH / 2) {
            MainScene.currentInstance.baseContainer.x = MainScene.currentInstance.baseContainer.x - GameConstants.GAME_WIDTH;
        }

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH * 3 / 2) {
            MainScene.currentInstance.baseContainer.x = MainScene.currentInstance.baseContainer.x - GameConstants.GAME_WIDTH * 2;
        }

        GameManager.moveCitizen("right");
    }

    public onRightDown(): void {

        if (GameVars.onAnimation || GameVars.paused || this.tweening) {
            return;
        }

        this.tweening = true;
        GameVars.onAnimation = true;

        this.leftBtn.alpha = 0;
        this.rightBtn.alpha = 0;

        this.leftAlert.alpha = 0;
        this.rightAlert.alpha = 0;

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH / 2) {
            this.rightBtn.visible = false;
            this.rightAlert.visible = false;
            this.leftBtn.setFrames("arrow_credits", "arrow_credits");
            setTimeout(() => {
                GameManager.moveCitizen("rigth");
            }, 500);
        }

        if (MainScene.currentInstance.baseContainer.x === GameConstants.GAME_WIDTH * 3 / 2) {
            this.leftBtn.visible = true;
            this.leftAlert.visible = GameManager.showLeftAlert();
            this.rightBtn.setFrames("arrow_jigowatts", "arrow_jigowatts");
            setTimeout(() => {
                GameManager.moveCitizen("center");
            }, 500);
        }

        this.scene.tweens.add({
            targets: MainScene.currentInstance.baseContainer,
            x: MainScene.currentInstance.baseContainer.x - GameConstants.GAME_WIDTH,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: () => {
                GameVars.onAnimation = false;
                this.tweening = false;
                this.leftBtn.alpha = 1;
                this.rightBtn.alpha = 1;

                this.leftAlert.alpha = 1;
                this.rightAlert.alpha = 1;
            },
            onCompleteScope: this
        });
    }

    public enterEditMode(): void {

        this.exchangeBtn.setVisible(false);
        this.editBtn.setVisible(false);

        this.cancelBtn.setVisible(true);
        this.saveBtn.setVisible(true);

        this.leftAlert.setVisible(false);
        this.rightAlert.setVisible(false);

        this.wallColorSelector.setVisible(GameVars.stakedApartmentCustom ? true : false);
        this.floorColorSelector.setVisible(GameVars.stakedApartmentCustom ? true : false);
    }

    public exitEditMode(): void {

        this.exchangeBtn.setVisible(true);
        this.editBtn.setVisible((!GameVars.visitMode) ? true : false);

        this.cancelBtn.setVisible(false);
        this.saveBtn.setVisible(false);

        this.leftAlert.visible = this.leftBtn.visible && GameManager.showLeftAlert();
        this.rightAlert.visible = this.rightBtn.visible && GameManager.showRigthAlert();

        this.wallColorSelector.setVisible(false);
        this.floorColorSelector.setVisible(false);
    }

    private onDownExchange(): void {

        if (GameVars.paused) {
            return;
        }

        GameManager.showExchangeLayer();
    }

    private onDownEdit(): void {

        if (GameVars.paused) {
            return;
        }

        GameManager.enterEditMode();
    }

    private onDownCancel(): void {

        if (GameVars.paused) {
            return;
        }

        GameManager.exitEditMode(true);
    }

    private onDownSave(): void {

        if (GameVars.paused) {
            return;
        }

        (async () => {

            MainScene.currentInstance.showBlockingLayer();

            let result = await GameManager.saveDecorationData(this.wallColorSelector.selectedColor, this.floorColorSelector.selectedColor);

            // console.log(result);

            MainScene.currentInstance.hideBlockingLayer();

            if (result) {
                GameManager.saveNewBackground(this.wallColorSelector.selectedColor, this.floorColorSelector.selectedColor);
                GameManager.exitEditMode(false);
            }
        })();


    }
}
