import { Scene } from "phaser";

export class LedText extends Phaser.GameObjects.Container {

    private bmpText: Phaser.GameObjects.BitmapText;

    public get text() {
        return this.bmpText.text;
    }
    public set text(value: string) {
        this.bmpText.setText(value);
        this.align();
    }

    constructor(scene: Scene, x: number, y: number, text: string, size: number, tint: number) {
        
        super(scene, x, y);

        this.bmpText = new Phaser.GameObjects.BitmapText(scene, 0, 0, "LED_counter_7", text);
        this.bmpText
            .setFontSize(30)
            .setFontSize(size)
            .setTint(tint)
            .setCenterAlign();

        this.add(this.bmpText);

        this.align();
    }

    private align(): void {

        const rect = this.bmpText.getTextBounds();
        this.bmpText.x = -rect.local.width / 2;
        this.bmpText.y = -rect.local.height / 2;
    }
}
