import { GameConstants } from "../../GameConstants";
import { GameVars } from "../../GameVars";
import { ExchangeLayer } from "../exchange/ExchangeLayer";

export class GigaSwapManager {

    public static playerMoves: number[];
    public static adversaryMoves: number[];
    public static adversaryAccount: string;
    public static roundResults: string [];
    public static round: number;
    public static carbz: number;
    public static credits: number;
    public static jigo: number;

    public static init(carbz: number, credits: number, jigo: number): void {
        
        GigaSwapManager.playerMoves = [];
        GigaSwapManager.adversaryMoves = [];
        GigaSwapManager.adversaryAccount = null;
        GigaSwapManager.roundResults = [];
        GigaSwapManager.round = 0;

        GigaSwapManager.carbz = carbz;
        GigaSwapManager.credits = credits;
        GigaSwapManager.jigo = jigo;
    }    

    public static onMoveSelected(move: number): void {

        GigaSwapManager.playerMoves.push(move);

        const playerSelectedMovesContainer = ExchangeLayer.currentInstance.gigaSwapContainer.playerContainer.selectedMovesContainer;

        playerSelectedMovesContainer.onMoveSelected(move);
    }

    public static onGigaSwapMovesSelected(): void {

        (async () => {

            let quantities = [];

            if (GigaSwapManager.carbz) {
                quantities.push(GigaSwapManager.carbz.toFixed(4) + " CDCARBZ");
            }

            if (GigaSwapManager.credits) {
                quantities.push(GigaSwapManager.credits.toFixed(4) + " LUDIO");
            }

            if (GigaSwapManager.jigo) {
                quantities.push(GigaSwapManager.jigo.toFixed(4) + " CDJIGO");
            }

            let timestamp = Math.round(Date.now() / 1000);
            let choices = GigaSwapManager.playerMoves[0] + "," + GigaSwapManager.playerMoves[1] + "," + GigaSwapManager.playerMoves[2];

            let data = await GameVars.api.gigaswapTokens({from: GameVars.userName, to: GameConstants.CONTRACT_NAME, quantities: quantities, memo: "gigaswap:" + timestamp + ":" + choices});
            
            if (!data.error) {

                let data2 = await GameVars.api.checkGigaswapState();

                while (!data2 || data2[0].status === 0 || data2[0].timestamp !== timestamp) {

                    await new Promise(res => setTimeout(res, 500));

                    data2 = await GameVars.api.checkGigaswapState();
                }

                data2[0].opponent_choices = [
                    parseInt(data2[0].opponent_choices.substr(0, 2)), 
                    parseInt(data2[0].opponent_choices.substr(2, 2)), 
                    parseInt(data2[0].opponent_choices.substr(4, 2))
                ];

                GigaSwapManager.adversaryAccount = data2[0].opponent;

                let adversaryCitizenInfo = await GameVars.api.getCitizenInfo(GigaSwapManager.adversaryAccount);
                let adversaryCitizenData = await GameVars.api.getCitizenData(adversaryCitizenInfo.citizen_id);

                ExchangeLayer.currentInstance.gigaSwapContainer.adversaryContainer.setAdversaryData(adversaryCitizenData.data.rarity, adversaryCitizenData.data.data);

                for (let i = 0; i < 3; i++) {
                    GigaSwapManager.adversaryMoves.push(data2[0].opponent_choices[i]);

                    const result = GigaSwapManager.getRoundWinner(GigaSwapManager.playerMoves[i], data2[0].opponent_choices[i]);
    
                    GigaSwapManager.roundResults.push(result);
                }

                const gigaSwapContainer = ExchangeLayer.currentInstance.gigaSwapContainer;
                gigaSwapContainer.onMovesTransactionCompleted();

                setTimeout(() => {
                    GigaSwapManager.processRound();
                }, 1000);
            } else {
                ExchangeLayer.currentInstance.gigaSwapContainer.showRetryLayer(data.message);
            }
        })();
    }

    public static processRound(): void {

        const player = ExchangeLayer.currentInstance.gigaSwapContainer.playerContainer;
        const adversary = ExchangeLayer.currentInstance.gigaSwapContainer.adversaryContainer;

        const playerMove = GigaSwapManager.playerMoves[GigaSwapManager.round];
        const adversaryMove = GigaSwapManager.adversaryMoves[GigaSwapManager.round];

        player.executeMove(playerMove);
        adversary.executeMove(adversaryMove);

        GigaSwapManager.round ++;

        if (GigaSwapManager.round === 3) {
            GigaSwapManager.onGameEnded();
        } else {
            setTimeout(() => {
                GigaSwapManager.processRound();
            }, 4000);
        }
    }

    public static replay(): void {

        ExchangeLayer.currentInstance.hideGigaSwapContainer();
        ExchangeLayer.currentInstance.showGigaSwapContainer(GigaSwapManager.carbz, GigaSwapManager.credits, GigaSwapManager.jigo);
    }

    private static onGameEnded(): void {

        let playerPoints = 0;
        let adversaryPoints = 0;

        for (let i = 0; i < GigaSwapManager.roundResults.length; i ++) {
            const result = GigaSwapManager.roundResults[i];

            if (result === GameConstants.RESULT_PLAYER) {
                playerPoints ++;
            } else if (result === GameConstants.RESULT_ADVERSARY) {
                adversaryPoints ++;
            }
        }

        let result: string;

        if (playerPoints > adversaryPoints) {
            result = GameConstants.RESULT_PLAYER;
        } else if (playerPoints < adversaryPoints) {
            result = GameConstants.RESULT_ADVERSARY;
        } else {
            result = GameConstants.RESULT_TIE;
        }

        ExchangeLayer.currentInstance.gigaSwapContainer.onGameEnded(result);
    }

    private static getRoundWinner(playerMove: number, adversaryMove: number): string {

        let ret = GameConstants.RESULT_TIE;
    
        if (playerMove === GameConstants.ROCK) {

            if (adversaryMove === GameConstants.ROCK) {
                ret = GameConstants.RESULT_TIE;
            } else if (adversaryMove === GameConstants.SCISSORS) {
                ret = GameConstants.RESULT_PLAYER;
            } else {
                ret = GameConstants.RESULT_ADVERSARY;
            }

        } else if (playerMove === GameConstants.SCISSORS) {

            if (adversaryMove === GameConstants.ROCK) {
                ret = GameConstants.RESULT_ADVERSARY;
            } else if (adversaryMove === GameConstants.SCISSORS) {
                ret = GameConstants.RESULT_TIE;
            } else {
                ret = GameConstants.RESULT_PLAYER;
            }

        } else {

            if (adversaryMove === GameConstants.ROCK) {
                ret = GameConstants.RESULT_PLAYER;
            } else if (adversaryMove === GameConstants.SCISSORS) {
                ret = GameConstants.RESULT_ADVERSARY;
            } else {
                ret = GameConstants.RESULT_TIE;
            }
        }

        return ret;
    }
}
