import { GameConstants } from "../../GameConstants";
import { GameManager } from "../../GameManager";
import { Button } from "../../utils/Button";
import { GigaSwapManager } from "./GigaSwapManager";

export class GigaSwapGUI extends Phaser.GameObjects.Container {

    private rockBtn: Button;
    private paperBtn: Button;
    private scissorsBtn: Button;
    private f: number;
    private title: Phaser.GameObjects.Image;
    private pickLabel: Phaser.GameObjects.Text;
    private cancelBtn: Button;

    constructor(scene: Phaser.Scene) {

        super(scene);

        this.f = 0;

        this.title = new Phaser.GameObjects.Image(this.scene, 0, 130, "texture_atlas_1", "gigaswap");
        this.add(this.title);

        this.scene.tweens.add({
            targets: this.title,
            scaleX: 1.025,
            scaleY: 1.025,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 850,
            repeat: -1
        });

        this.rockBtn = new Button(this.scene, -85, 240, "texture_atlas_1", "rock_idle", "rock_over");
        this.rockBtn.onDown(function (): void {
            this.onDownMove(GameConstants.ROCK);
        }, this);
        this.add(this.rockBtn);

        this.paperBtn = new Button(this.scene, 0, 240, "texture_atlas_1", "paper_idle", "paper_over");
        this.paperBtn.onDown(function (): void {
            this.onDownMove(GameConstants.PAPER);
        }, this);
        this.add(this.paperBtn);

        this.scissorsBtn = new Button(this.scene, 85, 240, "texture_atlas_1", "scissors_idle", "scissors_over");
        this.scissorsBtn.onDown(function (): void {
            this.onDownMove(GameConstants.SCISSORS);
        }, this);
        this.add(this.scissorsBtn);

        this.pickLabel = new Phaser.GameObjects.Text(this.scene, 0, 300, "PICK YOUR 3 MOVES", {fontFamily: "Oswald-ExtraLight", fontSize: "20px", color: "#FFFFFF"});
        this.pickLabel.setOrigin(.5);
        this.add(this.pickLabel);

        this.cancelBtn = new Button(this.scene, 460, 20, "texture_atlas_1", "btn_cancel_off", "btn_cancel_on");
        this.cancelBtn.onDown(this.onDownCancelBtn, this);
        this.add(this.cancelBtn);

        // HAY Q HACER ESTO PQ EL METODO UPDATE NO SE UTILIZA DE MANERA AUTOMATICA
        this.scene.sys.updateList.add(this);
    }

    public preUpdate(): void {

        if (GigaSwapManager.playerMoves.length === 0) {

            this.f ++;

            if (this.f === 60) {

                this.f = 0;

                this.scene.tweens.add({
                    targets: this.rockBtn,
                    y: 238,
                    scaleX: 1.05,
                    scaleY: 1.05,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 200,
                    yoyo: true
                });

                this.scene.tweens.add({
                    targets: this.paperBtn,
                    y: 238,
                    scaleX: 1.05,
                    scaleY: 1.05,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 200,
                    yoyo: true,
                    delay: 200
                });

                this.scene.tweens.add({
                    targets: this.scissorsBtn,
                    y: 238,
                    scaleX: 1.05,
                    scaleY: 1.05,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 200,
                    yoyo: true,
                    delay: 400
                });
            }

        } else {

            this.rockBtn.setScale(1);
            this.paperBtn.setScale(1);
            this.scissorsBtn.setScale(1);
        }
    }

    public onMovesTransactionCompleted(): void {

        this.scene.tweens.add({
            targets: this.title,
            scaleX: 1.075,
            scaleY: 1.075,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 650,
            onComplete: function(): void {

                if (!this.scene) {
                    return;
                }

                this.scene.tweens.add({
                    targets: this.title,
                    scaleX: 0,
                    scaleY: 0,
                    ease: Phaser.Math.Easing.Cubic.Out,
                    duration: 150,
                    onComplete: function(): void {
                        this.title.destroy();
                    },
                    onCompleteScope: this
                });
            },
            onCompleteScope: this
        });

        this.pickLabel.destroy();

        this.scene.tweens.add({
            targets: this.pickLabel,
            alpha: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 400
        });

        this.scene.tweens.add({
            targets: this.rockBtn,
            angle: 700,
            scaleX: 0,
            scaleY: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: function(): void {
                this.rockBtn.destroy();
            },
            onCompleteScope: this
        });

        this.scene.tweens.add({
            targets: this.paperBtn,
            angle: 700,
            scaleX: 0,
            scaleY: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: function(): void {
                this.paperBtn.destroy();
            },
            onCompleteScope: this,
            delay: 300
        });

        this.scene.tweens.add({
            targets: this.scissorsBtn,
            angle: 700,
            scaleX: 0,
            scaleY: 0,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            onComplete: function(): void {
                this.scissorsBtn.destroy();
            },
            onCompleteScope: this,
            delay: 600
        });
    }

    public disableButtons(): void {

        this.rockBtn.disable();
        this.paperBtn.disable();
        this.scissorsBtn.disable();

        // YA NO HAY MARCHA ATRAS
        this.cancelBtn.visible = false;
    }

    public onGameEnded(result: string): void {
        //
    }

    private onDownMove(move: number): void {

        GigaSwapManager.onMoveSelected(move);
    }

    private onDownCancelBtn(): void {
        
        GameManager.hideExchangeLayer();
    }
}
