import { GameVars } from "./../../GameVars";

export class Column extends Phaser.GameObjects.Container {

    private static BASE_COLORS = [0xD465B3, 0xD85581, 0xDC7E52, 0xEDB155, 0x009A81, 0x0095B7, 0x835CB4, 0x87C5C9, 0x00517B];
    private static SHADOW_COLORS = [0x823AC6, 0xA02770, 0x914E36, 0x7D543D, 0x00566B, 0x004180, 0x083982, 0x368396, 0x15294E];

    private base: Phaser.GameObjects.Image;
    private light: Phaser.GameObjects.Image;
    private shadow: Phaser.GameObjects.Image;
   
    constructor(scene: Phaser.Scene, x: number, y: number) {

        super(scene);

        this.setPosition(x, y);

        this.base = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_3", "column_base");
        this.add(this.base);

        this.light = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_3", "column_lights");
        this.add(this.light);

        this.shadow = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_3", "column_shadow");
        this.add(this.shadow);
    }

    public setColors(value?: number): void {

        this.base.setTint(Column.BASE_COLORS[value !== undefined ? value : GameVars.backgroundValues.wall]); 
        this.shadow.setTint(Column.SHADOW_COLORS[value !== undefined ? value : GameVars.backgroundValues.wall]); 
    }
}
