import { Button } from "./../../utils/Button";
import { GameConstants } from "../../GameConstants";
import { GameVars } from "../../GameVars";
import { GameManager } from "../../GameManager";

export class ClaimLayer extends Phaser.GameObjects.Container {

    constructor(scene: Phaser.Scene, data: any, time: number, index?: number) {

        super(scene);

        const background = new Phaser.GameObjects.Graphics(this.scene);
        background.fillStyle(0x000000, .25);
        background.alpha = .01;
        background.fillRect(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT);
        background.setInteractive(new Phaser.Geom.Rectangle(0, 0, GameConstants.GAME_WIDTH, GameConstants.GAME_HEIGHT), Phaser.Geom.Rectangle.Contains);
        this.add(background);

        const midContainer = new Phaser.GameObjects.Container(this.scene, GameConstants.GAME_WIDTH / 2, GameConstants.GAME_HEIGHT / 2 + 50);
        midContainer.alpha = 0;
        this.add(midContainer);

        if (index && index === 1) {
            midContainer.x = GameConstants.GAME_WIDTH / 2 + 300;
        }

        const layerBck = new Phaser.GameObjects.Image(this.scene, 0, 0, "texture_atlas_1", "info_box");
        midContainer.add(layerBck);

        const title = new Phaser.GameObjects.Text(this.scene, 0, -140, "CLAIM SUMMARY", { fontFamily: "Oswald-Medium", fontSize: "40px", color: "#FFFFFF", align: "center" });
        title.setOrigin(.5);
        midContainer.add(title);

        const reward = new Phaser.GameObjects.Text(this.scene, 70, -60, "", { fontFamily: "Oswald-Medium", fontSize: "25px", color: "#44FFF8", align: "center" });
        reward.setOrigin(1, .5);
        midContainer.add(reward);

        const rewardValue = new Phaser.GameObjects.Text(this.scene, reward.x, reward.y, "", { fontFamily: "Oswald-Medium", fontSize: "25px", color: "#22F722", align: "center" });
        rewardValue.setOrigin(0, .5);
        midContainer.add(rewardValue);

        const icon = new Phaser.GameObjects.Image(this.scene, rewardValue.x + rewardValue.width + 5, rewardValue.y, "texture_atlas_1", "icon_credits");
        icon.setOrigin(0, .5);
        icon.setScale(.9);
        midContainer.add(icon);

        const rewardExtra = new Phaser.GameObjects.Text(this.scene, 70, -25, "", { fontFamily: "Oswald-Medium", fontSize: "22px", color: "#44FFF8", align: "center" });
        rewardExtra.setOrigin(1, .5);
        midContainer.add(rewardExtra);

        const rewardExtraValue = new Phaser.GameObjects.Text(this.scene, rewardExtra.x, rewardExtra.y, "", { fontFamily: "Oswald-Medium", fontSize: "22px", color: "#22F722", align: "center" });
        rewardExtraValue.setOrigin(0, .5);
        midContainer.add(rewardExtraValue);

        const iconExtra = new Phaser.GameObjects.Image(this.scene, rewardExtraValue.x + rewardExtraValue.width + 5, rewardExtraValue.y, "texture_atlas_1", "icon_credits");
        iconExtra.setOrigin(0, .5);
        iconExtra.setScale(.7);
        iconExtra.visible = false;
        midContainer.add(iconExtra);

        const staminaCons = new Phaser.GameObjects.Text(this.scene, 50, 30, "Stamina consumed:", { fontFamily: "Oswald-Light", fontSize: "25px", color: "#44FFF8", align: "center" });
        staminaCons.setOrigin(1, .5);
        midContainer.add(staminaCons);

        const staminaValue = new Phaser.GameObjects.Text(this.scene, staminaCons.x, staminaCons.y, "", { fontFamily: "Oswald-Light", fontSize: "25px", color: "#ff003b", align: "center" });
        staminaValue.setOrigin(0, .5);
        midContainer.add(staminaValue);

        const batteryCons = new Phaser.GameObjects.Text(this.scene, 50, 60, "Battery consumed:", { fontFamily: "Oswald-Light", fontSize: "25px", color: "#44FFF8", align: "center" });
        batteryCons.setOrigin(1, .5);
        midContainer.add(batteryCons);

        const batteryValue = new Phaser.GameObjects.Text(this.scene, batteryCons.x, batteryCons.y, "", { fontFamily: "Oswald-Light", fontSize: "25px", color: "#ff003b", align: "center" });
        batteryValue.setOrigin(0, .5);
        midContainer.add(batteryValue);

        if (!data) {

            reward.x = 55;
            reward.text = "CREDITS COLLECTED:";

            let max_unclaimed_credits = GameVars.configInfo.max_unclaimed_credits;

            if (GameVars.stakedWallets.length) {
                max_unclaimed_credits += GameVars.walletsConfigInfo.find(x => x.template_id === GameVars.stakedWallets[0].template_id).extra_capacity;
            }

            rewardValue.text = " +" + Math.round(Math.min(GameVars.playerInfo.unclaimed_credits, max_unclaimed_credits) * 100) / 100;

            icon.setPosition(rewardValue.x + rewardValue.width + 5, rewardValue.y);
            icon.setFrame("icon_credits");

            let wallet_stamina_consumed = GameVars.configInfo.wallet_stamina_consumed;
            let wallet_battery_consumed = GameVars.configInfo.wallet_battery_consumed;

            if (GameVars.stakedWallets.length) {

                wallet_stamina_consumed = GameVars.walletsConfigInfo.find(x => x.template_id === GameVars.stakedWallets[0].template_id).stamina_consumed;
                wallet_battery_consumed = GameVars.walletsConfigInfo.find(x => x.template_id === GameVars.stakedWallets[0].template_id).battery_consumed;
            }

            staminaValue.setPosition(staminaCons.x, staminaCons.y);
            staminaValue.text = " -" + wallet_stamina_consumed + " units";

            batteryValue.setPosition(batteryCons.x, batteryCons.y);
            batteryValue.text = " -" + wallet_battery_consumed + " units";

            const btnOkay = new Button(this.scene, 0, 135, "texture_atlas_1", "btn_ok", "btn_ok");
            btnOkay.onDown(() => {
                this.claimCredits(midContainer);
            }, this);
            btnOkay.onOver(() => {
                btnOkay.setScale(1.05);
            }, this);
            btnOkay.onOut(() => {
                btnOkay.setScale(1);
            }, this);
            midContainer.add(btnOkay);

            background.on("pointerdown", () => {
                GameManager.enablePiggybankClaim();
                this.destroy();
            }, this);

        } else {

            const citizenInfo = GameVars.citizensInfo.find(x => x.key === GameVars.playerInfo.type);

            rewardValue.text = " +" + parseInt(data.rewards[0]);

            if (data.type === "Jigowatts") {

                reward.text = "JIGOWATTS COLLECTED:";

                icon.setPosition(rewardValue.x + rewardValue.width + 5, rewardValue.y);
                icon.setFrame("icon_jigowatts");

                if (citizenInfo.claim_extra) {
                    rewardExtra.text = citizenInfo.type.toUpperCase() + " BOOST " + citizenInfo.claim_extra + "%:"

                    rewardExtraValue.setPosition(rewardExtra.x, rewardExtra.y);
                    rewardExtraValue.text = " +" + (parseInt(data.rewards[0]) * (citizenInfo.claim_extra)) / 100;

                    iconExtra.visible = true;
                    iconExtra.setPosition(rewardExtraValue.x + rewardExtraValue.width + 5, rewardExtraValue.y);
                    iconExtra.setFrame("icon_jigowatts");
                }

                staminaValue.setPosition(staminaCons.x, staminaCons.y);
                staminaValue.text = " -" + data.stamina_consumed + " units";

                batteryCons.text = "Integrity consumed:";

                batteryValue.setPosition(batteryCons.x, batteryCons.y);
                batteryValue.text = " -" + data.integrity_consumed + " units";

                const btnOkay = new Button(this.scene, 0, 135, "texture_atlas_1", "btn_ok", "btn_ok");
                btnOkay.onDown(() => {
                    this.claimTreadmill(midContainer, btnOkay, index);
                }, this);
                btnOkay.onOver(() => {
                    btnOkay.setScale(1.05);
                }, this);
                btnOkay.onOut(() => {
                    btnOkay.setScale(1);
                }, this);
                midContainer.add(btnOkay);

                background.on("pointerdown", () => {
                    GameManager.enableTreadmillClaim(index);
                    this.destroy();
                }, this);

            } else if (data.type === "Carbz") {

                reward.text = "CARBZ COLLECTED:";

                icon.setPosition(rewardValue.x + rewardValue.width + 5, rewardValue.y);
                icon.setFrame("icon_carbz");

                if (citizenInfo.claim_extra) {
                    rewardExtra.text = citizenInfo.type.toUpperCase() + " BOOST " + citizenInfo.claim_extra + "%:"

                    rewardExtraValue.setPosition(rewardExtra.x, rewardExtra.y);
                    rewardExtraValue.text = " +" + (parseInt(data.rewards[0]) * (citizenInfo.claim_extra)) / 100;

                    iconExtra.visible = true;
                    iconExtra.setPosition(rewardExtraValue.x + rewardExtraValue.width + 5, rewardExtraValue.y);
                    iconExtra.setFrame("icon_carbz");
                }

                staminaCons.text = "Load consumed:";

                staminaValue.setPosition(staminaCons.x, staminaCons.y);
                staminaValue.text = " -" + data.integrity_consumed + " units";

                batteryValue.setPosition(batteryCons.x, batteryCons.y);
                batteryValue.text = " -" + data.battery_consumed + " units";

                const btnOkay = new Button(this.scene, 0, 135, "texture_atlas_1", "btn_ok", "btn_ok");
                btnOkay.onDown(() => {
                    this.claimDrinkMachine(midContainer, btnOkay, index);
                }, this);
                btnOkay.onOver(() => {
                    btnOkay.setScale(1.05);
                }, this);
                btnOkay.onOut(() => {
                    btnOkay.setScale(1);
                }, this);
                midContainer.add(btnOkay);

                background.on("pointerdown", () => {
                    GameManager.enableDrinkMachineClaim(index);
                    this.destroy();
                }, this);
            }
        }

        this.scene.tweens.add({
            targets: background,
            alpha: 1,
            ease: Phaser.Math.Easing.Linear,
            duration: time
        });

        this.scene.tweens.add({
            targets: midContainer,
            y: GameConstants.GAME_HEIGHT / 2 + 20,
            alpha: 1,
            ease: Phaser.Math.Easing.Cubic.Out,
            duration: 500,
            delay: time,
        });
    }

    private async claimCredits(midContainer: Phaser.GameObjects.Container): Promise<void> {

        let result = await GameManager.claimCredits();

        if (result) {
            if (!this.scene) {
                return;
            }
            this.scene.tweens.add({
                targets: midContainer,
                alpha: 0,
                scaleX: .75,
                scaleY: .75,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 350,
                onComplete: () => {
                    GameManager.onClaimTransactionEnd();
                    this.destroy();
                },
                onCompleteScope: this
            });
        } else {
            //  
        }
    }

    private async claimTreadmill(midContainer: Phaser.GameObjects.Container, btnOkay: Button, index: number): Promise<void> {
        btnOkay.disableInteractive();
        btnOkay.setAlpha(0.3);
        let result = await GameManager.claimTool(GameVars.stakedTreadmill[index].asset_id);

        if (result) {
            if (!this.scene) {
                return;
            }
            this.scene.tweens.add({
                targets: midContainer,
                alpha: 0,
                scaleX: .75,
                scaleY: .75,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 350,
                onComplete: () => {
                    GameManager.onClaimTreadmillTransactionEnd(index);
                    this.destroy();
                },
                onCompleteScope: this
            });
        } else {
            // 
            btnOkay.setInteractive();
            btnOkay.setAlpha(1);
        }
    }

    private async claimDrinkMachine(midContainer: Phaser.GameObjects.Container, btnOkay: Button, index: number): Promise<void> {
        btnOkay.disableInteractive();
        btnOkay.setAlpha(0.3);
        let result = await GameManager.claimTool(GameVars.stakedDrink[index].asset_id);

        if (result) {
            if (!this.scene) {
                return;
            }
            this.scene.tweens.add({
                targets: midContainer,
                alpha: 0,
                scaleX: .75,
                scaleY: .75,
                ease: Phaser.Math.Easing.Cubic.Out,
                duration: 350,
                onComplete: () => {
                    GameManager.onClaimDrinkMachineTransactionEnd(index);
                    this.destroy();
                },
                onCompleteScope: this
            });
        } else {
            // 
        }
    }
}
